import React from 'react'
import { Link, useLocation } from 'react-router-dom';
import Togglemode from './../general/Togglemode';

const AHeader = () => {
    const dets = useLocation()
  return (
    <div className="flex justify-between py-3 px-3 dark:bg-slate-700">
        <div className="flex items-center gap-3">
            {dets.pathname !== '/admin' && <div className="cursor-pointer text-slate-600 text-xl button dark:text-slate-200"> <i onClick={() => window.history.back()} className="fas fa-arrow-left fa-fw"></i> </div>}
            <div className="font-semibold text-xl drop-shadow-lg dark:text-slate-200">Jogglecryp</div>
        </div>
        <div className="">
            <div className="flex items-center">
                <Togglemode />
                <Link to='/admin_profile'><div className="relative px-4"><i className="far button text-xl fa-user fa-fw dark:text-slate-200"></i> <i className="fas text-red-500 text-[0.6rem] fa-circle fa-fw absolute right-4"></i> </div></Link>
                <Link to='/manage_notification' className="relative px-4">
                    <i className="far text-xl dark:text-slate-200 fa-bell button fa-fw"></i>
                </Link>
            </div>
        </div>
    </div>
  )
}

export default AHeader