import React from 'react'
import { Link } from 'react-router-dom'

const AFooter = () => {
  return (
    <div className='h-[11vh] border-t md:hidden overflow-hidden dark:bg-slate-700 dark:border-t-slate-400'>
      {/* <div className="bg-white w-full fixed bottom-[11vh] rounded-tl-xl rounded-tr-xl">
        <Link to='/'>
          <div className="border-b-[1px] py-2 px-3"></div>
        </Link>
      </div> */}
        <div className="grid grid-cols-5">
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/manage_vendors' className='flex items-center flex-col'> <i className="fas text-lg fa-user-friends fa-fw"></i> <div className="text-xs capitalize font-semibold">vendors</div> </Link> </div>
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/manage_transactions' className='flex items-center flex-col'> <i className="fas text-lg fa-exchange-alt fa-fw"></i> <div className="text-xs capitalize font-semibold">transactions</div> </Link> </div>
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/admin' className='flex items-center flex-col'> <i className="fas text-4xl fa-house-damage fa-fw"></i></Link> </div>
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/manage_wallets' className='flex items-center flex-col'> <i className="fas text-lg fa-wallet fa-fw"></i> <div className="text-xs capitalize font-semibold">wallets</div> </Link> </div>
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/admin_pages' className='flex items-center flex-col'> <i className="fas text-lg fa-align-right fa-fw"></i> <div className="text-xs capitalize font-semibold">pages</div> </Link> </div>
        </div>
    </div>
  )
}

export default AFooter