import axios from 'axios';
import Cookies from 'js-cookie';
import React from 'react'
import { useSelector, useDispatch } from 'react-redux';
import { logoutUser } from '../../store/reducers/dataReducer';
import { useNavigate } from 'react-router-dom';

const Logout = () => {
    const {user} = useSelector(state => state.data)
    const dispatch = useDispatch()
    const navigate = useNavigate()
    const togLogout = () => {
        dispatch(logoutUser())
    }
    const logsout = async () => {
        try {
            const res = await axios.post(`/user/logoutuser/${user.id}`)
            if(res.data.status === 200) {
                Cookies.set('token', res.data.token)
                navigate('/login')
                dispatch(logoutUser())
            }else {
                console.log(res.data)
            }
        } catch (error) {
            return console.log(`error ${error}`)
        }
    }
  return (
    <div>
        <div className="bg-black/60 h-screen w-full dark:text-slate-200 fixed top-0 left-0 flex justify-center items-center z-10">
            <div className="bg-white p-4 rounded-xl dark:bg-slate-700 extras">
                <div className="text-sm">Hi! {user.fname}, Are you sure you want to logout?</div>
                <div className="flex items-center justify-between mt-5">
                    <button className='py-2 px-5 bg-slate-400 rounded-full text-xs capitalize font-semibold button' onClick={togLogout}>cancel</button>
                    <button className='py-2 px-5 bg-red-600 text-red-100 rounded-full text-xs capitalize font-semibold button' onClick={logsout}>continue</button>
                </div>
            </div>
        </div>
    </div>
  )
}

export default Logout