
import axios from 'axios'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'

const Vendor = props => {
    const { imgurl } = useSelector(state => state.data)
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [hist, setHist] = useState([])
    const [goodfeeds, setGoodfeeds] = useState([])
    const [badfeeds, setBadfeeds] = useState([])
    const [chist, setChist] = useState([])
    const [bal, setBal] = useState(0)
    const openPurchasePage = () => {
        navigate(`/purchase/${props.item.id}/secure_transfer_protocol`)
    }

    const vendorProperty = useCallback(async() => {
        const res = await axios.get(`/vendor/vendor-property/${props.item.id}`)
        if(res.data.status === 200) {
            setHist(res.data.hist)
            setGoodfeeds(res.data.goodfeeds)
            setBadfeeds(res.data.badfeeds)
            setChist(res.data.chist)
            setBal(res.data.bal)
        }else {
            console.log(res.data)
        }
    }, [props])

    useEffect(() => {
        dispatch(() => vendorProperty())
    }, [dispatch, vendorProperty])

    return (
        <div className="py-2 px-3 mb-2 justify-between shadow-lg bg-white dark:bg-slate-700 rounded-lg cursor-pointer" onClick={openPurchasePage}>
            <div className="flex gap-3 w-full">
                <div className=""> <img src={`${imgurl}/profiles/${props.item.user.image}`} alt="" className="w-8 h-8 rounded-full object-cover" /> </div>
                <div className="w-full">
                    <div className="flex items-center w-full justify-between">
                        <div className="text-sm font-semibold"> <i className="far fa-user text-xs fa-fw"></i> {props.item.user.lname} <sup> <i className="fas text-blue-400 fa-check-circle fa-fw"></i> </sup> </div>
                        <div className=""> <i className="fas text-xs fa-chevron-right fa-fw"></i> </div>
                    </div>
                    <div className="flex gap-5 items-center my-2">
                        <div className="text-xs"> <button className="text-xs text-green-500"> <i className="far fa-thumbs-up fa-fw"></i> </button>:{goodfeeds.length} </div>
                        <div className="text-xs"> <button className="text-xs text-red-500"> <i className="far fa-thumbs-down fa-fw"></i> </button>:{badfeeds.length} </div>
                    </div>
                    <div className="flex mb-2 justify-between items-center">
                        <div className="text-xs capitalize">Total transfers</div>
                        <div className="text-xs uppercase">({hist.length})</div>
                    </div>
                    <div className="flex mb-2 justify-between items-center">
                        <div className="text-xs capitalize">Completed transfers</div>
                        <div className="text-xs uppercase">({chist.length})</div>
                    </div>
                    <div className="flex justify-between items-center">
                        <div className="text-xs capitalize">Total transfered</div>
                        <div className="text-xs uppercase">${bal} usd</div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Vendor