import React, { useEffect, useState } from 'react'

const Togglemode = () => {
    const [theme, setTheme] = useState(localStorage.getItem('theme'));
    useEffect(() => {
      if(theme === 'dark') {
        document.documentElement.classList.add('dark')
      }else {
        document.documentElement.classList.remove('dark')
      }
    }, [theme])
  
    const handletoggle = () => {
      if(theme === 'dark') {
        localStorage.setItem('theme', 'light')
        setTheme('light')
      }else {
        localStorage.setItem('theme', 'dark')
        setTheme('dark')
      }
      
    }
    return (
      // <div className=""> <i onClick={handletoggle} className="fas fa-home fa-fw"></i> </div>
      <div className="relative w-14 rounded-full bg-slate-300 dark:bg-slate-600 h-3.5">
        <button className={`w-5 h-5 absolute top-[-0.2rem] rounded-full ${theme === 'dark' ? 'right-0 bg-indigo-400' : 'left-0 bg-slate-600'}`} onClick={handletoggle}></button>
      </div>
    );
}

export default Togglemode
