import axios from 'axios'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { toast } from 'react-toastify'

const AddWalletButton = props => {
    const dispatch = useDispatch()
    const {user} = useSelector(state => state.data)
    const [isadded, setIsadded] = useState(false)
    const [loading, setLoading] = useState(false)

    const addWallet = async (id) => {
        setLoading(true)
        const data = {
            user_id: user.id,
            adminwallet: id
        }
        const res = await axios.post('/wallet/add-wallet', data)
        setLoading(false)
        if(res.data.status === 200) {
            console.log(res.data);
            props.sendSignal()
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const checkButton = useCallback(async () => {
        const res = await axios.get(`/wallet/mywallets/${user.id}/${props.item.id}`)
        if(res.data.msg === 'yes') {
            setIsadded(true)
        }else {
            setIsadded(false)
        }
    }, [props, user.id])

    
    useEffect(() => {
        dispatch(() => checkButton())
    }, [checkButton, dispatch])
  return (
    <div className=""> <button onClick={() => addWallet(props.item.id)} disabled={loading} className={`py-1 button px-3 ${isadded ? 'bg-orange-400 text-orange-800' : 'bg-indigo-700 text-indigo-200'} capitalize font-semibold text-sm rounded-full`}>{isadded ? 'added' : loading ? '....' : 'add'}</button> </div>
  )
}

export default AddWalletButton
