import axios from 'axios'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import img from '../../images/img.png'
import whats from '../../images/whats.png'
import phoneimg from '../../images/phone.png'
import moment from 'moment'
import Sidenav from './../../components/user/Sidenav';

const Referrals = () => {
    const { user, imgurl } = useSelector(state => state.data)
    const [zone, setZone] = useState('pending')
    const [users, setUsers] = useState([])
    const dispatch = useDispatch()
    const [cc] = useState('btn py-3 hover:bg-slate-200 font-semibold capitalize button bg-white dark:bg-slate-700 shadow-xl dark:hover:bg-slate-600 px-10 border dark:border-slate-500 rounded-full text-xs md:text-sm')

    const allReferrals = useCallback(async () => {
        const res = await axios.get(`/user/my-refs/${user.id}`)
        if (res.data.status === 200) {
            setUsers(res.data.msg)
        }
    }, [user])

    useEffect(() => {
        dispatch(() => allReferrals())
    }, [allReferrals, dispatch])
    return (
        <div>

        <div className="h-screen w-full flex">
            <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[98%] mx-auto">
                                <div className="grid grid-cols-2 gap-5 mb-7">
                                    <button onClick={() => setZone('pending')} className={`${cc}`}>Inactive Mining Users</button>
                                    <button onClick={() => setZone('active')} className={`${cc}`}>Active Mining Users</button>
                                </div>

                                {user.length < 1 ? <div className="italic">You have not referred anyone</div> : users.map((item) => (
                                    item.mine[0].status === zone && <div className={`mb-2 bg-white dark:bg-slate-700 dark:font-semibold rounded-xl grid grid-cols-2 w-[98%] md:w-11/12 mx-auto shadow-xl p-3`} key={item.id}>
                                        <div className="flex gap-2">
                                            <div className=""> <img src={!item.image ? img : `${imgurl}/profiles/${item.image}`} alt="" className="w-8 h-8 shadow-lg rounded-full object-cover" /> </div>
                                            <div className="">
                                                <div className="text-sm">{item.fname} {item.lname}.</div>
                                                <div className="text-xs">Mining: {item.mine[0].status}</div>
                                                <div className="text-xs"> <i className="far fa-clock fa-fw"></i> {moment(item.createdAt).calendar()} </div>
                                            </div>
                                        </div>
                                        <div className="flex items-center gap-5 ml-auto">
                                            <a href={`tel:${item.contact}`} rel='noreferrer' target='_blank' className=""> <img src={phoneimg} alt="" className="w-6 h-6 rounded-full" /> </a>
                                            <a href={`https://wa.me/${item.contact}`} rel='noreferrer' target='_blank' className=""> <img src={whats} alt="" className="w-8 h-8 rounded-full" /> </a>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Referrals