import { createSlice } from '@reduxjs/toolkit'

const initialState = {
  user: {},
  logout: false,
  // imgurl: 'http://localhost:5000',
  imgurl: ['https://jogserver.jogglecryp.com/api', 'http://jogserver.jogglecryp.com/api'],
  adminwallet: {},
  singlepackage: {},
  singleairdrop: {},
  vendor: {},
  singleproposal: {},
  wallet: {},
  news: {},
  notify: {},
  allnotify: [],
  allusernotify: []
}

export const counterSlice = createSlice({
  name: 'user',
  initialState,
  reducers: {
    getUserData: (state, action) => {
        state.user = action.payload
    },
    logoutUser: (state) => {
        state.logout = !state.logout
    },
    getAdminwallet: (state, action) => {
      state.adminwallet = action.payload
    },
    getSinglePackage: (state, action) => {
      state.singlepackage = action.payload
    },
    getSingleAirdrop: (state, action) => {
      state.singleairdrop = action.payload
    },
    getSingleVendor: (state, action) => {
      state.vendor = action.payload
    },
    getSingleProposal: (state, action) => {
      state.singleproposal = action.payload
    },
    getSingleWallet: (state, action) => {
      state.wallet = action.payload
    },
    getSingleNews: (state, action) => {
      state.news = action.payload
    },
    getSingleNotify: (state, action) => {
      state.notify = action.payload
    },
    getAllNotifies: (state, action) => {
      state.allnotify = action.payload
    },
    getAllUserNotifies: (state, action) => {
      state.allusernotify = action.payload
    }
  },
})

// Action creators are generated for each case reducer function
export const { getUserData, logoutUser, getAdminwallet, getSinglePackage, getSingleAirdrop, getSingleVendor, getSingleProposal, getSingleWallet, getSingleNews, getSingleNotify, getAllNotifies, getAllUserNotifies } = counterSlice.actions

export default counterSlice.reducer