import axios from 'axios';
import React, { useState } from 'react'
import { ToastContainer, toast } from 'react-toastify';

const ModifyUser = (props) => {
    const [user, setUser] = useState({
        fname: props.user.fname,
        lname: props.user.lname, 
        contact: props.user.contact,
        bonus: props.user.bonus,
        permanent: props.user.permanent === 'true' ? true : false,
        block: props.user.block === 'true' ? true : false,
        blockduration: props.user.blockduration,
    })
    const handleForms = e => {
        setUser({...user, [e.target.name]: e.target.value})
    }

    const handleUpdateForm = async e => {
        e.preventDefault()
        if(!user.fname || !user.lname || !user.contact) {
            return toast.error(`User needs appropraite information for account setup`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const data = {
            fname: user.fname,
            lname: user.lname, 
            contact: user.contact,
            bonus: user.bonus,
            block: user.block,
            permanent: user.permanent,
            blockduration: user.blockduration
        }

        const res =  await axios.post(`/user/admin-update-user-info/${props.user.id}`, data)
        if(res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }else{
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white p-4 rounded-xl md:w-2/5 w-11/12 max-h-[85vh] extras overflow-y-auto dark:bg-slate-700 dark:text-slate-200">
                        <div className="flex justify-between items-center mb-5">
                            <div className="text-xl dark:text-slate-200 text-slate-600 drop-shadow-xl">Manage {props.user.fname}'s Account</div>
                            <div className=""> <i onClick={props.closeview} className="fas fa-times cursor-pointer text-2xl dark:text-slate-200 text-slate-600 fa-fw"></i> </div>
                        </div>
                        <form onSubmit={handleUpdateForm}>
                            <div className="text-right text-xs"> <i className="far fa-envelope fa-fw"></i> {props.user.email}</div>
                            <div className="flex gap-4 mb-7">
                                <div className="w-full">
                                    <div className="text-xs">First Name</div>
                                    <input type="text" name="fname" value={user.fname} onChange={handleForms} placeholder='lastName' className="outline-none capitalize p-2 text-xs w-full bg-transparent border-b" />
                                </div>
                                <div className="w-full">
                                    <div className="text-xs">Last Name</div>
                                    <input type="text" name="lname" value={user.lname} onChange={handleForms} placeholder='firstName' className="outline-none capitalize p-2 text-xs w-full bg-transparent border-b" />
                                </div>
                            </div>
                                <div className="w-full mb-7">
                                    <div className="text-xs">Phone Number</div>
                                    <input type="text" name="contact" value={user.contact} onChange={handleForms} placeholder='555 555 555' className="outline-none capitalize p-2 text-xs w-full bg-transparent border-b" />
                                </div>
                            <div className="flex gap-4 mb-7">
                                <div className="w-full">
                                    <div className="text-xs">Temporal Account Status</div>
                                    <select name="block" value={user.block} onChange={handleForms} className="outline-none capitalize p-2 text-xs w-full bg-transparent border-b">
                                        <option value="true">positive</option>
                                        <option value="false">negative</option>
                                    </select>
                                </div>
                                <div className="w-full">
                                    <div className="text-xs">Disable account duration (Days)</div>
                                    <input type="number" name="blockduration" value={user.blockduration} onChange={handleForms} placeholder='0' className="outline-none capitalize p-2 text-xs w-full bg-transparent border-b" />
                                </div>
                            </div>
                            <div className="flex gap-4">
                                <div className="w-full">
                                    <div className="text-xs">Permanent Block Account Status</div>
                                    <select name="permanent" value={user.permanent} onChange={handleForms} className="outline-none capitalize p-2 text-xs w-full bg-transparent border-b">
                                        <option value="true">positive</option>
                                        <option value="false">negative</option>
                                    </select>
                                </div>
                                <div className="w-full">
                                    <div className="text-xs">Modify User Bonus</div>
                                    <input type="text" name="bonus" value={user.bonus} onChange={handleForms} placeholder='bonus' className="outline-none capitalize p-2 text-xs w-full bg-transparent border-b" />
                                </div>
                            </div>
                            <div className="text-right mt-6">
                                <button className="py-2 px-6 rounded-full text-xs button font-semibold text-orange-100 bg-orange-600 capitalize">update</button>
                            </div>
                        </form>
                    </div>
                </div>
                <ToastContainer />
            </div>
    )
}

export default ModifyUser
