import axios from 'axios'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import ToggleWalletVisibleButton from './ToggleWalletVisibleButton'

const VisibleWallets = props => {
    const {user, imgurl} = useSelector(state => state.data)
    const dispatch = useDispatch()
    const [wallets, setWallets] = useState([])

    const myWallets = useCallback(async () => {
        const res = await axios.get(`/wallet/all/${user.id}`)
        if(res.data.status === 200) {
            setWallets(res.data.msg)
        }
    }, [user])

    useEffect(() => {
        dispatch(() => myWallets())
    }, [dispatch, myWallets])
    return (
        <div className="h-screen w-full bg-black/60 flex items-center top-0 left-0 justify-center fixed z-10">
            <div className="bg-white dark:bg-slate-700 dark:text-slate-200 extras p-4 rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                <div className="flex justify-between items-center mb-4">
                    <div className="text-xl dark:text-slate-200 text-slate-600 drop-shadow-xl">Make your wallet invisible in the dashboard</div>
                    <div className=""> <i onClick={props.closeview} className="fas fa-times cursor-pointer text-2xl dark:text-slate-200 text-slate-600 fa-fw"></i> </div>
                </div>
                {wallets.length < 1 ? <div className="italic">You have no wallet yet</div> : wallets.map((item) => (
                    <div className="grid grid-cols-2 p-2 mb-2 border dark:border-slate-500 rounded-xl shadow-lg" key={item.id}>
                        <div className="flex gap-2 items-center">
                            <div className=""> <img src={`${imgurl}/wallets/${item.adminwallet.image}`} alt="" className="w-7 h-7 bg-cover rounded-full" /> </div>
                            <div className="">
                                <div className="text-sm font-semibold capitalize">{item.adminwallet.name} <span className="uppercase">({item.adminwallet.short})</span> </div>
                                <div className="text-xs">${(item.crypto / item.adminwallet.current_price).toFixed(2)}</div>
                            </div>
                        </div>
                        <div className="ml-auto my-auto">
                            <ToggleWalletVisibleButton item={item} sendSignal={() => dispatch(() => myWallets())} />
                        </div>
                    </div>
                )) }
            </div>
        </div>
    )
}

export default VisibleWallets
