import React, { useCallback, useEffect, useState } from 'react'
import Footer from '../../components/user/Footer';
import Header from '../../components/user/Header';
import { useNavigate, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import axios from 'axios';
import { toast, ToastContainer } from 'react-toastify';
import Sidenav from './../../components/user/Sidenav';

const Withdraw = () => {
    const [tabs, setTabs] = useState('bank')
    const [viewfee, setViewfee] = useState('-')
    const { id } = useParams()
    const dispatch = useDispatch()
    const [wallet, setWallet] = useState({})
    const [admin, setAdmin] = useState({})
    const { imgurl, user } = useSelector(state => state.data)
    const navigate = useNavigate()
    const [loading, setLoading] = useState(false)
    const [forms, setForms] = useState({
        account_holder: '',
        account_number: '',
        bankname: '',
        amount: ''
    })
    const [crypts, setCrypts] = useState({
        address: '',
        amount: '',
        password: ''
    })

    const handleForms = e => {
        setForms({...forms, [e.target.name]: e.target.value})
    }

    const handleCryptos = e => {
        setCrypts({...crypts, [e.target.name]: e.target.value})
    }
    
    // get bank details
    const fetchBank = useCallback(async () => {
        const res = await axios.get(`/withdraw/mybank/${user.id}`)
        if(res.data.status === 200) {
            const detail = res.data.msg
            return setForms({
                account_holder: detail?.aholder,
                account_number: detail?.anumber,
                bankname: detail?.bankname,
                amount: ''
            })
        }
        return setForms({
            account_holder: '',
            account_number: '',
            bankname: '',
            amount: '',
        })
    }, [user])

    const getWallet = useCallback(async () => {
        const res = await axios.get(`/wallet/single/${id}`)
        if (res.data.status === 200) {
            setWallet(res.data.msg)
            setAdmin(res.data.msg.adminwallet)
        }
    }, [id])

    useEffect(() => {
        dispatch(() => getWallet())
        dispatch(() => fetchBank())
    }, [dispatch, getWallet, fetchBank])

    const bankWithdrawal = async e => {
        e.preventDefault()
        const data = {
            account_holder: forms.account_holder,
            account_number: forms.account_number,
            bankname: forms.bankname,
            amount: parseInt(viewfee),
        }
        if(!forms.amount) {
            return toast.error('Enter a withdrawable amount', {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        setLoading(true)
        const res = await axios.post(`/withdraw/save-withdrawal/${user.id}/${wallet.id}`, data)
        setLoading(false)
        if(res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            setTimeout(() => {
                navigate('/transactions')
            }, 3000);
        }else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const cryptoWtihdrawal = async e => {
        e.preventDefault()
        const data = {
            address: crypts.address,
            amount: parseInt(viewfee),
            password: crypts.password
        }
        if(!crypts.amount) {
            return toast.error('Enter a withdrawable amount', {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        setLoading(true)
        const res = await axios.post(`/withdraw/save-crypto-withdrawal/${user.id}/${wallet.id}`, data)
        setLoading(false)
        console.log(res.data);
        if(res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            setTimeout(() => {
                navigate('/transactions')
            }, 3000);
        }else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const calcAmountfee = e => {
        let data = e.target.value;
        if(data.length > 0) {
            let sum = (parseInt(data) * parseFloat(admin.withfee)).toFixed(2)
            return setViewfee(sum)
        }
        return setViewfee('-')
    }

    return (
        <div>

        <div className="h-screen w-full flex">
            <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-700">
                            <Header />
                            <div className="flex flex-col items-center justify-center py-7">
                                <div className="drop-shadow-xl mb-3">Withdraw from {admin.name} wallet </div>
                                {admin.image && <img src={`${imgurl}/wallets/${admin.image}`} alt="" className="w-24 rounded-full" />}
                                <div className="text-3xl text-green-500 font-semibold capitalize flex">${(wallet.crypto / admin.current_price).toFixed(2)} <span className="uppercase text-sm italic">usd</span> </div>
                                <div className="font-semibold">{wallet.crypto / admin.current_price === 0 ? 0 : parseFloat(wallet.crypto).toFixed(9)} <span className="uppercase">{admin.short}</span> </div>
                            </div>
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="grid grid-cols-2 px-4 py-4">
                                    <button onClick={() => setTabs('bank')} className={`text-center ${tabs === 'bank' ? 'bg-indigo-700 text-indigo-100' : 'bg-slate-100 dark:bg-slate-500'} py-3 shadow-xl capitalize font-semibold rounded-tl-3xl rounded-bl-3xl`}>bank withdrawal</button>
                                    <button onClick={() => setTabs('crypto')} className={`text-center ${tabs === 'crypto' ? 'bg-indigo-700 text-indigo-100' : 'bg-slate-100 dark:bg-slate-500'} py-3 shadow-xl capitalize font-semibold rounded-tr-3xl rounded-br-3xl`}>send {admin.name}</button>
                                </div>
                                <div className="bg-white dark:bg-slate-700 rounded-xl shadow-xl px-4 py-8 mt-6">
                                    <div className="grid grid-cols-2 mb-6">
                                        <div className=""></div>
                                        <div className="border p-3 rounded-lg">
                                            <div className="text-sm font-semibold flex items-center justify-between w-full">Fee per withdrawal: <span>{Math.ceil(parseFloat(admin.withfee) * 100 - 100)}%</span> </div>
                                            <div className="text-sm font-semibold flex items-center justify-between w-full">Total Charges: <span> {viewfee} </span> </div>
                                        </div>
                                    </div>
                                    {tabs === 'bank' && forms.bankname && <form onSubmit={bankWithdrawal}>
                                        <div className="grid gap-8 mb-4 grid-cols-1 md:grid-cols-2">
                                            <div className="">
                                                <input type="text" readOnly name="account_holder" value={forms.account_holder} onChange={handleForms} placeholder='Account Holder' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                                            </div>
                                            <div className="">
                                                <input type="number" readOnly name="account_number" value={forms.account_number} onChange={handleForms} placeholder='Account Number' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                                            </div>
                                        </div>
                                        <div className="mb-4">
                                            <input type="text" readOnly name="bankname" value={forms.bankname} onChange={handleForms} placeholder='Bank Name' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                                        </div>
                                        <div className="my-4">
                                            <input type="text" name="amount" onKeyUp={calcAmountfee} value={forms.amount} onChange={handleForms} placeholder='Amount' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                                        </div>
                                        <div className="w-fit ml-auto">
                                            <button className={`py-3 px-4 ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} rounded-full shadow-xl text-indigo-200 font-semibold capitalize button text-sm flex items-center gap-3`}> {loading && <div className="loading"></div> } send withdrawal</button>
                                        </div>
                                    </form> }
                                    {tabs === 'bank' && !forms.bankname && <div className="text-xs font-semibold bg-orange-100 py-2 px-3 rounded-lg text-orange-700 mt-5 shadow-xl"> <i className="fas fa-exclamation-circle fa-fw"></i> Upload your bank details from your settings to enable withdrawal via bank</div>}
                                    {tabs === 'crypto' && <form onSubmit={cryptoWtihdrawal}>
                                        <div className="my-4">
                                            <input type="text" name="address" value={crypts.address} onChange={handleCryptos} placeholder='Crypto Wallet Address' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                                            <div className="text-xs text-red-600 dark:text-red-400 font-semibold">Crypto wallet address must match the wallet you are withdrawing from</div>
                                        </div>
                                        <div className="my-4">
                                            <input type="number" name="amount" onKeyUp={calcAmountfee} value={crypts.amount} onChange={handleCryptos}  placeholder='Amount' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                                        </div>
                                        <div className="my-4">
                                            <input type="password" name="password" value={crypts.password} onChange={handleCryptos}  placeholder='Confirm with Password' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                                        </div>
                                        <div className="w-fit ml-auto">
                                            <button className={`py-3 px-4 ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} rounded-full shadow-xl text-indigo-200 font-semibold capitalize button text-sm flex items-center gap-3`}> {loading && <div className="loading"></div> }send withdrawal</button>
                                        </div>
                                    </form>}
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Withdraw