import axios from 'axios'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom';
import { toast, ToastContainer } from 'react-toastify';

const Coinswap = props => {
    const { id } = useParams()
    const { user, imgurl } = useSelector(state => state.data)
    const dispatch = useDispatch()
    const [walletOption, setWalletoption] = useState('')
    const [amount, setAmount] = useState('')
    const [wallets, setWallet] = useState({})
    const [admin, setAdmin] = useState({})
    const [viewfee, setViewfee] = useState('-')
    const [singlewallet, setSinglewallet] = useState({})
    const [showwall, setShowwall] = useState(false)
    const [loading, setLoading] =  useState(false)

    const getWallet = useCallback(async () => {
        const res = await axios.get(`/wallet/all/${user.id}`)
        if (res.data.status === 200) {
            setWallet(res.data.msg)
        }
    }, [user])

    useEffect(() => {
        dispatch(() => getWallet())
    }, [dispatch, getWallet])


    const calcAmountfee = e => {
        let data = e.target.value;
        if (data.length > 0) {
            let sum = (parseInt(data) * parseFloat(admin.withfee)).toFixed(2)
            return setViewfee(sum)
        }
        return setViewfee('-')
    }

    const validateWallet = async e => {
        const val = e.target.value
        setWalletoption(val)
        if (val) {
            const res = await axios.get(`/wallet/single/${val}`)
            setShowwall(true)
            if (res.data.status === 200) {
                setSinglewallet(res.data.msg)
                setAdmin(res.data.msg.adminwallet)
            }
        }else {
            setShowwall(false)
        }
    }

    const saveSwap = async e => {
        e.preventDefault()
        const data = {
            wallet_to: walletOption,
            wallet_from: id,
            amount: parseFloat(viewfee),
        }

        if (walletOption === id) {
            return toast.info(`Cannot perform swap between the same coin, select a different coin`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if (amount === '') {
            return toast.info(`Enter a valid amount to swap`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        setLoading(true)
        const res = await axios.post(`/withdraw/swapcoin/${user.id}`, data)
        setLoading(false)
        if (res.data.status === 200) {
            setViewfee('-')
            setAmount('')
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white p-4 rounded-xl md:w-2/5 w-11/12 max-h-[80vh] extras overflow-y-auto dark:bg-slate-700 dark:text-slate-200">
                    <div className="flex justify-between items-center mb-4">
                        <div className="text-xl dark:text-slate-200 text-slate-600 drop-shadow-xl">Swap Coin</div>
                        <div className=""> <i onClick={props.closeview} className="fas fa-times cursor-pointer text-2xl dark:text-slate-200 text-slate-600 fa-fw"></i> </div>
                    </div>
                    <div className="grid grid-cols-2 mb-4">
                        <div className=""></div>
                        <div className="border dark:border-slate-500 p-3 rounded-lg">
                            <div className="text-sm font-semibold flex items-center justify-between w-full">Fee per Swap: <span>{!walletOption ? `-` : Math.ceil(parseFloat(admin.swapfee) * 100 - 100)}%</span> </div>
                            <div className="text-sm font-semibold flex items-center justify-between w-full">Total Charges: <span> {viewfee} </span> </div>
                        </div>
                    </div>
                    <form onSubmit={saveSwap}>
                        <div className="flex border dark:border-slate-500 rounded-xl p-1 mb-4">
                            <select name="wallet" value={walletOption} onChange={(e) => validateWallet(e)} className='w-full bg-transparent text-sm dark:bg-slate-700 dark:text-slate-200 text-slate-600 outline-none'>
                                <option value="">Select Wallet</option>
                                {wallets && wallets.length > 0 && wallets.map((item) => (
                                    <option key={item.id} value={item.id}> {item.adminwallet.name} ({item.adminwallet.short.toUpperCase()}) </option>
                                ))}
                            </select>
                            <button className="py-2 px-4 dark:text-slate-200 text-slate-600"> {!walletOption ? <i className="fas fa-wallet fa-fw"></i> : admin.image && <img src={`${imgurl}/wallets/${admin.image}`} alt="" className="w-6 rounded-full" />} </button>
                        </div>
                        {walletOption && admin.name && <div className="text-xs dark:text-slate-200 text-slate-600 text-right mb-4 font-semibold">Available balance in {admin.name.toUpperCase()}: ${(singlewallet.crypto / admin.current_price).toFixed(2)}</div>}
                        {walletOption && <div className="flex border dark:border-slate-500 rounded-lg p-1">
                            <input type="text" name="amount" onKeyUp={calcAmountfee} value={amount} onChange={e => { setAmount(e.target.value) }} placeholder='Amount' className="w-full bg-transparent outline-none text-sm p-2" />
                            <div className="p-2 dark:text-slate-200 text-slate-600"> <i className="fas fa-money-bill-wave fa-fw"></i> </div>
                        </div>}
                        <div className="w-fit ml-auto mt-6">
                            {showwall && <button disabled={loading ? true : false} className={`py-3 px-6 capitalize ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} text-indigo-200 rounded-full text-xs shadow-xl font-semibold flex items-center gap-3`}> {loading && <div className="loading"></div> } swap coin</button>}
                        </div>
                    </form>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Coinswap