import React from 'react'
import AFooter from '../../components/admin/AFooter'
import AHeader from '../../components/admin/AHeader'
import { Link } from 'react-router-dom';

const APages = () => {
    return (
        <div>
        <div className="h-screen w-full flex">
            <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
            <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                <div className="h-[89vh] md:h-screen overflow-y-auto">
                    <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-10">
                            <div className="">
                                <div className="text-2xl font-semibold capitalize drop-shadow-lg my-6 px-4">other pages</div>
                                <div className="">
                                    <Link to='/manage_aridrops'>
                                    <div className="grid grid-cols-2 py-2 px-4 border-b-[1px] dark:border-slate-500 mb-3">
                                            <div className="capitalize text-sm">manage airdrops</div>
                                            <div className="text-right"> <i className="fas fa-chevron-right text-slate-400 text-xs fa-fw"></i> </div>
                                        </div>
                                    </Link>
                                    <Link to='/manage_packages'>
                                    <div className="grid grid-cols-2 py-2 px-4 border-b-[1px] dark:border-slate-500 mb-3">
                                            <div className="capitalize text-sm">manage packages</div>
                                            <div className="text-right"> <i className="fas fa-chevron-right text-slate-400 text-xs fa-fw"></i> </div>
                                        </div>
                                    </Link>
                                    <Link to='/track_vendors_transfers'>
                                    <div className="grid grid-cols-2 py-2 px-4 border-b-[1px] dark:border-slate-500 mb-3">
                                            <div className="capitalize text-sm">track vendors transactions</div>
                                            <div className="text-right"> <i className="fas fa-chevron-right text-slate-400 text-xs fa-fw"></i> </div>
                                        </div>
                                    </Link>
                                    <Link to='/manage_users'>
                                    <div className="grid grid-cols-2 py-2 px-4 border-b-[1px] dark:border-slate-500 mb-3">
                                            <div className="capitalize text-sm">manage users</div>
                                            <div className="text-right"> <i className="fas fa-chevron-right text-slate-400 text-xs fa-fw"></i> </div>
                                        </div>
                                    </Link>
                                    <Link to='/manage_news  '>
                                    <div className="grid grid-cols-2 py-2 px-4 border-b-[1px] dark:border-slate-500 mb-3">
                                            <div className="capitalize text-sm">manage newsfeeds</div>
                                            <div className="text-right"> <i className="fas fa-chevron-right text-slate-400 text-xs fa-fw"></i> </div>
                                        </div>
                                    </Link>
                                    <Link to='/manage_transactions'>
                                    <div className="grid grid-cols-2 py-2 px-4 border-b-[1px] dark:border-slate-500 mb-3">
                                            <div className="capitalize text-sm">manage transactions</div>
                                            <div className="text-right"> <i className="fas fa-chevron-right text-slate-400 text-xs fa-fw"></i> </div>
                                        </div>
                                    </Link>
                                    <Link to='/admin_profile'>
                                    <div className="grid grid-cols-2 py-2 px-4 border-b-[1px] dark:border-slate-500 mb-3">
                                            <div className="capitalize text-sm">manage admin profile</div>
                                            <div className="text-right"> <i className="fas fa-chevron-right text-slate-400 text-xs fa-fw"></i> </div>
                                        </div>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default APages