import axios from 'axios';
import React from 'react'
import { useDispatch, useSelector } from 'react-redux';
import { toast, ToastContainer } from 'react-toastify';
import { getSingleVendor } from '../../store/reducers/dataReducer';

const AuthorizeVendor = props => {
    const { vendor, imgurl } = useSelector(state => state.data)
    const dispatch = useDispatch()

    const handleDecline = async e => {
        e.preventDefault()
        const res = await axios.get(`/vendor/decline-proposal/${vendor.id}`)
        if (res.data.status === 200) {
            props.sendSignal()
            dispatch(getSingleVendor(res.data.user))
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            console.log(res.data);
        }
    }

    const handleVerify = async e => {
        e.preventDefault()
        const res = await axios.get(`/vendor/verify-proposal/${vendor.id}`)
        if (res.data.status === 200) {
            props.sendSignal()
            dispatch(getSingleVendor(res.data.user))
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            console.log(res.data);
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white p-4 extras rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                    <div className="flex items-center justify-between mb-4">
                        <div className="text-xl text-slate-700 font-semibold">User's details</div>
                        <div className="">  <i onClick={props.closeview} className="fas fa-times fa-fw text-2xl cursor-pointer text-slate-500"></i> </div>
                    </div>
                    <div className="relative">
                        <div className="absolute top-0 right-0">
                            <button className={`py-1 px-3 text-xs rounded-full shadow-xl capitalize font-semibold italic ${vendor.status === 'pending' ? 'bg-slate-600 text-slate-200' : vendor.status === 'declined' ? 'bg-red-700 text-red-200' : 'bg-green-700 text-green-200'}`}> {vendor.status} </button>
                        </div>
                        <img src={`${imgurl}/profiles/${vendor.user.image}`} alt="" className="w-36 h-36 shadow-xl mb-4 object-cover object-center mx-auto rounded-full" />
                    </div>
                    <div className="grid grid-cols-2 mb-3">
                        <div className="text-sm font-semibold capitalize">fullname</div>
                        <div className="text-right text-sm"> {vendor.user.fname} {vendor.user.lname} </div>
                    </div>
                    <div className="grid grid-cols-2 mb-3">
                        <div className="text-sm font-semibold capitalize">Email Address</div>
                        <div className="text-right text-sm"> {vendor.user.email} </div>
                    </div>
                    <div className="grid grid-cols-2 mb-3">
                        <div className="text-sm font-semibold capitalize">Phone number</div>
                        <div className="text-right text-sm"> {vendor.user.contact} </div>
                    </div>
                    <div className="grid grid-cols-2 mb-3">
                        <div className="text-sm font-semibold capitalize">location</div>
                        <div className="text-right text-sm"> {vendor.user.country}, {vendor.user.state} </div>
                    </div>
                    <div className="grid grid-cols-2 mb-3">
                        <div className="text-sm font-semibold capitalize">residential Address</div>
                        <div className="text-right text-sm"> {vendor.user.address} </div>
                    </div>
                    <hr />
                    <div className="mb-3">
                        <div className="text-sm">Identity Card</div>
                        {vendor.idcard}
                        <img src={`${imgurl}/documents/${vendor.idcard}`} alt="" className="w-full max-h-72 border-dashed border-slate-500 object-cover object-center" />
                    </div>
                    <div className="mb-3">
                        {vendor.passport}
                        <div className="text-sm">Passport Photograph</div>
                        <img src={`${imgurl}/documents/${vendor.passport}`} alt="" className="w-full max-h-72 border-dashed border-slate-500 object-cover object-center" />
                    </div>
                    <div className="mb-3">
                        <div className="text-sm">Proof of Address</div>
                        <img src={`${imgurl}/documents/${vendor.poa}`} alt="" className="w-full max-h-72 border-dashed border-slate-500 object-cover object-center" />
                    </div>
                    <hr />
                    {vendor.status === 'pending' && <div className="grid grid-cols-2 my-4">
                        <form onSubmit={handleDecline}> <button className="btn py-2 px-8 bg-red-700 text-red-200 capitalize text-xs font-semibold rounded-full button"> decline</button> </form>
                        <form onSubmit={handleVerify} className='text-right'> <button className="btn py-2 px-8 bg-green-700 text-green-200 capitalize button text-xs font-semibold rounded-full"> verify</button> </form>
                    </div>}
                    {vendor.status === 'declined' &&<div className="text-right my-4">
                        <button className="btn py-2 px-8 bg-red-700 text-red-200 capitalize text-xs font-semibold rounded-full button"> remove from proposals</button>
                    </div>}
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default AuthorizeVendor