import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import moment from 'moment/moment'
import axios from 'axios'
import { getSingleAirdrop } from '../../store/reducers/dataReducer'

const SingleAirdrop = props => {
    const { imgurl } = useSelector(state => state.data)
    const dispatch = useDispatch()

    const setSingleAirdrop = async () => {
        const res = await axios.get(`/adminwallet/get-airdrop/${props.item.id}`)
        if (res.data.status === 200) {
            dispatch(getSingleAirdrop(res.data.msg))
            props.opens()
        }
    }
    return (
        <div className="flex items-center py-2 px-3 bg-white dark:bg-slate-700 dark:text-slate-200 rounded-xl shadow-xl">
            <div className="flex w-full">
                <div className=""> <img src={`${imgurl}/wallets/${props.item.adminwallet.image}`} alt="" className="w-10" /> </div>
                <div className="w-full px-2 mr-2">
                    <div className="font-semibold capitalize"> {props.item.adminwallet.name} <span className="uppercase">({props.item.adminwallet.short})</span> </div>
                    <div className="flex pb-1">
                        <div className="w-full text-sm text-slate-600 dark:text-slate-200">Airdrop {moment().isAfter(props.item.startd) ? 'started' : 'starts'}</div>
                        <div className="text-right w-full text-sm text-slate-600 dark:text-slate-200"> {moment(props.item.startd).fromNow()} </div>
                    </div>
                    <div className="flex pb-1">
                        <div className="w-full text-sm text-slate-600 dark:text-slate-200">Airdrop {moment().isAfter(props.item.stopd) ? 'ended' : 'ends'}</div>
                        <div className="text-right w-full text-sm text-slate-600 dark:text-slate-200"> {moment(props.item.stopd).fromNow()} </div>
                    </div>
                    <div className="flex pb-1">
                        <div className="w-full text-sm text-slate-600 dark:text-slate-200">Current giveaway</div>
                        <div className="text-right w-full text-sm text-slate-600 dark:text-slate-200">{(props.item.percent * 100 - 100).toFixed(2)}% </div>
                    </div>
                    <div className="flex pb-1">
                        <div className="w-full text-sm text-slate-600 dark:text-slate-200">Referral Commission</div>
                        <div className="text-right w-full text-sm text-slate-600 dark:text-slate-200">${props.item.refcom} </div>
                    </div>
                </div>
            </div>
            <div className="text-right self-end">
                <button className="py-2 px-3 text-xs bg-orange-700 text-orange-200 rounded-full shadow-xl font-semibold capitalize hover:bg-orange-600" onClick={setSingleAirdrop}>modify</button>
            </div>
        </div>
    )
}

export default SingleAirdrop