import React, { useCallback, useEffect, useRef, useState } from 'react'
import Footer from '../../components/user/Footer';
import Header from '../../components/user/Header';
import { useNavigate, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import axios from 'axios';
import Coinswap from '../../components/user/Coinswap';
import { toast, ToastContainer } from 'react-toastify';
import Sidenav from './../../components/user/Sidenav';

const CoinRoom = () => {
    const { id } = useParams()
    const dispatch = useDispatch()
    const [loading, setLoading] = useState(false)
    const copyinput = useRef()
    const [wallet, setWallet] = useState({})
    const [admin, setAdmin] = useState({})
    const { imgurl, user } = useSelector(state => state.data)
    const navigate = useNavigate()
    const [view, setView] = useState(false)
    const [viewfee, setViewfee] = useState('-')
    const [forms, setForms] = useState({
        address: '',
        amount: '',
        password: ''
    })

    const handleforms = e => {
        setForms({ ...forms, [e.target.name]: e.target.value })
    }

    const getWallet = useCallback(async () => {
        const res = await axios.get(`/wallet/single/${id}`)
        if (res.data.status === 200) {
            setWallet(res.data.msg)
            setAdmin(res.data.msg.adminwallet)
        }
        // eslint-disable-next-line
    }, [])


    useEffect(() => {
        dispatch(() => getWallet())
        // eslint-disable-next-line
    }, [dispatch, getWallet])


    const calcAmountfee = e => {
        let data = e.target.value;
        if (data.length > 0) {
            let sum = (parseInt(data) * parseFloat(admin.withfee)).toFixed(2)
            return setViewfee(sum)
        }
        return setViewfee('-')
    }

    const handleCopy = () => {
        copyinput.current.select();
        copyinput.current.setSelectionRange(0, 99999);
        navigator.clipboard.writeText(copyinput.current.value);
        toast.info('Copied', {
            position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
        });
    }

    const handlep2pTransfer = async e => {
        e.preventDefault()
        setLoading(true)
        const data = {
            address: forms.address,
            wallet: wallet.id,
            amount: parseInt(viewfee),
            password: forms.password
        }

        if (!forms.address) {
            setLoading(false)
            return toast.error('Provide a valid p2p wallet address', {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        if (!forms.amount) {
            setLoading(false)
            return toast.error('Provide a valid amount to send', {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        const res = await axios.post(`/withdraw/peer-transfer/${user.id}`, data)
        setLoading(false)
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            setForms({ ...forms, password: '', amount: '' })
            setTimeout(() => {
                navigate('/transactions')
            }, 3000);
        } else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            {view && <Coinswap closeview={() => { setView(false); dispatch(() => getWallet()) }} />}

            <div className="h-screen w-full flex">
                <Sidenav />
                <div className="bg-slate-50 dark:bg-slate-800 dark:text-slate-200 font-semibold w-full md:w-[80%]">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit dark:bg-slate-700 pb-5 bg-white">
                            <Header />
                            {/* <div className="flex flex-col justify-center items-center py-4">
                                {admin.image && <img src={`${imgurl}/wallets/${admin.image}`} alt="" className="w-20 rounded-full" />}
                                <div className="font-semibold dark:text-slate-200 text-slate-600 text-3xl flex">${(wallet.crypto / admin.current_price).toFixed(2)} <span className="uppercase text-sm italic">usd</span> </div>
                                <div className="font-semibold dark:text-slate-200 text-slate-600 text-sm">  {wallet.crypto / admin.current_price === 0 ? 0 : parseFloat(wallet.crypto).toFixed(9)} <span className="uppercase">btc</span> </div>
                                <div className="text-xs text-green-700 font-semibold capitalize"> <i className="fas fa-exchange-alt fa-fw"></i> +2.05% </div>
                            </div> */}
                            <div className="flex items-center justify-center flex-col mb-8">
                                {admin.image && <img src={`${imgurl}/wallets/${admin.image}`} alt="" className="w-20 rounded-full" />}
                                <div className="text-2xl capitalize">{admin.name} <span className="uppercase"> ({admin.short})</span> </div>
                            </div>
                            <div className="flex items-center justify-around">
                                <div className="text-center cursor-pointer" onClick={() => navigate(`/coin/${id}/deposit`)}>
                                    <div className="bg-sky-800 text-sky-200 rounded-full py-3.5 px-4 w-fit mx-auto"><i className="fas fa-sync-alt text-xl fa-fw"></i></div>
                                    <div className="dark:text-slate-200 text-slate-700 text-sm font-semibold capitalize mt-2">deposit</div>
                                </div>
                                <div className="text-center cursor-pointer" onClick={() => setView(true)}>
                                    <div className="bg-sky-800 text-sky-200 rounded-full py-3.5 px-4 w-fit mx-auto"><i className="fas fa-random text-xl fa-fw"></i></div>
                                    <div className="dark:text-slate-200 text-slate-700 text-sm font-semibold capitalize mt-2">swap</div>
                                </div>
                                <div className="text-center cursor-pointer" onClick={() => navigate('/vendors')}>
                                    <div className="bg-sky-800 text-sky-200 rounded-full py-3.5 px-4 w-fit mx-auto"><i className="fas fa-exchange-alt text-xl fa-fw"></i></div>
                                    <div className="dark:text-slate-200 text-slate-700 text-sm font-semibold capitalize mt-2">buy</div>
                                </div>
                                <div className="text-center cursor-pointer" onClick={() => navigate(`/coin/${id}/withdraw`)}>
                                    <div className="bg-sky-800 text-sky-200 rounded-full py-3.5 px-4 w-fit mx-auto"><i className="fas fa-credit-card text-xl fa-fw"></i></div>
                                    <div className="dark:text-slate-200 text-slate-700 text-sm font-semibold capitalize mt-2">withdraw/send</div>
                                </div>
                            </div>
                        </div>
                        <div className="h-[auto] pb-24">
                            <div className="grid grid-cols-2 gap-3 w-11/12 md:w-2/3 mx-auto my-9">
                                <div className="bg-gradient-to-r from-sky-100 to-slate-200 dark:bg-gradient-to-r dark:from-sky-900 dark:to-slate-600 rounded-xl shadow-xl p-2">
                                    <div className="text-xs capitalize">available balance</div>
                                    <div className="text-lg text-right capitalize">${parseFloat(wallet.crypto / admin.current_price).toFixed(2)}</div>
                                    <div className="text-xs text-right uppercase">{wallet.crypto / admin.current_price === 0 ? 0 : parseFloat(wallet.crypto).toFixed(9)} {admin.short}</div>
                                </div>
                                <div className="bg-gradient-to-r from-red-100 to-slate-200 dark:bg-gradient-to-r dark:from-red-900 dark:to-slate-600 rounded-xl shadow-xl p-2">
                                    <div className="text-xs capitalize">locked balance</div>
                                    <div className="text-lg text-right capitalize">${!wallet.lockedbal ? '0.00' : parseFloat(wallet.lockedbal).toFixed(2)}</div>
                                    <div className="text-xs text-right uppercase">{!wallet.lockedbal ? 0 : parseFloat(wallet.lockedbal * admin.current_price).toFixed(9)} {admin.short}</div>
                                </div>
                            </div>
                            <div className="w-[97%] mx-auto py-3 bg-white dark:bg-slate-700 shadow-xl rounded-xl mt-6 px-4">
                                <div className="text-lg tetx-slate-600"> <span className="uppercase text-orange-400 font-semibold">{admin.name}</span> P2P Wallet Address</div>
                                <div className="text-xs mb-5">Note: P2P wallet Address is only use to transfer <span className="text-orange-500 capitalize">{admin.name}</span> within the JoggleCryp platform and not for external wallet transfers.</div>
                                <div className="flex items-center">
                                    <input type="text" ref={copyinput} readOnly defaultValue={wallet.ads} className="outline-none bg-transparent dark:border-slate-500 w-full border rounded-tl-xl rounded-bl-xl p-2 text-sm" />
                                    <button onClick={handleCopy} className="bg-indigo-700 text-indigo-200 text-sm font-semibold py-2 px-6 capitalize border-[1px] border-indigo-700 rounded-tr-xl rounded-br-xl">copy</button>
                                </div>
                            </div>
                            <div className="flex flex-col items-center justify-center py-9">
                                <div className="text-4xl mb-2 dark:text-slate-200 text-slate-600"> <i className="far fa-user fa-fw"></i> <i className="fas fa-exchange-alt fa-fw"></i> <i className="far fa-user fa-fw"></i> </div>
                                <div className="text-lg dark:text-slate-200 text-slate-600">P2P Transfer</div>
                            </div>
                            <div className="w-[97%] mx-auto">
                                <div className="grid grid-cols-2 mb-4">
                                    <div className=""></div>
                                    <div className="border p-3 rounded-lg">
                                        <div className="text-sm font-semibold flex items-center justify-between w-full">Fee per Transfer: <span>{Math.ceil(parseFloat(admin.p2pfee) * 100 - 100)}%</span> </div>
                                        <div className="text-sm font-semibold flex items-center justify-between w-full">Total Charges: <span> {viewfee} </span> </div>
                                    </div>
                                </div>
                                <form onSubmit={handlep2pTransfer}>
                                    <div className="mb-4">
                                        <div className="dark:text-slate-200 text-slate-600 text-sm font-semibold">PEER Wallet Address</div>
                                        <input type="text" name="address" onChange={handleforms} value={forms.address} className="outline-none bg-transparent dark:border-slate-500 w-full text-sm py-3 px-3 rounded-lg border" placeholder='Enter P2P wallet address' />
                                    </div>
                                    <div className="mb-4">
                                        <div className="dark:text-slate-200 text-slate-600 text-sm font-semibold">Amount to spend</div>
                                        <input type="number" name="amount" onKeyUp={calcAmountfee} value={forms.amount} onChange={handleforms} className="outline-none bg-transparent dark:border-slate-500 w-full text-sm py-3 px-3 rounded-lg border" placeholder='Enter Amount' />
                                    </div>
                                    <div className="mb-4">
                                        <div className="dark:text-slate-200 text-slate-600 text-sm font-semibold">Enter Password to confirm transfer</div>
                                        <input type="password" name="password" value={forms.password} onChange={handleforms} className="outline-none bg-transparent dark:border-slate-500 w-full text-sm py-3 px-3 rounded-lg border" placeholder='********' />
                                    </div>
                                    <div className="">
                                        <button disabled={loading ? true : false} className={`${loading ? 'bg-sky-500' : 'bg-sky-800'} text-sky-200 w-full py-3 capitalize font-semibold rounded-lg button flex items-center justify-center gap-3`}> {loading && <div className="loading"></div> } confirm payment</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div className="">
                        <Footer />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default CoinRoom