import axios from 'axios'
import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { toast, ToastContainer } from 'react-toastify'

const ModifyWallet = props => {
    const { adminwallet } = useSelector(state => state.data)
    const [image, setImage] = useState(null)
    const [qrcode, setQrcode] = useState(null)
    const [forms, setForms] = useState({
        name: adminwallet ? adminwallet.name : '',
        short: adminwallet ? adminwallet.short : '',
        price: adminwallet ? adminwallet.price : '',
        current_price: adminwallet ? adminwallet.current_price : '',
        max_with: adminwallet ? adminwallet.max_with : '1.80',
        min_with: adminwallet ? adminwallet.min_with : '',
        percent: adminwallet ? adminwallet.percent : '1.0',
        swapfee: adminwallet ? adminwallet.swapfee : '',
        bonusfee: adminwallet ? adminwallet.bonusfee : '',
        withfee: adminwallet ? adminwallet.withfee : '',
        p2pfee: adminwallet ? adminwallet.p2pfee : '',
        address: adminwallet ? adminwallet.ads : '',
    })
    const handleforms = e => {
        // e.persist()
        setForms({ ...forms, [e.target.name]: e.target.value })
    }
    const handleprice = e => {
        let value = e.target.value
        if (forms.percent !== '') {
            let calc = forms.percent * value
            setForms({ ...forms, current_price: calc.toFixed(9) })
            // console.log(calc.toFixed(9))
        }
    }
    const handlePercent = e => {
        let value = e.target.value
        if (forms.price !== '') {
            let calc = forms.price * value
            setForms({ ...forms, current_price: calc.toFixed(9) })
            // console.log(calc.toFixed(9))
        }
    }
    const saveWallet = async e => {
        e.preventDefault()
        const data = new FormData();
        data.append('name', forms.name)
        data.append('short', forms.short)
        data.append('price', forms.price)
        data.append('current_price', forms.current_price)
        data.append('max_with', forms.max_with)
        data.append('min_with', forms.min_with)
        data.append('percent', forms.percent)
        data.append('swapfee', forms.swapfee)
        data.append('bonusfee', forms.bonusfee)
        data.append('withfee', forms.withfee)
        data.append('p2pfee', forms.p2pfee)
        data.append('address', forms.address)
        data.append('image', !image ? adminwallet.image : image)
        data.append('qrcode', !qrcode ? adminwallet.qrcode : qrcode)
        console.log(data)

        const res = !adminwallet.name ? await axios.post('/adminwallet/add-wallet', data) : await axios.put(`/adminwallet/update-wallet/${adminwallet.id}`, data)
        if (res.data.status === 200) {
            console.log(res.data);
            props.sendSignal()
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            if (!adminwallet) {
                setForms({
                    name: '', short: '', price: '', current_price: '', max_with: '', min_with: '', percent: '1.0', swapfee: '', bonusfee: '', withfee: '', p2pfee: '', address: '',
                })
            }
        } else {
            console.log(res.data);
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white p-4 rounded-xl md:w-2/5 w-11/12 max-h-[85vh] extras overflow-y-auto dark:bg-slate-700 dark:text-slate-200">
                        <form onSubmit={saveWallet}>
                            <div className="text-right">
                                <div className="">  <i onClick={props.closeform} className="fas fa-times fa-fw text-2xl cursor-pointer text-slate-500"></i> </div>
                            </div>
                            <div className="grid grid-cols-2 gap-5 mb-4">
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Name of wallet</div>
                                    <input type="text" name="name" value={forms.name} onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='Name of wallet' />
                                </div>
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Wallet short name</div>
                                    <input type="text" name="short" value={forms.short} onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='Wallet short name' />
                                </div>
                            </div>
                            <div className="grid grid-cols-3 gap-5 mb-4">
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Price</div>
                                    <input type="text" name="price" value={forms.price} onChange={handleforms} onKeyUp={handleprice} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='price' />
                                </div>
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Percentage</div>
                                    <input type="text" name="percent" value={forms.percent} onChange={handleforms} onKeyUp={handlePercent} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='percent' />
                                </div>
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Current Price</div>
                                    <input type="text" name="current_price" value={forms.current_price} readOnly onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='current price' />
                                </div>
                            </div>
                            <div className="grid grid-cols-2 gap-5 mb-4">
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Min Withdrawal</div>
                                    <input type="text" name="min_with" value={forms.min_with} onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='Min Withdrawal' />
                                </div>
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Max withdrawal</div>
                                    <input type="text" name="max_with" value={forms.max_with} onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='Max Withdrawal' />
                                </div>
                            </div>
                            <div className="grid grid-cols-4 gap-5 mb-4">
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Swap Fee</div>
                                    <input type="text" name="swapfee" value={forms.swapfee} onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='Swap fee' />
                                </div>
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Bonus Fee</div>
                                    <input type="text" name="bonusfee" value={forms.bonusfee} onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='bonus fee' />
                                </div>
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Withd Fee</div>
                                    <input type="text" name="withfee" value={forms.withfee} onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='with fee' />
                                </div>
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">P2P Fee</div>
                                    <input type="text" name="p2pfee" value={forms.p2pfee} onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='p2p fee' />
                                </div>
                            </div>
                            <div className="mb-4">
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Wallet Address</div>
                                    <input type="text" name="address" value={forms.address} onChange={handleforms} className="outline-none bg-transparent border-b-[1px] text-xs w-full" placeholder='Wallet Address' />
                                </div>
                            </div>
                            <div className="grid grid-cols-2 gap-8 mb-4">
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Upload wallet logo</div>
                                    <input type="file" name="image" onChange={e => setImage(e.target.files[0])} className="outline-none bg-transparent border-b-[1px] text-xs w-full" />
                                </div>
                                <div className="">
                                    <div className="text-xs text-slate-500 font-semibold">Upload wallet Qrcode image</div>
                                    <input type="file" name="qrcode" onChange={e => setQrcode(e.target.files[0])} className="outline-none bg-transparent border-b-[1px] text-xs w-full" />
                                </div>
                            </div>
                            <div className="text-right">
                                <button className="py-3 px-5 text-white bg-indigo-600 rounded-full shadow-xl text-xs font-semibold capitalize">save wallet</button>
                            </div>
                        </form>
                    </div>
                </div>
                <ToastContainer />
            </div>
    )
}

export default ModifyWallet