import axios from 'axios'
import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { toast, ToastContainer } from 'react-toastify'

const UpdateEmailSetting = props => {
    const { user } = useSelector(state => state.data)
    const [password, setPassword] = useState('')
    const [pass, setPass] = useState(1)
    const [email, setEmail] = useState('')
    const [code, setCode] = useState('')
    const [resetcode, setResetcode] = useState('')
    const [loading, setLoading] = useState(false)

    const confirmWithPassword = async e => {
        e.preventDefault()
        setLoading(true)
        if (!password) {
            setLoading(false)
            return toast.error(`Enter your current Account password`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        const data = { password: password }
        const res = await axios.put(`/user/confirm-password-settings/${user.id}`, data)
        setLoading(false)
        if (res.data.status === 200) {
            setPass(2)
        } else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const handleEmailUpdate = async e => {
        e.preventDefault()
        setLoading(true)
        if (!email) {
            setLoading(false)
            return toast.error(`Email Address field is required`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const data = {
            email: email
        }
        const res = await axios.put(`/user/update-email-address/${user.id}`, data)
        setLoading(false)
        if (res.data.status === 200) {
            setPass(3)
            setCode(res.data.msg)
            console.log(res.data.msg)
        } else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const confirmEmailCode = async e => {
        e.preventDefault()
        setLoading(true)
        if (!resetcode) {
            setLoading(false)
            return toast.error(`Enter a valid verification code`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if (resetcode !== code) {
            setLoading(false)
            return toast.error(`Invalid Verification code detected`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const data = {
            code: code,
            email: email
        }
        const res = await axios.put(`/user/complete-email-update/${user.id}`, data)
        setLoading(false)
        if (res.data.status === 200) {
            setPass(1)
            return toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    const resendCode = async () => {
        const res = await axios.get(`/user/send-verification-code/${email}`)
        if(res.data.status === 200) {
            console.log(res.data.msg);
            setCode(res.data.msg)
        }else { 
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div className="h-screen w-full bg-black/60 flex items-center top-0 left-0 justify-center fixed z-10">
            <div className="bg-white dark:bg-slate-700 dark:text-slate-200 extras p-4 rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                <div className="flex justify-between items-center mb-4">
                    <div className="text-xl dark:text-slate-200 text-slate-600 drop-shadow-xl">Change Account Email Address</div>
                    <div className=""> <i onClick={props.closeview} className="fas fa-times cursor-pointer text-2xl dark:text-slate-200 text-slate-600 fa-fw"></i> </div>
                </div>
                <div className="">
                    <form className={pass === 1 ? '' : 'hidden'} onSubmit={confirmWithPassword}>
                        <div className="text-xs text-slate-600 dark:text-slate-200 mb-3">Enter your current Account password to confirm this process</div>
                        <input type="password" name="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="Enter Password" className="bg-transparent outline-none border dark:border-slate-500 w-full rounded p-2 text-sm" />
                        <div className="w-fit ml-auto mt-4"><button disabled={loading ? true : false} className={`py-2 px-6 ${loading ? 'bg-indigo-400' : 'bg-indigo-600'} flex items-center gap-3 rounded-full text-xs shadow-xl font-semibold text-slate-200 capitalize`}> {loading && <div className="loading"></div>} next</button></div>
                    </form>
                    <form className={pass === 2 ? '' : 'hidden'} onSubmit={handleEmailUpdate}>
                        <div className="mb-4">
                            <div className="text-xs mb-2">New Email Address</div>
                            <input type="email" name="email" value={email} onChange={e => setEmail(e.target.value)} placeholder="Enter New Email Address" className="outline-none bg-transparent w-full border dark:border-slate-500 rounded-lg p-2 text-sm" />
                        </div>
                        <div className="w-fit ml-auto mt-4"><button disabled={loading ? true : false} className={`py-2 px-6 ${loading ? 'bg-indigo-400' : 'bg-indigo-600'} flex items-center gap-3 rounded-full text-xs shadow-xl font-semibold text-slate-200 capitalize`}> {loading && <div className="loading"></div>} continue</button></div>
                    </form>
                    <form className={pass === 3 ? '' : 'hidden'} onSubmit={confirmEmailCode}>
                        <div className="mb-4">
                            <div className="text-xs mb-2">A verification code has been sent to {email.slice(0, 3)}***********{email.slice(-10)} </div>
                            <input type="text" name="resetcode" value={resetcode} onChange={e => setResetcode(e.target.value)} placeholder="*******" className="outline-none bg-transparent w-full border dark:border-slate-500 rounded-lg p-2 text-sm" />
                        </div>
                        <div className="mb-4"> <div className="flex gap-2 items-center text-sm text-slate-700"> Didn't recieve code? <div className="text-indigo-600 cursor-pointer" onClick={resendCode}>resend code</div> </div> </div>
                        <div className="w-fit ml-auto mt-4"><button disabled={loading ? true : false} className={`py-2 px-6 ${loading ? 'bg-indigo-400' : 'bg-indigo-600'} flex items-center gap-3 rounded-full text-xs shadow-xl font-semibold text-slate-200 capitalize`}> {loading && <div className="loading"></div>} confirm</button></div>
                    </form>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default UpdateEmailSetting
