import Cookies from 'js-cookie'
import React, { useEffect, useState } from 'react'
import { isExpired } from 'react-jwt';
import { useSelector } from 'react-redux';
import { Navigate, Outlet, useNavigate } from 'react-router-dom';
import loop from '../images/loop1.gif'

const UnAuthenticatedRoutes = () => {
    const token = Cookies.get('token')
    const exp = isExpired(token)
    const navigate = useNavigate()
    const {user} = useSelector(state => state.data)
    const [loading, setLoading] = useState(true)
    useEffect(() => {
      setTimeout(() => {
        if(!exp) {
          if(user.role === 'user') {
            navigate('/dashboard')
            setLoading(false)
          }else {
            navigate('/admin')
            setLoading(false)
          }
          setLoading(false)
        }else {
          setLoading(false)
        }
      }, 3000);
        // eslint-disable-next-line
    }, [])
  return (
    loading ? <div className="h-screen bg-slate-50 w-full flex items-center justify-center"> <div className="w-48 h-48" style={{ background: `url(${loop})` }}></div> </div> :
    exp ? <Outlet /> : user.role === 'user' ? <Navigate to='/dashboard' />  : <Navigate to='/admin' /> 
  )
}

export default UnAuthenticatedRoutes