import axios from 'axios'
import moment from 'moment/moment'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import img from '../../images/img.png'

const AllFeedBacks = props => {
    const { imgurl } = useSelector(state => state.data)
    const [feeds, setFeeds] = useState([])
    const [total, setTotal] = useState('')
    const [allgood, setAllgood] = useState('')
    const [allbad, setAllbad] = useState('')
    const dispatch = useDispatch()

    const getfeeds = useCallback(async () => {
        const res = await axios.get(`/vendor/my-feedbacks/${props.trans}`)
        if (res.data.status === 200) {
            setFeeds(res.data.msg)
            setTotal(res.data.count)
            setAllgood(res.data.good)
            setAllbad(res.data.bad)
        } else {
            console.log(res.data);
        }
    }, [props])
    useEffect(() => {
        dispatch(() => getfeeds())
    }, [dispatch, getfeeds])

    // const getgoodfeeds = () => {
    //     //
    // }

    return (
        <div className="">
            <div className="grid grid-cols-3">
                <div className="text-xs flex gap-1 justify-center items-center capitalize text-center">all  <div className="bg-slate-500 w-fit text-slate-100 text-xs px-[6px] py-[1px] rounded-md">{total > 99 ? '99+' : total}</div> </div>
                <div className="text-xs flex gap-1 justify-center items-center capitalize text-center">positive  <div className="bg-slate-500 text-slate-100 w-fit text-xs px-[6px] py-[1px] rounded-md">{allgood > 99 ? '99+' : allgood}</div> </div>
                <div className="text-xs flex gap-1 justify-center items-center capitalize text-center">negative  <div className="bg-slate-500 text-slate-100 w-fit text-xs px-[6px] py-[1px] rounded-md">{allbad > 99 ? '99+' : allbad}</div> </div>
            </div>
            <div className="mt-3">
                {/* each feedback */}
                {feeds.length > 0 ? feeds.map((item) => (
                    <div className="flex gap-1 border-b dark:border-slate-500 py-2" key={item.id}>
                        <div className=""> <img src={item.user.image ? `${imgurl}/profiles/${item.user.image}` : img} alt="" className="w-8 h-8 rounded-full" /> </div>
                        <div className="w-[97%] mx-auto">
                            <div className="flex items-center justify-between w-full">
                                <div className="text-sm">{item.user.fname}</div>
                                <div className="flex gap-5 items-center my-2">
                                    <div className="text-xs"> <button className={`text-xs ${item.status === 'good' ? 'text-green-500' : 'text-red-500'}`}> <i className={`far ${item.status === 'good' ? 'fa-thumbs-up' : 'fa-thumbs-down'} fa-fw`}></i> </button> </div>
                                </div>
                            </div>
                            <div className="text-xs text-slate-400 capitalize"> <i className="far fa-clock fa-fw"></i> {moment(item.createdAt).calendar()}</div>
                            <div className="text-xs p-2 bg-slate-100 dark:bg-slate-800 rounded-lg mt-2">{item.text}</div>
                        </div>
                    </div>
                )) : <div className="text-sm">Vendor has no feedbacks yet</div>}

                {/* ========================ends */}
            </div>
        </div>
    )
}

export default AllFeedBacks