import React, { useState } from 'react'
import { ToastContainer, toast } from 'react-toastify'
import axios from 'axios';
import { useSelector } from 'react-redux';

const ModifyNotify = (props) => {
  const {notify} = useSelector(state => state.data)
  const [loading, setLoading] = useState(false)
  const [forms, setForms] = useState({
      title: '',
      message: '',
      duration: '',
  })
  const handleForms = (e) => {
      setForms({...forms, [e.target.name]: e.target.value})
  }

  const saveNotify = async (e) => {
      e.preventDefault()
      setLoading(true)
      const data = {
          title: forms.title,
          message: forms.message,
          duration: forms.duration
      }

      if(!forms.title || !forms.message) {
          setLoading(false)
          return toast.error(`All form fields are mandatory`, {
              position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
          });
      }

      const res = await axios.post(`/news/add-notification`, data)
      setLoading(false)
      if(res.data.status === 200) {
          setForms({
              title: '',
              message: '',
              duration: ''
          })
          props.sendSignal()
          toast.success(res.data.msg, {
              position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
          });
      }else {
          toast.error(res.data.msg, {
              position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
          });
      }
  }
  return (
    <div>
      <div className="h-screen w-full bg-black/60 flex items-center dark:text-slate-200 justify-center fixed z-10">
        <div className="bg-white dark:bg-slate-700 p-4 extras rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
          <div className="flex items-center justify-between mb-4">
            {notify.id ? <form> <button className="btn py-2 px-8 bg-red-700 text-red-200 capitalize text-xs font-semibold rounded-full"> delete</button> </form> : <div></div>}
            <div className="">  <i onClick={props.closeform} className="fas fa-times fa-fw text-2xl cursor-pointer text-slate-500"></i> </div>
          </div>
          <form onSubmit={saveNotify}>
            <div className="mb-4">
              <div className="text-sm">Title of Notification</div>
              <input type="text" name="title" value={forms.title} onChange={handleForms} placeholder="Enter Title" className="outline-none bg-transparent rounded-lg text-sm border w-full p-2 dark:border-slate-600" />
            </div>
            <div className="mb-4">
              <div className="text-sm">Duration</div>
              <input type="date" name="duration" value={forms.duration} onChange={handleForms} placeholder="Enter Duration" className="outline-none bg-transparent rounded-lg text-sm border w-full p-2 dark:border-slate-600" />
            </div>
            <div className="mb-4">
              <div className="text-sm">Notification Message</div>
              <textarea name="message" value={forms.message} onChange={handleForms} placeholder="Type message here!..." cols="30" rows="10" className="outline-none bg-transparent rounded-lg text-sm border w-full p-2 dark:border-slate-600"></textarea>
            </div>
            <div className="w-fit ml-auto">
              <button className="py-2.5 px-6 flex items-center text-indigo-100 bg-indigo-600 rounded-full text-sm font-semibold capitalize gap-3" disabled={loading ? true : false}> {loading && <div className="loading"></div>} save notification </button>
            </div>
          </form>
        </div>
      </div>
      <ToastContainer />
    </div>
  )
}

export default ModifyNotify
