import axios from 'axios'
import React from 'react'
import { useSelector } from 'react-redux'
import { Link, useNavigate } from 'react-router-dom'
import { toast, ToastContainer } from 'react-toastify'

const Footer = () => {
  const {user} = useSelector(state => state.data)
  const navigate = useNavigate()
  const enterMining = async (e) => {
    e.preventDefault()
    const res = await axios.get(`/package/get-mining/${user.id}`)
    if (res.data.msg.status === 'pending') {
      return toast.info('Looks like you have no active mining in progress', {
          position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
      });
    }
    navigate('/mining')
  }
  return (
    <div className='h-[11vh] border-t md:hidden overflow-hidden dark:bg-slate-700 dark:border-t-slate-400'>
        <div className="grid grid-cols-5">
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/' onClick={enterMining} className='flex items-center flex-col'> <i className="fas text-lg fa-chart-line fa-fw"></i> <div className="text-xs capitalize font-semibold">mining</div> </Link> </div>
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/transactions' className='flex items-center flex-col'> <i className="fas text-lg fa-exchange-alt fa-fw"></i> <div className="text-xs capitalize font-semibold">transactions</div> </Link> </div>
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/dashboard' className='flex items-center flex-col'> <i className="fas text-4xl fa-house-damage fa-fw"></i></Link> </div>
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/settings' className='flex items-center flex-col'> <i className="fas text-lg fa-cog fa-fw"></i> <div className="text-xs capitalize font-semibold">settings</div> </Link> </div>
            <div className="py-2 button text-sky-900 dark:text-slate-200"> <Link to='/pages' className='flex items-center flex-col'> <i className="fas text-lg fa-align-right fa-fw"></i> <div className="text-xs capitalize font-semibold">pages</div> </Link> </div>
        </div>
        <ToastContainer />
    </div>
  )
}

export default Footer