import axios from 'axios'
import React from 'react'
import { useDispatch } from 'react-redux';
import { getSinglePackage } from '../../store/reducers/dataReducer';

const UserPackages = props => {
    const dispatch = useDispatch()
    const viewpack = async id => {
        const res = await axios.get(`/package/${id}`)
        if(res.data.status === 200) {
            dispatch(getSinglePackage(res.data.msg))
            props.openNextpage()
        }
    }

    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white dark:bg-slate-700 px-4 py-6 rounded-xl md:w-2/5 extras w-11/12 max-h-[80vh] overflow-y-auto">
                    <div className="flex justify-between items-center pb-6">
                        <div className="text-xl font-semibold dark:text-slate-200 text-slate-600 drop-shadow-xl">Select your preferred package</div>
                        <div className="text-2xl text-zinc-600 dark:text-zinc-300"> <i className="fas fa-times fa-fw cursor-pointer" onClick={props.closeViews}></i> </div>
                    </div>
                    <div className="grid grid-cols-2 gap-5">
                        {props.packages.map((item) => (
                            <div className="border shadow-xl rounded-tr-[2rem] rounded-bl-[2rem] bg-gradient-to-r from-white to-sky-100 pb-3 dark:from-slate-900 dark:to-slate-800 dark:border-slate-500 dark:text-slate-200" key={item.id}>
                                <div className="bg-indigo-700 w-2/3 text-indigo-100 capitalize shadow-xl font-semibold p-3 rounded-tr-3xl rounded-br-3xl">{item.name}</div>
                                <div className="bg-orange-500 w-2/3 ml-auto text-orange-100 capitalize shadow-xl font-semibold text-xl p-3 rounded-tl-3xl rounded-bl-3xl">${item.amount}</div>
                                <div className="grid grid-cols-2 p-2 dark:border-b-slate-500 border-b-[1px] mt-3">
                                            <div className="text-xs font-semibold">Bonus to recieve</div>
                                            <div className="text-xs text-right font-semibold">${item.bonus}</div>
                                        </div>
                                        <div className="grid grid-cols-2 p-2 dark:border-b-slate-500 border-b-[1px]">
                                            <div className="text-xs font-semibold">Topup after</div>
                                            <div className="text-xs text-right font-semibold">{item.topup} hour{item.topup !== 1 ? 's' : ''} </div>
                                        </div>
                                        <div className="grid grid-cols-2 p-2 dark:border-b-slate-500 border-b-[1px]">
                                            <div className="text-xs font-semibold">Duration</div>
                                            <div className="text-xs text-right font-semibold">{item.duration} Day{item.duration !== 1 ? 's' : ''} </div>
                                        </div>
                                        <div className="grid grid-cols-2 p-2">
                                            <div className="text-xs font-semibold">Referral commission</div>
                                            <div className="text-xs text-right font-semibold">{(item.refcom * 100 - 100).toFixed(2)}%</div>
                                        </div>
                                <div className="text-right px-3 pt-3">
                                    <button onClick={() => viewpack(item.id)} className='bg-indigo-700 text-indigo-100 py-1 px-3 rounded-full shadow-xl text-xs capitalize font-semibold'>purchase</button>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </div>
    )
}

export default UserPackages