import axios from 'axios'
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { ToastContainer } from 'react-toastify'
import Bankdetails from '../../components/user/Bankdetails'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import VisibleWallets from '../../components/user/VisibleWallets'
import ChangePassword from '../../components/user/ChangePassword'
import UpdateEmailSetting from '../../components/user/UpdateEmailSetting'
import Logout from '../../components/general/Logout'
import { logoutUser } from '../../store/reducers/dataReducer'
import Sidenav from './../../components/user/Sidenav';

const Settings = () => {
    const dispatch = useDispatch()
    const { user, logout } = useSelector(state => state.data)
    const [mybank, setMybank] = useState({})
    const [views, setViews] = useState('')
    const [dets, setDets] = useState(false)
    const [viewwalls, setViewwalls] = useState(false)
    const [viewpass, setViewpass] = useState(false)
    const [viewemail, setViewemail] = useState(false)
    const closeview = () => {
        setViews('')
    }

    // get bank details
    const fetchBank = async () => {
        try {
            const res = await axios.get(`/withdraw/mybank/${user.id}`)
            setViews('bank')
            if (res.data.status === 200) {
                setDets(true)
                return setMybank(res.data.msg)
            }
            return setMybank({})
        } catch (error) {
            setDets(false)
            return console.log(error);
        }
    }
    return (
        <div>
            {dets && views === 'bank' && <Bankdetails closeview={closeview} mybank={mybank} />}
            {viewpass && <ChangePassword closeview={() => setViewpass(false)} />}
            {viewemail && <UpdateEmailSetting closeview={() => setViewemail(false)} />}
            {logout && <Logout />}

<div className="h-screen w-full flex">
    <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-700">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="bg-white button dark:bg-slate-700 rounded-xl shadow-xl flex p-3 gap-3 mb-2 cursor-pointer" onClick={() => setViewpass(true)}>
                                    <div className="">
                                        <div className=" bg-purple-400 text-purple-100 text-xl shadow-lg rounded-full py-1.5 px-2"> <i className="fas fa-shield-alt fa-fw"></i> </div>
                                    </div>
                                    <div className="w-full flex justify-between items-center">
                                        <div className="">
                                            <div className="font-semibold text-sm dark:text-slate-200 text-slate-700">Update Account Password</div>
                                            <div className="text-xs font-semibold dark:text-slate-200 text-slate-500">Set your local bank details to recieve funds when requesting for a withdrawal</div>
                                        </div>
                                        <div className="text-slate-600 dark:text-slate-200"> <i className="fas fa-ellipsis-v fa-fw"></i> </div>
                                    </div>
                                </div>
                                <div className="bg-white button dark:bg-slate-700 rounded-xl shadow-xl flex p-3 gap-3 mb-2 cursor-pointer" onClick={fetchBank}>
                                    <div className="">
                                        <div className="bg-indigo-400 text-indigo-100 text-xl shadow-lg rounded-full py-1.5 px-2"> <i className="fas fa-university fa-fw"></i> </div>
                                    </div>
                                    <div className="w-full flex justify-between items-center">
                                        <div className="">
                                            <div className="font-semibold text-sm dark:text-slate-200 text-slate-700">Withdrawal Bank Details</div>
                                            <div className="text-xs font-semibold dark:text-slate-200 text-slate-500">Set your local bank details to recieve funds when requesting for a withdrawal</div>
                                        </div>
                                        <div className="text-slate-600 dark:text-slate-200"> <i className="fas fa-ellipsis-v fa-fw"></i> </div>
                                    </div>
                                </div>
                                {viewwalls && <VisibleWallets closeview={() => setViewwalls(!viewwalls)} />}
                                <div className="bg-white button dark:bg-slate-700 rounded-xl shadow-xl flex p-3 gap-3 mb-2 cursor-pointer" onClick={() => setViewwalls(!viewwalls)}>
                                    <div className="">
                                        <div className="bg-green-400 text-white text-xl shadow-lg rounded-full py-1.5 px-2"> <i className="fas fa-wallet fa-fw"></i> </div>
                                    </div>
                                    <div className="w-full flex justify-between items-center">
                                        <div className="">
                                            <div className="font-semibold text-sm dark:text-slate-200 text-slate-700">Toggle wallets visibility</div>
                                            <div className="text-xs font-semibold dark:text-slate-200 text-slate-500">worried about someone spying on your wallet balance, turn off wallet visibility</div>
                                        </div>
                                        <div className="text-slate-600 dark:text-slate-200"> <i className="fas fa-ellipsis-v fa-fw"></i> </div>
                                    </div>
                                </div>
                                <div className="bg-white button dark:bg-slate-700 rounded-xl shadow-xl flex p-3 gap-3 mb-2 cursor-pointer" onClick={() => setViewemail(true)}>
                                    <div className="">
                                        <div className="bg-cyan-400 text-indigo-100 text-xl shadow-lg rounded-full py-1.5 px-2"> <i className="far fa-envelope fa-fw"></i> </div>
                                    </div>
                                    <div className="w-full flex justify-between items-center">
                                        <div className="">
                                            <div className="font-semibold text-sm dark:text-slate-200 text-slate-700">Update Email Address</div>
                                            <div className="text-xs font-semibold dark:text-slate-200 text-slate-500">No longer have access to your email address?, request for an update</div>
                                        </div>
                                        <div className="text-slate-600 dark:text-slate-200"> <i className="fas fa-ellipsis-v fa-fw"></i> </div>
                                    </div>
                                </div>
                                <div className="bg-white button dark:bg-slate-700 rounded-xl shadow-xl flex p-3 gap-3 mb-2 cursor-pointer" onClick={() => dispatch(logoutUser())}>
                                    <div className="">
                                        <div className="bg-red-400 text-red-100 text-xl shadow-lg rounded-full py-1.5 px-2"> <i className="fas fa-power-off fa-fw"></i> </div>
                                    </div>
                                    <div className="w-full flex justify-between items-center">
                                        <div className="">
                                            <div className="font-semibold text-sm dark:text-slate-200 text-slate-700">Logout</div>
                                        </div>
                                        <div className="text-slate-600 dark:text-slate-200"> <i className="fas fa-ellipsis-v fa-fw"></i> </div>
                                    </div>
                                </div>

                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Settings