import React, { useCallback, useEffect, useState } from 'react'
import Header from './../../components/user/Header';
import Footer from './../../components/user/Footer';
import ViewNotify from './../../components/admin/ViewNotify';
import { useDispatch } from 'react-redux';
import axios from 'axios';
import moment from 'moment';
import { useSelector } from 'react-redux';
import { getAllNotifies, getAllUserNotifies } from '../../store/reducers/dataReducer';
import Sidenav from './../../components/user/Sidenav';

const UserNotify = () => {
  const {user} = useSelector(state => state.data)
  const [closeview, setCloseview] = useState(false)
  const [notes, setNotes] = useState([])
  const [notify, setNotify] = useState({})
  const dispatch = useDispatch()

  const getAllNotes = useCallback(async() => {
    const res = await axios.get(`/news/get-usernotes/${user.id}`)
    if (res.data.status === 200) {
        dispatch(getAllNotifies(res.data.notify))
        dispatch(getAllUserNotifies(res.data.usernotify))
        setNotes(res.data.notify)
    }
  }, [dispatch, user])

  useEffect(() => {
      dispatch(() => getAllNotes())
  }, [dispatch, getAllNotes])

  const getSingleNotify = async (id) => {
      const res = await axios.get(`/news/single-notify/${id}`)
      if(res.data.status === 200) {
        const noteres = await axios.post(`/news/add-usernotes/${res.data.msg.id}/${user.id}`)
        if(noteres.data.status === 200) {
          setNotify(res.data.msg)
          dispatch(() => getAllNotes())
          setCloseview(!closeview)
        }
      }else {
          console.log(res.data)
      }
  }
  return (
    <div>
        {closeview && <ViewNotify notify={notify} closeform={() => setCloseview(!closeview)} /> }

<div className="h-screen w-full flex">
    <Sidenav />
            <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                <div className="h-[89vh] md:h-screen overflow-y-auto">
                    <div className="h-fit bg-white dark:bg-slate-800">
                        <Header />
                    </div>
                    <div className="h-[auto] pb-24 mt-8 w-[97%] mx-auto">
                        <div className="p-4">
                            <div className="mb-8">
                                <div className="text-xl drop-shadow-lg font-semibold">Latest Notifications</div>
                            </div>
                            {notes.length > 0 ? notes.map((item) => (
                                <div onClick={() => getSingleNotify(item.id)} className="flex items-center bg-white cursor-pointer hover:bg-slate-100 dark:bg-slate-700 dark:text-slate-200 rounded-xl py-2 mb-2 shadow-lg px-3 border dark:border-slate-500 w-full gap-3" key={item.id}>
                                    <div className="text-xl mx-1 bg-slate-300 dark:bg-indigo-400 rounded-full py-1.5 px-2 text-indigo-100"> <i className="far fa-bell fa-fw"></i> </div>
                                    <div className="w-full">
                                        <div className="grid grid-cols-2">
                                            <div className="font-semibold text-sm">{item.title.slice(0, 25)}...</div>
                                            <div className="text-xs text-right"> <i className="far fa-clock fa-fw"></i> {moment(item.createdAt).fromNow()}</div>
                                        </div>
                                        <div className="text-sm">{item.message.slice(0, 60)}...</div>
                                    </div>
                                </div>
                            )) : <div className="">No Notificatons found...</div>}
                        </div>
                    </div>
                </div>
                {/* footer */}
                <div>
                    <Footer />
                </div>
            </div>
        </div>
    </div>
  )
}

export default UserNotify
