import axios from 'axios'
import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { toast, ToastContainer } from 'react-toastify'

const Bankdetails = props => {
    const { user } = useSelector(state => state.data)
    const [loading, setLoading] = useState(false)
    const [forms, setForms] = useState({
        account_holder: !props.mybank?.aholder ? '' : props.mybank.aholder,
        account_number: !props.mybank?.aholder ? '' : props.mybank.anumber,
        password: '',
        bankname: !props.mybank?.aholder ? '' : props.mybank.bankname,
    })
    const handleForms = e => {
        setForms({ ...forms, [e.target.name]: e.target.value })
    }
    const saveDetails = async e => {
        e.preventDefault()
        setLoading(true)
        const data = {
            account_holder: forms.account_holder,
            account_number: forms.account_number,
            password: forms.password,
            bankname: forms.bankname
        }
        const res = await axios.post(`/withdraw/bank-details/${user.id}`, data)
        setLoading(false)
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white dark:bg-slate-700 dark:text-slate-200 p-4 extras rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                    <div className="flex justify-between items-center mb-4">
                        <div className="text-xl dark:text-slate-200 text-slate-600 drop-shadow-xl">Add Bank Details</div>
                        <div className=""> <i onClick={props.closeview} className="fas fa-times cursor-pointer text-2xl dark:text-slate-200 text-slate-600 fa-fw"></i> </div>
                    </div>
                    {<form onSubmit={saveDetails}>
                        <div className="grid gap-8 mb-4 grid-cols-1 md:grid-cols-2">
                            <div className="">
                                <input type="text" name="account_holder" value={forms.account_holder} onChange={handleForms} placeholder='Account Holder' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                            </div>
                            <div className="">
                                <input type="text" name="account_number" value={forms.account_number} onChange={handleForms} placeholder='Account Number' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                            </div>
                        </div>
                        <div className="mb-4">
                            <input type="text" name="bankname" value={forms.bankname} onChange={handleForms} placeholder='Bank Name' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                        </div>
                        <div className="my-4">
                            <input type="password" name="password" value={forms.password} onChange={handleForms} placeholder='Account Password' className="outline-none text-sm bg-transparent border-b-[1px] w-full p-2" />
                        </div>
                        <div className="w-fit ml-auto">
                            <button disabled={loading ? true : false} className={`py-3 px-4 ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} flex items-center gap-3 rounded-full button shadow-xl text-indigo-200 font-semibold capitalize text-sm`}> {loading && <div className="loading"></div> } save details</button>
                        </div>
                    </form>}
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Bankdetails