import axios from 'axios'
import moment from 'moment/moment'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import AFooter from '../../components/admin/AFooter'
import AHeader from '../../components/admin/AHeader'
import ConfirmTransaction from '../../components/admin/ConfirmTransaction'
import Paginator from './../../components/general/Paginator';

const Atransactions = () => {
    const [tabs, setTabs] = useState('deposit')
    const [opentab, setOpentab] = useState(false)
    const [transactions, setTransactions] = useState([])
    const [singletrans, setSingletrans] = useState({})
    const [page, setPage] = useState(1)
    const [limit] = useState(10)
    const [total, setTotal] = useState(0)
    const dispatch = useDispatch()

    const allTransactions = useCallback(async () => {
        const res = (tabs === 'deposit') ? await axios.get(`/deposit/all`) : await axios.get(`/withdraw/all`)
        if(res.data.status === 200) {
            setTransactions(res.data.msg)
            setTotal(res.data.count)
        }
    }, [tabs])

    const indexOfLastTrans = page * limit
    const indexOfFirstTrans = indexOfLastTrans - limit
    const currentTrans = transactions.slice(indexOfFirstTrans, indexOfLastTrans)

    useEffect(() => {
        dispatch(() => allTransactions())
    }, [allTransactions, dispatch])

    const handleopenTab = async (id) => {
        try {
            const res = tabs === 'deposit' ? await axios.get(`/deposit/single/${id}`) : await axios.get(`/withdraw/single/${id}`)
            setSingletrans(res.data.msg)
            setOpentab(true)
        } catch (error) {
            console.log(error)
        }
    }
    const handlePaginate = (number) => setPage(number)
    const prevPageBtn = () => setPage(prev => prev - 1)
    const nextPageBtn = () => setPage(prev => prev + 1)
    return (
        <div>
            {opentab && <ConfirmTransaction item={singletrans} tab={tabs} sendSignal={() => allTransactions()} closeOpentab={() => setOpentab(false)} />}
            <div className="h-screen w-full flex">
                <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-8 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="grid grid-cols-2">
                                    <button onClick={() => setTabs('deposit')} className={`text-center ${tabs === 'deposit' ? 'bg-indigo-700 text-indigo-100' : 'bg-slate-100 dark:bg-slate-600'} py-3 shadow-xl capitalize font-semibold rounded-tl-3xl rounded-bl-3xl`}>deposits</button>
                                    <button onClick={() => setTabs('withdrawal')} className={`text-center ${tabs === 'withdrawal' ? 'bg-indigo-700 text-indigo-100' : 'bg-slate-100 dark:bg-slate-600'} py-3 shadow-xl capitalize font-semibold rounded-tr-3xl rounded-br-3xl`}>withdrawals</button>
                                </div>
                                <div className="mt-6">
                                    {transactions && currentTrans.length > 0 ? currentTrans.map((item) => (
                                        <div className="flex items-center bg-white dark:bg-slate-700 dark:border-slate-500 rounded-xl py-2 mb-1 shadow-lg px-3 border w-full gap-3" key={item.id}>
                                            <div className="text-xl mx-1 bg-slate-300 dark:bg-slate-500 rounded-full py-1.5 px-2 text-indigo-100"> <i className="far fa-bell fa-fw"></i> </div>
                                            <div className="w-full">
                                                <div className="flex items-center justify-between w-full">
                                                    <div className="flex justify-between w-full">
                                                        <div className="flex gap-4 items-center">
                                                            <div className="font-semibold text-slate-600 dark:text-slate-200 capitalize">{item.title}</div>
                                                            {item.tag === 'pending' ? <div className="text-red-500 text-xs">( <i className="fas fa-minus fa-fw"></i> ${item.amount} USD )</div> : <div className={`text-xs ${tabs === 'deposit' ? 'text-green-700 dark:text-green-200' : 'text-red-500 dark:text-red-200'}`}>( <i className={`fas ${tabs === 'deposit' ? 'fa-plus' : 'fa-minus'} fa-fw`}></i> ${item.amount} USD )</div>}
                                                        </div>
                                                        <div className="" onClick={() => handleopenTab(item.id)}> <button className="py-1 px-2 text-xs bg-indigo-600 text-indigo-100 rounded-full font-semibold capitalize">check</button> </div>
                                                    </div>
                                                    <div className="">
                                                        <div className={`${item.tag === 'pending' ? 'text-red-700 dark:text-red-200' : 'text-green-600 dark:text-green-200'} text-xs font-semibold`}>{item.tag === 'pending' ? `${item.tag}...` : item.tag}</div>
                                                    </div>
                                                </div>
                                                <div className="text-xs py-1 text-slate-600 dark:text-slate-200 px-2 font-semibold">{item.message.slice(0, 50)}...</div>
                                                <div className="text-right text-xs text-indigo-500 dark:text-indigo-300"> <i className="fas fa-globe fa-fw"></i> {moment(item.createdAt).calendar()} <span className={`italic ${item.status === 'pending' || item.status === 'declined' ? 'text-red-600 dark:text-red-200' : 'text-green-600 dark:text-green-200'} ml-5`}>{item.status}</span> </div>
                                            </div>
                                        </div>
                                    )) : <div className="">No {tabs} record found...</div>}
                                    <Paginator perpage={limit} total={total} page={page} handlePaginate={handlePaginate} prevPageBtn={prevPageBtn} nextPageBtn={nextPageBtn} />
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Atransactions
