import React from 'react'
import logo from '../../images/jog.png'

const Notfound = () => {
  return (
    <div className="flex flex-col items-center justify-center h-screen w-full">
        <img src={logo} alt="" className="w-24" />
        <div className="font-bold text-orange-500 text-3xl flex mb-10">jogglecryp <span className="text-3xl"> &reg; </span> </div>
        <div className="text-9xl font-bold flex drop-shadow-xl">404 <div className="text-2xl text-slate-600 italic">page</div> </div>
        <div className="text-lg text-slate-700 mb-24">It seems the page you are looking for does not exists!..</div>
    </div>
  )
}

export default Notfound