import moment from 'moment'
import React from 'react'
import { useSelector } from 'react-redux'

const ViewNotify = (props) => {
    const {user} =  useSelector(state => state.data)
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white dark:bg-slate-700 dark:text-slate-200 extras p-4 rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                    <div className="flex items-center justify-between mb-1">
                        <div></div>
                        <div className="">  <i onClick={props.closeform} className="fas fa-times fa-fw text-2xl cursor-pointer text-slate-500"></i> </div>
                    </div>
                    <div className="font-semibold mb-3 pb-3 border-b">{props.notify.title}</div>
                    <div className="text-xs"> <i className="far fa-clock fa-fw"></i> {moment(props.notify.createdAt).calendar()}</div>
                    {user.role === 'admin' && <div className="text-xs">Duration: <i className="far fa-clock fa-fw"></i> {moment(props.notify.duration).calendar()}</div>}
                    <div className="mt-3 whitespace-pre-wrap text-sm">{props.notify.message}</div>
                </div>
            </div>
        </div>
    )
}

export default ViewNotify
