import axios from 'axios'
import moment from 'moment'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import img from '../../images/img.png'
import Sidenav from './../../components/user/Sidenav';

const VendorTransactions = () => {
    const { user, imgurl } = useSelector(state => state.data)
    const dispatch = useDispatch()
    const [trans, setTrans] = useState([])
    const [feeds, setFeeds] = useState([])
    const [zone, setZone] = useState('trans')
    const [cc] = useState('btn py-2.5 hover:bg-slate-200 font-semibold capitalize bg-white button dark:bg-slate-700 shadow-xl dark:hover:bg-slate-600 px-10 border dark:border-slate-500 rounded-full')


    const allHistory = useCallback(async () => {
        const res = await axios.get(`/vendor/vendorhistories/${user.id}`)
        if (res.data.status === 200) {
            setTrans(res.data.msg)
            setFeeds(res.data.feeds)
        }
    }, [user])

    useEffect(() => {
        dispatch(() => allHistory())
    }, [dispatch, allHistory])

    return (
        <div>

        <div className="h-screen w-full flex">
            <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[98%] mx-auto">
                                <div className="grid grid-cols-2 gap-5 mb-7">
                                    <button onClick={() => setZone('trans')} className={`${cc}`}>transactions</button>
                                    <button onClick={() => setZone('feeds')} className={`${cc}`}>feedbacks</button>
                                </div>
                                <div className={zone === 'trans' ? '' : 'hidden'}>
                                    {trans.length < 1 ? <div className="italic">No Transactions yet</div> : trans.map((item) => (
                                        <div className="flex gap-1 bg-white dark:bg-slate-700 rounded-lg mb-1 px-3 shadow-xl py-2" key={item.id}>
                                            <div className=""> <img src={item.user.image ? `${imgurl}/profiles/${item.user.image}` : img} alt="" className="w-8 h-8 rounded-full" /> </div>
                                            <div className="w-[97%] mx-auto">
                                                <div className="flex items-center justify-between w-full border-b py-1 dark:border-slate-500">
                                                    <div className="text-sm">{item.user.fname}</div>
                                                    <div className="text-xs"> {item.status} </div>
                                                </div>
                                                <div className="flex items-center justify-between w-full border-b py-1 dark:border-slate-500">
                                                    <div className="text-sm">Amount client paid</div>
                                                    <div className="text-xs"> ${parseInt(item.amount)} </div>
                                                </div>
                                                <div className="flex items-center justify-between w-full border-b py-1 dark:border-slate-500">
                                                    <div className="text-sm">Amount client recieved</div>
                                                    <div className="text-xs"> ${parseInt(item.amount - (item.amount * item.fee - item.amount))} </div>
                                                </div>
                                                <div className="flex items-center justify-between w-full border-b py-1 dark:border-slate-500">
                                                    <div className="text-sm">Date initiated</div>
                                                    <div className="text-xs"> {moment(item.createdAt).calendar()} </div>
                                                </div>
                                                {item.reason && <div className="text-xs p-2 bg-slate-100 dark:bg-slate-800 rounded-lg mt-2">{item.reason}</div>}
                                            </div>
                                        </div>
                                    ))}
                                </div>

                                <div className={zone === 'feeds' ? '' : 'hidden'}>
                                    {feeds.length < 1 ? <div className="italic">No Feedbacks yet</div> : feeds.map((item) => (
                                        <div className="flex gap-1 bg-white dark:bg-slate-700 rounded-lg mb-1 px-3 shadow-xl py-2" key={item.id}>
                                            <div className=""> <img src={item.user.image ? `${imgurl}/profiles/${item.user.image}` : img} alt="" className="w-8 h-8 rounded-full" /> </div>
                                            <div className="w-[97%] mx-auto">
                                                <div className="flex items-center justify-between w-full">
                                                    <div className="text-sm">{item.user.fname}</div>
                                                    <div className="flex gap-5 items-center my-2">
                                                        <div className="text-xs"> <button className={`text-xs ${item.status === 'good' ? 'text-green-500' : 'text-red-500'}`}> <i className={`far ${item.status === 'good' ? 'fa-thumbs-up' : 'fa-thumbs-down'} fa-fw`}></i> </button> </div>
                                                    </div>
                                                </div>
                                                <div className="text-xs text-slate-400 capitalize"> <i className="far fa-clock fa-fw"></i> {moment(item.createdAt).calendar()}</div>
                                                <div className="text-xs p-2 bg-slate-100 dark:bg-slate-800 rounded-lg mt-2">{item.text}</div>
                                            </div>
                                        </div>
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default VendorTransactions