import React, { useCallback, useEffect, useState } from 'react'
import { Link, useNavigate, useParams } from 'react-router-dom'
import jog from '../../images/jog.png'
import { toast, ToastContainer } from 'react-toastify';
import axios from 'axios';
import Cookies from 'js-cookie';
import { useDispatch } from 'react-redux';

const Register = () => {
    const { id } = useParams()
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [checks, setChecks] = useState(false)
    const [zone, setZone] = useState(1)
    const [code, setCode] = useState('')
    const [loading, setLoading] = useState(false)
    const [pass1, setPass1] = useState(false)
    const [pass2, setPass2] = useState(false)
    const [forms, setForms] = useState({
        firstname: '',
        lastname: '',
        email: '',
        phone: '',
        country: '',
        state: '',
        password: '',
        confirm_password: '',
        resetcode: ''
    })

    const getReferral = useCallback(() => {
        if (id !== undefined) {
            localStorage.setItem('refid', id)
        }
    }, [id])

    useEffect(() => {
        dispatch(() => getReferral())
    }, [dispatch, getReferral])
    const handleforms = (e) => {
        e.persist();
        setForms({ ...forms, [e.target.name]: e.target.value })
    }

    const nextRegister = async () => {
        setLoading(true)
        if(!forms.firstname || !forms.lastname || !forms.email || !forms.phone || !forms.country || !forms.state || !forms.password || !forms.confirm_password) {
            setLoading(false)
            return toast.error(`All form fields are required to be filled properly`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const res = await axios.get(`/user/send-verification-code/${forms.email}`)
        setLoading(false)
        if(res.data.status === 200) {
            console.log(res.data.msg);
            setCode(res.data.msg)
            setZone(2)
        }else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    const handlesaveform = async e => {
        e.preventDefault();
        if(forms.resetcode.length < 1) {
            return toast.error(`Enter a Valid verification code`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if(forms.resetcode !== code) {
            return toast.error(`Verification code not correct, check the values properly`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        
        const data = {
            firstname: forms.firstname,
            lastname: forms.lastname,
            email: forms.email,
            contact: forms.phone,
            country: forms.country,
            state: forms.state,
            password: forms.password,
            confirm_password: forms.confirm_password,
            upline: id ? id : ''
        }
        if (!checks) {
            return toast.error(`You have to agree to our privacy policy rules`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        setLoading(true)
        const res = await axios.post('/user/register', data)
        setLoading(false)
        if(localStorage.getItem('refid')) {
            localStorage.removeItem('refid')
        }
        if (res.data.status === 200) {
            setForms({
                firstname: '',
                lastname: '',
                email: '',
                phone: '',
                country: '',
                state: '',
                password: '',
                confirm_password: '',
                resetcode: ''
            })
            Cookies.set('token', res.data.token)
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            setTimeout(() => {
                navigate('/dashboard')
            }, 3000)
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

    }

    const resendCode = async () => {
        const res = await axios.get(`/user/send-verification-code/${forms.email}`)
        if(res.data.status === 200) {
            console.log(res.data.msg);
            setCode(res.data.msg)
            setZone(2)
        }else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="grid grid-col-1 md:grid-cols-2 h-screen w-full">
                <div className="bg-slate-800 hidden md:block relative">
                    <div className="loginbg absolute top-0 left-0 w-full h-screen mix-blend-overlay"></div>
                </div>
                <div className="md:bg-slate-50 bg-slate-800">
                    <div className="loginbg absolute top-0 left-0 w-full h-screen mix-blend-overlay block md:hidden"></div>
                    <div className="flex items-center justify-center w-full h-screen relative">
                        <div className="bg-white/70 backdrop-blur-sm w-11/12 md:w-3/4 shadow-xl rounded-xl px-3 py-5">
                            <div className="flex items-center justify-center border-b-[1px] pb-3 mb-2">
                                <img src={jog} alt="" className="w-14" />
                            </div>
                            <form onSubmit={handlesaveform}>
                                <div className={`my-3 ${zone === 2 ? '' : 'hidden'}`}> <div className="cursor-pointer text-indigo-500 text-sm capitalize font-semibold" onClick={() => setZone(1)}> <i className="fas fa-arrow-left fa-fw text-xs"></i>back </div> </div>
                                <div className={zone === 1 ? '' : 'hidden'}>
                                    <div className="grid grid-cols-2 gap-4 mb-4">
                                        <div className="flex items-center">
                                            <div className=""> <i className="far fa-user fa-fw"></i> </div>
                                            <input type="text" name="firstname" value={forms.firstname} onChange={handleforms} placeholder='Firstname' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                        </div>
                                        <div className="flex items-center">
                                            <div className=""> <i className="far fa-user fa-fw"></i> </div>
                                            <input type="text" name="lastname" value={forms.lastname} onChange={handleforms} placeholder='Lastname' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                        </div>
                                    </div>
                                    <div className="flex items-center mb-3">
                                        <div className=""> <i className="far fa-envelope fa-fw"></i> </div>
                                        <input type="email" name="email" value={forms.email} onChange={handleforms} placeholder='Email Address' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                    </div>
                                    <div className="flex items-center mb-3">
                                        <div className=""> <i className="fas fa-phone-alt fa-fw"></i> </div>
                                        <input type="text" name="phone" value={forms.phone} onChange={handleforms} placeholder='Contact' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                    </div>
                                    <div className="grid grid-cols-2 gap-4 mb-3">
                                        <div className="flex items-center">
                                            <div className=""> <i className="far fa-flag fa-fw"></i> </div>
                                            <input type="text" name="country" value={forms.country} onChange={handleforms} placeholder='Country' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                        </div>
                                        <div className="flex items-center">
                                            <div className=""> <i className="far fa-flag fa-fw"></i> </div>
                                            <input type="text" name="state" value={forms.state} onChange={handleforms} placeholder='State' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                        </div>
                                    </div>
                                    <div className="grid grid-cols-2 gap-4 mb-3">
                                        <div className="flex items-center relative">
                                            <div className="absolute top-2 text-sm text-slate-600 cursor-pointer right-0" onClick={() => setPass1(!pass1)}> <i className={`fas ${pass1 ? 'fa-eye' : 'fa-eye-slash'} fa-fw`}></i> </div>
                                            <div className=""> <i className="fas fa-shield-alt fa-fw"></i> </div>
                                            <input type={pass1 ? 'text' : 'password'} name="password" value={forms.password} onChange={handleforms} placeholder='Password' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                        </div>
                                        <div className="flex items-center relative">
                                            <div className="absolute top-2 text-sm text-slate-600 cursor-pointer right-0" onClick={() => setPass2(!pass2)}> <i className={`fas ${pass2 ? 'fa-eye' : 'fa-eye-slash'} fa-fw`}></i> </div>
                                            <div className=""> <i className="fas fa-shield-alt fa-fw"></i> </div>
                                            <input type={pass2 ? 'text' : 'password'} name="confirm_password" value={forms.confirm_password} onChange={handleforms} placeholder='Confirm Password' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                        </div>
                                    </div>
                                    <div className="flex items-center mb-4">
                                        <div className=""> <i className="fas fa-user-friends fa-fw"></i> </div>
                                        <input type="referrals" name="referrals" defaultValue={localStorage.refid ? localStorage.refid : ''} readOnly onChange={handleforms} placeholder='Referral ID' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                    </div>
                                </div>
                                <div className={zone === 2 ? '' : 'hidden'}>
                                    <div className="text-xs">A verification code has been sent to {forms.email.slice(0, 3)}**********{forms.email.slice(-10)}, confirm eith the verification code sent.</div>
                                    <div className="my-4 w-2/3 mx-auto">
                                        <input type="text" name="resetcode" value={forms.resetcode} onChange={handleforms} placeholder='*******' className="outline-none border rounded-lg p-2 bg-transparent w-full text-center" />
                                    </div>
                                    <div className="mb-4"> <div className="flex gap-2 items-center text-sm text-slate-700"> Didn't recieve code? <div className="text-indigo-600 cursor-pointer" onClick={resendCode}>resend code</div> </div> </div>
                                    <div className="mb-3">
                                        <label className='text-xs text-slate-500 flex gap-3'>
                                            <input type="checkbox" name="checks" value={checks} onChange={(e) => setChecks(e.target.checked)} /> <span className="">By clicking the checkbox, you agree to our privacy and policy, and actions to be taken when bridged. <Link to='/' className='text-indigo-500'>read more</Link></span>
                                        </label>
                                    </div>
                                </div>
                                <div className="flex justify-between items-center mb-4">
                                    <Link to='/login' className='capitalize font-semibold text-indigo-600 drop-shadow-xl'>login account</Link>
                                    {zone === 1 && <button type='button' disabled={loading ? true : false} className={`py-3 px-6 ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} text-white text-sm capitalize button rounded-full shadow-xl flex items-center gap-3`} onClick={nextRegister}> {loading && <div className="loading"></div> } continue</button>}
                                    {zone === 2 && <button className={`py-3 px-6 ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} text-white text-sm capitalize button rounded-full shadow-xl`} disabled={loading ? true : false}>{loading ? 'loading...' : 'create account'}</button>}
                                </div>
                                <hr />
                                <div className="text-center"> <Link to='/' className='text-sm text-indigo-600 font-semibold'>Go Home</Link> </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Register