import React, { useState } from 'react'
import AHeader from '../../components/admin/AHeader';
import AFooter from '../../components/admin/AFooter';
import ModifyPackage from '../../components/admin/ModifyPackage';
import axios from 'axios';
import { useQuery } from 'react-query';
import { getSinglePackage } from '../../store/reducers/dataReducer';
import { useDispatch } from 'react-redux';
import { ToastContainer } from 'react-toastify';
import moment from 'moment/moment';

const ManagePackages = () => {
    const dispatch = useDispatch()
    const [formopen, setFormopen] = useState(false)
    const getAllpackages = async () => {
        const res = await axios.get('/package')
        return res.data.msg;
    }

    const { data, isLoading, isError, refetch } = useQuery('wallets', getAllpackages);

    const fetchSinglepack = async (id) => {
        try {
            const res = await axios.get(`/package/${id}`)
            if (res.data.status === 200) {
                dispatch(getSinglePackage(res.data.msg))
                setFormopen(true)
            }
        } catch (error) {
            return console.log(error)
        }
    }
    
    return (
        <div>
            {formopen && <ModifyPackage closeform={() => setFormopen(false)} sendSignal={refetch} />}
            <div className="h-screen w-full flex">
                <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-24 mt-8 w-[97%] mx-auto">
                            <div className="text-right mb-5">
                                <button onClick={() => { setFormopen(!formopen); dispatch(getSinglePackage({})) }} className="py-3 px-5 capitalize bg-indigo-700 text-white text-xs font-semibold rounded-full shadow-xl"> <i className="fas fa-plus fa-fw"></i> new package </button>
                            </div>
                            <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-5">
                                {isLoading ? <div className="italic">Loading...</div> : isError ? <div className="">Error Fetching Packages</div> : data.length > 0 ? data.map((item) => (
                                    <div className="border shadow-xl rounded-tr-[2rem] rounded-bl-[2rem] bg-gradient-to-r from-white dark:from-slate-600 to-sky-100 dark:to-slate-700 dark:border-slate-500 pb-3" key={item.id}>
                                        <div className="bg-indigo-700 w-2/3 text-indigo-100 capitalize shadow-xl font-semibold p-3 rounded-tr-3xl rounded-br-3xl">{item.name}</div>
                                        <div className="bg-orange-500 w-2/3 ml-auto text-orange-100 capitalize shadow-xl font-semibold text-xl p-3 rounded-tl-3xl rounded-bl-3xl">${item.amount}</div>
                                        <div className="grid grid-cols-2 p-2 border-b-[1px] mt-3">
                                            <div className="text-xs font-semibold">Bonus to recieve</div>
                                            <div className="text-xs text-right font-semibold">${item.bonus}</div>
                                        </div>
                                        <div className="grid grid-cols-2 p-2 border-b-[1px]">
                                            <div className="text-xs font-semibold">Topup after</div>
                                            <div className="text-xs text-right font-semibold">{item.topup} hour{item.topup !== 1 ? 's' : ''} </div>
                                        </div>
                                        <div className="grid grid-cols-2 p-2 border-b-[1px]">
                                            <div className="text-xs font-semibold">Duration</div>
                                            <div className="text-xs text-right font-semibold">{item.duration} Day{item.duration !== 1 ? 's' : ''} </div>
                                        </div>
                                        <div className="grid grid-cols-2 p-2">
                                            <div className="text-xs font-semibold">Referral commission</div>
                                            <div className="text-xs text-right font-semibold">{(item.refcom * 100 - 100).toFixed(2)}%</div>
                                        </div>
                                        <div className="grid grid-cols-2 p-2">
                                            <div className="text-xs font-semibold">Created:</div>
                                            <div className="text-xs text-right font-semibold">{moment(item.createdAt).calendar()}</div>
                                        </div>
                                        <div className="text-right px-3 pt-3">
                                            <button onClick={() => fetchSinglepack(item.id)} className="py-2 px-5 text-xs bg-indigo-700 text-indigo-100 capitalize font-semibold rounded-full">modify</button>
                                        </div>
                                    </div>
                                )) : <div className="italic">No Record to fetch...</div>}
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default ManagePackages