import React from 'react'
import { useSelector } from 'react-redux'
import img from '../../images/img.png'
import moment from 'moment/moment'
import axios from 'axios'
import { toast, ToastContainer } from 'react-toastify'


const ConfirmTransaction = props => {
    const { imgurl } = useSelector(state => state.data)
    const handleDecline = async (e) => {
        e.preventDefault()
        const res = props.tab === 'deposit' ? await axios.post(`/deposit/decline-deposit/${props.item.id}`) : await axios.post(`/withdraw/decline-withdrawal/${props.item.id}`)
        console.log(res.data);
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            props.sendSignal()
        }
    }

    const handleConfirm = async (e) => {
        e.preventDefault()
        console.log(props.item.id)
        const res = props.tab === 'deposit' ? await axios.post(`/deposit/confirm-deposit/${props.item.id}`) : await axios.post(`/withdraw/confirm-withdrawal/${props.item.id}`)
        console.log(res.data);
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            props.sendSignal()
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex dark:text-slate-200 items-center justify-center fixed z-10">
                <div className="bg-white dark:bg-slate-700 p-4 extras rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                    <div className="flex justify-between items-center">
                        <div className="flex gap-3">
                            <img src={img} alt="" className="w-10 rounded-full shadow-xl" />
                            <div className="">
                                <div className="font-semibold text-sm"> {props.item.user.fname} </div>
                                <div className="text-xs text-sky-800 dark:text-sky-300">{props.tab} on: {moment(props.item.createdAt).calendar()} </div>
                            </div>
                        </div>
                        <div className=""> <i className="fas fa-times fa-fw text-2xl cursor-pointer text-slate-600" onClick={() => props.closeOpentab()}></i> </div>
                    </div>
                    <div className="text-lg font-semibold capitalize py-3 flex"> {props.item.title} <span className={`text-xs italic ${props.item.status === 'pending' || props.item.status === 'declined' ? 'text-red-600' : 'text-green-600'}`}>({props.item.status})</span> </div>
                    {props.tab === 'deposit' && props.item.status === 'pending' && <img src={`${imgurl}/deposits/${props.item.image}`} alt="" className="w-full h-52 object-cover" />}
                    <div className="text-sm text-slate-600 pt-3 dark:text-slate-200 break-words"> {props.item.message} </div>

                    {props.item.status === 'pending' && <div className="flex justify-between items-center mt-3 pt-3 border-t-[1px]">
                        <form onSubmit={handleDecline}>
                            <button className='py-2 px-4 bg-red-700 text-red-200 rounded-full text-sm capitalize font-semibold button'>decline</button>
                        </form>
                        <form onSubmit={handleConfirm}>
                            <button className="py-2 px-4 bg-sky-700 text-sky-200 rounded-full text-sm capitalize font-semibold button">confirm</button>
                        </form>
                    </div>}
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default ConfirmTransaction
