import axios from 'axios'
import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { toast } from 'react-toastify'

const DeclineTransfer = props => {
    const {singleproposal} = useSelector(state => state.data)
    const [text, setText] = useState('')
    const [textcount, setTextcount] = useState(0)
    const [maxlen] = useState(200)
    const handleText = e => {
        const val = e.target.value 
        if(val.length === maxlen) {
            return setTextcount('-')
        }
        setTextcount(val.length)
    }

    const alterTransfer = async (e) => {
        e.preventDefault()
        const data = {
            reason: text
        }
        if(!text) {
            return toast.error(`Enter a valid reason for terminating transfer`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const res = await axios.post(`/vendor/submitrejection/${singleproposal.id}`, data)
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            props.closeview()
            props.sendSignal()
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white p-4 rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto dark:bg-slate-700 dark:text-slate-200">
                    <div className="">
                        <div className="text-xl"> <i onClick={props.back} className="fas cursor-pointer fa-arrow-left fa-fw"></i> </div>
                    </div>
                    <div className="text-xs my-4">Kindly provide a reason for declining this transfer.</div>
                    <form onSubmit={alterTransfer}>
                        <div className="mb-4">
                            <textarea maxLength={maxlen} name="text" value={text} onChange={e => setText(e.target.value)} onKeyUp={handleText} placeholder='Give your reason!...' cols="30" rows="6" className="outline-none text-sm p-2 w-full bg-transparent dark:border-slate-500 border rounded-lg"></textarea>
                            <div className="text-xs text-right">{textcount}/{maxlen}</div>
                        </div>
                        <div className="text-right">
                            <button className="py-2.5 px-10 bg-orange-500 text-orange-100 rounded-full text-xs capitalize font-semibold">submit</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}

export default DeclineTransfer