import React, { useCallback, useEffect } from 'react'
import AFooter from './../../components/admin/AFooter';
import AHeader from './../../components/admin/AHeader';
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import img from '../../images/img.png'
import { useState } from 'react';
import Paginator from '../../components/general/Paginator';
import ModifyUser from '../../components/admin/ModifyUser';

const ManageUsers = () => {
    const { imgurl } = useSelector(state => state.data)
    const [page, setPage] = useState(1)
    const [limit] = useState(10)
    const [data, setData] = useState([])
    const [total, setCount] = useState(0)
    const [view, setView] = useState(false)
    const [user, setUser] = useState({})
    const [wallets, setWallets] = useState([])
    const dispatch = useDispatch()
    const getAllUsers = useCallback(async () => {
        const res = await axios.get(`/user/all/admin-users`)
        if (res.data.status === 200) {
            setData(res.data.msg)
            setCount(res.data.count)
        }
    }, [])

    const indexOfLastTrans = page * limit
    const indexOfFirstTrans = indexOfLastTrans - limit
    const currentuser = data.slice(indexOfFirstTrans, indexOfLastTrans)

    useEffect(() => {
        dispatch(() => getAllUsers())
    }, [dispatch, getAllUsers])

    const setSingleUser = async (id) => {
        const res = await axios.get(`/user/single-user/admin/${id}`)
        if (res.data.status === 200) {
            setUser(res.data.msg)
            setWallets(res.data.wallets)
            setView(true)
        }
    }
    const handlePaginate = (number) => setPage(number)
    const prevPageBtn = () => setPage(prev => prev - 1)
    const nextPageBtn = () => setPage(prev => prev + 1)
    return (
        <div>
            {view && <ModifyUser user={user} wallets={wallets} closeview={() => setView(false)} />}
            <div className="h-screen w-full flex">
                <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="">
                                <div className="pl-5 w-11/12 md:w-2/3 mx-auto pb-4">
                                    <div className="text-xl font-semibold drop-shadow-lg">Showing Latest ({currentuser.length} / {total}) Users</div>
                                </div>
                                <div className="w-11/12 md:w-2/3 mx-auto border rounded-xl">
                                    {data.length < 1 ? <div className="italic">No User at the moment</div> : currentuser.map((item) => (
                                        <div className="p-2 flex gap-3 items-center border-b" key={item.id}>
                                            <div className=""> <img src={!item.image ? img : `${imgurl}/profiles/${item.image}`} alt="" className="w-7 h-7 rounded-full bg-cover" /> </div>
                                            <div className="w-full">
                                                <div className="grid grid-cols-2">
                                                    <div className=""> <i className="far text-xs fa-user fa-fw"></i> {(`${item.fname} ${item.lname}`).slice(0, 15)}</div>
                                                    <div className="text-right"> <button onClick={() => setSingleUser(item.id)} className="text-xs bg-orange-600 button py-1 px-2 text-orange-100 font-semibold shadow-lg hover:bg-orange-700 rounded-full">more</button> </div>
                                                </div>
                                                <div className="text-xs"> <i className="far fa-envelope fa-fw text-xs"></i> {item.email}</div>
                                            </div>
                                        </div>
                                    ))}
                                    <div className="p-3">
                                        <Paginator perpage={limit} total={total} page={page} handlePaginate={handlePaginate} prevPageBtn={prevPageBtn} nextPageBtn={nextPageBtn} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ManageUsers
