import axios from "axios"
import { useState } from "react"
import { useSelector } from "react-redux"
import { toast, ToastContainer } from "react-toastify"

const ChangePassword = props => {
    const { user } = useSelector(state => state.data)
    const [password, setPassword] = useState('')
    const [pass, setPass] = useState(false)
    const [loading, setLoading] = useState(false)
    const [pass1, setPass1] = useState(false)
    const [pass2, setPass2] = useState(false)
    const [forms, setForms] = useState({
        password: '',
        confirm_password: ''
    })

    const handleForms = e => {
        setForms({ ...forms, [e.target.name]: e.target.value })
    }
    const confirmWithPassword = async e => {
        e.preventDefault()
        setLoading(true)
        if (!password) {
            setLoading(false)
            return toast.error(`Enter your current Account password`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        const data = { password: password }
        const res = await axios.put(`/user/confirm-password-settings/${user.id}`, data)
        setLoading(false)
        if (res.data.status === 200) {
            setPass(true)
        } else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    const handlePasswordUpdate = async e => {
        e.preventDefault()
        setLoading(true)
        if (!forms.password || !forms.confirm_password) {
            setLoading(false)
            return toast.error(`Password fields are required`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if (forms.confirm_password !== forms.password) {
            setLoading(false)
            return toast.error(`Passwords mismatched`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const data = {
            password: forms.password
        }
        const res = await axios.put(`/user/update-password/${user.id}`, data)
        setLoading(false)
        if (res.data.status === 200) {
            setPass(false)
            return toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div className="h-screen w-full bg-black/60 flex items-center top-0 left-0 justify-center fixed z-10">
            <div className="bg-white dark:bg-slate-700 dark:text-slate-200 extras p-4 rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                <div className="flex justify-between items-center mb-4">
                    <div className="text-xl dark:text-slate-200 text-slate-600 drop-shadow-xl">Change Account Password</div>
                    <div className=""> <i onClick={props.closeview} className="fas fa-times cursor-pointer text-2xl dark:text-slate-200 text-slate-600 fa-fw"></i> </div>
                </div>
                <div className="">
                    {!pass && <form onSubmit={confirmWithPassword}>
                        <div className="text-xs text-slate-600 dark:text-slate-200 mb-3">Enter your current Account password to confirm this process</div>
                        <input type="password" name="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="Enter Password" className="bg-transparent outline-none border dark:border-slate-500 w-full rounded p-2 text-sm" />
                        <div className="w-fit ml-auto mt-4"><button disabled={loading ? true : false} className={`py-2.5 px-6 ${loading ? 'bg-indigo-400' : 'bg-indigo-600'} flex items-center rounded-full text-xs shadow-xl font-semibold text-slate-200 capitalize`}> {loading && <div className="loading"></div>} confirm</button></div>
                    </form>}
                    {pass && <form onSubmit={handlePasswordUpdate}>
                        <div className="mb-4 relative">
                            <div className="text-xs mb-2">New Password</div>
                            <div className="absolute top-8 text-sm text-slate-600 cursor-pointer right-4" onClick={() => setPass1(!pass1)}> <i className={`fas ${pass1 ? 'fa-eye' : 'fa-eye-slash'} fa-fw`}></i> </div>
                            <input type={pass1 ? 'text' : 'password'} name="password" value={forms.password} onChange={handleForms} placeholder="Enter New Password" className="outline-none bg-transparent w-full border dark:border-slate-500 rounded-lg p-2 text-sm" />
                        </div>
                        <div className="mb-4 relative">
                            <div className="text-xs mb-2">Confirm New Password</div>
                            <div className="absolute top-8 text-sm text-slate-600 cursor-pointer right-4" onClick={() => setPass2(!pass2)}> <i className={`fas ${pass2 ? 'fa-eye' : 'fa-eye-slash'} fa-fw`}></i> </div>
                            <input type={pass2 ? 'text' : 'password'} name="confirm_password" value={forms.confirm_password} onChange={handleForms} placeholder="Confirm New Password" className="outline-none bg-transparent w-full border dark:border-slate-500 rounded-lg p-2 text-sm" />
                        </div>
                        <div className="w-fit ml-auto mt-4"><button disabled={loading ? true : false} className={`py-2.5 px-6 ${loading ? 'bg-indigo-400' : 'bg-indigo-600'} flex items-center gap-3 rounded-full text-xs shadow-xl font-semibold text-slate-200 capitalize`}> {loading && <div className="loading"></div>} update password</button></div>
                    </form>}
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default ChangePassword