import React, { useCallback, useEffect, useRef, useState } from 'react'
import Footer from '../../components/user/Footer';
import Header from '../../components/user/Header';
import { useNavigate, useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import axios from 'axios';
import { toast, ToastContainer } from 'react-toastify';
import Sidenav from './../../components/user/Sidenav';

const Deposit = () => {
    const { id } = useParams()
    const dispatch = useDispatch()
    const [wallet, setWallet] = useState({})
    const [admin, setAdmin] = useState({})
    const { imgurl } = useSelector(state => state.data)
    const navigate = useNavigate()
    const copyinput = useRef()
    const [photos, setPhotos] = useState({ image: null, img: null })
    const [amount, setAmount] = useState('')
    const { user } = useSelector(state => state.data)
    const [loading, setLoading] = useState(false)


    const getWallet = useCallback(async () => {
        const res = await axios.get(`/wallet/single/${id}`)
        if (res.data.status === 200) {
            setWallet(res.data.msg)
            setAdmin(res.data.msg.adminwallet)
        }
    }, [id])


    useEffect(() => {
        dispatch(() => getWallet())
    }, [dispatch, getWallet])

    const handleCopy = () => {
        copyinput.current.select();
        copyinput.current.setSelectionRange(0, 99999);
        navigator.clipboard.writeText(copyinput.current.value);
        toast.info('Copied', {
            position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
        });
    }

    const handleimageUpload = e => {
        let file = e.target.files[0]
        if(file.size > 3000000) {
            return toast.error('Screenshot must not exceed 3MB', {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if(!file.type.startsWith('image/')) {
            return toast.error('Screenshot must be a valid image format', {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        let reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = (e) => {
            setPhotos({
                image: file,
                img: e.target.result
            })
        }
    }

    const closeUpload = () => {
        setPhotos({
            image: null, img: null
        })
        document.getElementById('uploadimg').value = null
    }

    const uploadDeposit = async e => {
        e.preventDefault()
        const data = new FormData()
        data.append('image', photos.image)
        data.append('amount', amount)
        data.append('wallet', wallet.id)
        if (!photos.image) {
            return toast.error('a screenshot of your payment proof must be uploaded', {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if (!amount) {
            return toast.error('Specify the deposited amount', {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        setLoading(true)
        const res = await axios.post(`/deposit/save-deposit/${user.id}`, data)
        setLoading(false)
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            setTimeout(() => {
                navigate('/transactions')
            }, 3000);
        } else {
            console.log(res.data)
        }
    }
    return (
        <div>

        <div className="h-screen w-full flex">
            <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="grid grid-cols-1 md:grid-cols-2 gap-8">
                                    <div className="bg-white dark:bg-slate-700 rounded-xl shadow-xl p-6">
                                        <div className="mx-auto w-fit mb-4">{admin.qrcode && <img src={`${imgurl}/qrcodes/${admin.qrcode}`} alt="" className="w-56" />}</div>
                                        <div className="flex items-center">
                                            <input type="text" ref={copyinput} readOnly defaultValue={admin.ads} className="outline-none bg-transparent dark:border-slate-500 w-full border rounded-tl-xl rounded-bl-xl p-2 text-sm" />
                                            <button onClick={handleCopy} className="bg-indigo-700 text-indigo-200 text-sm font-semibold py-2 px-6 capitalize border-[1px] border-indigo-700 rounded-tr-xl rounded-br-xl">copy</button>
                                        </div>
                                    </div>
                                    <div className="bg-white dark:bg-slate-700 shadow-xl rounded-xl p-4">
                                        <div className="text-sm text-orange-700 my-2 p-2 rounded-lg bg-orange-100"> <i className="fas fa-exclamation-circle fa-fw"></i> Kindly proceed to this stage once you have made your deposit</div>
                                        <form onSubmit={uploadDeposit}>
                                            <div className="relative">
                                                <label>
                                                    <div className="text-sm dark:text-slate-200 text-slate-600">Upload payment screenshot</div>
                                                    <div className="mb-4">
                                                        {!photos.img ? <div className="w-full h-52 border-dashed border-2 flex dark:border-slate-500 items-center justify-center"> <i className="fas dark:text-slate-200 text-slate-500 fa-plus fa-fw"></i> </div> : <div className="relative"><img src={photos.img} alt="" className="w-full h-52 object-cover" /></div>}
                                                        <input type="file" id="uploadimg" hidden name="image" onChange={handleimageUpload} />
                                                    </div>
                                                </label>
                                                {photos.img && <div className="absolute top-4 right-0 text-2xl dark:text-slate-200 text-slate-600 cursor-pointer"> <i onClick={closeUpload} className="fas fa-times-circle fa-fw"></i> </div>}
                                            </div>
                                            <div className="my-4">
                                                <div className="text-sm dark:text-slate-200 text-slate-600">Deposited Amount</div>
                                                <input type="text" name="amount" value={amount} onChange={e => setAmount(e.target.value)} placeholder='0.00' className="outline-none dark:border-slate-500 border-b-[1px] bg-transparent w-full text-sm p-2" />
                                            </div>
                                        <div className="w-fit ml-auto">
                                            <button className={`py-3 px-4 ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} rounded-full shadow-xl text-indigo-200 font-semibold capitalize button text-sm flex items-center gap-3`}> {loading && <div className="loading"></div> }upload deposit</button>
                                            </div>
                                        </form>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Deposit
