import axios from 'axios'
import moment from 'moment'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import Sidenav from './../../components/user/Sidenav';

const MiningHistory = () => {
    const { user } = useSelector(state => state.data)
    const dispatch = useDispatch()
    const [mined, setMined] = useState(0)
    const [mines, setMines] = useState([])
    const allMined = useCallback(async () => {
        const res = await axios.get(`/package/my-mined/${user.id}`)
        if (res.data.status === 200) {
            setMined(res.data.count)
            setMines(res.data.msg)
        }
    }, [user])

    useEffect(() => {
        dispatch(() => allMined())
    }, [dispatch, allMined])

    return (
        <div>

        <div className="h-screen w-full flex">
            <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200 font-semibold">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[98%] mx-auto">
                                <div className="text-xl p-3 font-semibold drop-shadow-lg">My Mining histories ({mined}) </div>
                                {mines.length < 1 ? <div className="italic">You have not mined any package yet</div> : mines.map((item) => (
                                    <div className="flex items-center bg-white dark:bg-slate-700 dark:text-slate-200 rounded-xl py-2 mb-2 shadow-lg px-3 border dark:border-slate-500 w-full gap-3" key={item.id}>
                                        <div className=""> <div className="rounded-full bg-sky-400 dark:bg-sky-600 text-sky-100 py-2 px-1.5 text-sm border uppercase">{String(item.package.name).slice(0, 3)}</div> </div>
                                        <div className="w-full">
                                            <div className="grid grid-cols-2">
                                                <div className="text-sm">Package mined</div>
                                                <div className="text-right text-sm">{item.package.name}</div>
                                            </div>
                                            <div className="grid grid-cols-2">
                                                <div className="text-sm">Amount Paid</div>
                                                <div className="text-right text-sm">${item.package.amount}</div>
                                            </div>
                                            <div className="grid grid-cols-2">
                                                <div className="text-sm">Total Amount recieved</div>
                                                <div className="text-right text-sm">${item.balance}</div>
                                            </div>
                                            <div className="grid grid-cols-2">
                                                <div className="text-sm">completed mining</div>
                                                <div className="text-right text-sm">{moment(item.createdAt).fromNow()}</div>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default MiningHistory
