import axios from 'axios'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { toast, ToastContainer } from 'react-toastify'


const ModifyAirdrop = props => {
    const {singleairdrop} = useSelector(state => state.data)
    const dispatch = useDispatch()
    const [wallet, setWallet] = useState([])
    const [forms, setForms] = useState({
        start_date: '',
        stop_date: '',
        percent: singleairdrop.startd ? singleairdrop.percent : '',
        wallet: singleairdrop.startd ? singleairdrop.wallet : '',
        refcom: singleairdrop.startd ? singleairdrop.refcom : '',
    })

    const handleforms = e => {
        setForms({...forms, [e.target.name]: e.target.value})
    }

    const getWallets = useCallback(async () => {
        const res = await axios.get(`/adminwallet`)
        if(res.data.status === 200) {
            setWallet(res.data.msg)
        }
    }, [])

    useEffect(() => {
      dispatch(() => getWallets())
    }, [dispatch, getWallets])

    const handleformDetails = async e => {
        e.preventDefault()
        const data = {
            start_date: forms.start_date,
            stop_date: forms.stop_date,
            percent: forms.percent,
            wallet: forms.wallet,
            refcom: forms.refcom,
        }
        
        const res = singleairdrop.startd ? await axios.put(`/adminwallet/update-airdrop/${singleairdrop.id}`, data) : await axios.post('/adminwallet/add-airdrop', data)
        if(res.data.status === 200) {
            props.sendSignal()
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            setForms({
                start_date: '',
                stop_date: '',
                percent: '',
                wallet: '',
                refcom: ''
            })
        }else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            console.log(res.data)
        }
    }
    
    const handleDelete = async (e) => {
        e.preventDefault()
        const res = await axios.delete(`/adminwallet/delete-airdrop/${singleairdrop.id}`)
        if(res.data.status === 200) {
            props.sendSignal()
            props.closeform()
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center dark:text-slate-200 justify-center fixed z-10">
                <div className="bg-white dark:bg-slate-700 p-4 extras rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                    <div className="flex items-center justify-between mb-4">
                        {singleairdrop.startd ? <form onSubmit={handleDelete}> <button className="btn button py-2 px-8 bg-red-700 text-red-200 capitalize text-xs font-semibold rounded-full"> delete</button> </form> : <div></div>}
                        <div className="">  <i onClick={props.closeform} className="fas fa-times fa-fw text-2xl cursor-pointer text-slate-500"></i> </div>
                    </div>
                    <form onSubmit={handleformDetails}>
                        {!singleairdrop.startd && <div className="mb-4">
                            <div className="text-sm text-slate-600 dark:text-slate-200">Connect airdrop to wallet</div>
                            <select name="wallet" className="outline-none bg-transparent dark:bg-slate-700 border-b w-full text-sm p-2" value={forms.wallet} onChange={handleforms}>
                                <option value="">Select wallet</option>
                                {wallet.map((item) => (
                                    <option value={item.id} key={item.id}> {item.name} ({item.short}) </option>
                                ))}
                            </select>
                        </div>}
                        <div className="mb-4">
                            <div className="text-sm text-slate-600 dark:text-slate-200">Date to start Airdrop</div>
                            <input type="date" name="start_date" className="outline-none bg-transparent border-b w-full text-sm p-2" value={forms.start_date} onChange={handleforms} />
                        </div>
                        <div className="mb-4">
                            <div className="text-sm text-slate-600 dark:text-slate-200">Date to End Airdrop</div>
                            <input type="date" name="stop_date" className="outline-none bg-transparent border-b w-full text-sm p-2" value={forms.stop_date} onChange={handleforms} />
                        </div>
                        <div className="mb-4">
                            <div className="text-sm text-slate-600 dark:text-slate-200">Airdrop Percentage</div>
                            <input type="number" name="percent" placeholder='1.10 as 10%' className="outline-none bg-transparent border-b w-full text-sm p-2" value={forms.percent} onChange={handleforms} />
                        </div>
                        <div className="mb-4">
                            <div className="text-sm text-slate-600 dark:text-slate-200">Airdrop Referral Commission ($) </div>
                            <input type="number" name="refcom" placeholder='$15' className="outline-none bg-transparent border-b w-full text-sm p-2" value={forms.refcom} onChange={handleforms} />
                        </div>
                        <div className="text-right">
                            <button className="py-3 px-5 bg-indigo-700 text-indigo-100 rounded-full shadow-xl button text-xs font-semibold capitalize">save details</button>
                        </div>
                    </form>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default ModifyAirdrop