import axios from 'axios'
import Cookies from 'js-cookie'
import React, { useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { toast, ToastContainer } from 'react-toastify'
import jog from '../../images/jog.png'
import Swal from 'sweetalert2'

const Login = () => {
    const navigate = useNavigate()
    const [checks, setChecks] = useState(false)
    const [zone, setZone] = useState(1)
    const [code, setCode] = useState('')
    const [pass1, setPass1] = useState(false)
    const [forms, setForms] = useState({
        email: '',
        password: '',
        resetcode: ''
    })
    const [btn, setBtn] = useState(false)
    const handleforms = (e) => {
        e.persist();
        setForms({ ...forms, [e.target.name]: e.target.value })
    }

    const nextRegister = async () => {
        setBtn(true)
        if (!forms.email || !forms.password) {
            setBtn(false)
            return Swal.fire({
                title: 'Request terminated',
                text: `All form fields are required to be filled properly`,
                icon: 'error',
                showConfirmButton: false
            })
        }
        const res = await axios.put(`/user/send-verification-logincode/${forms.email}/${forms.password}`)
        setBtn(false)
        if (res.data.status === 200) {
            console.log(res.data.msg);
            setCode(res.data.msg)
            setZone(2)
        } else {
            Swal.fire({
                title: 'Request terminated',
                text: res.data.msg,
                icon: 'error',
                showConfirmButton: false
            })
        }
    }

    const handlesaveform = async e => {
        e.preventDefault();

        if (forms.resetcode.length < 1) {
            return Swal.fire({
                title: 'Request terminated',
                text: `Enter a Valid verification code`,
                icon: 'error',
                showConfirmButton: false
            })
        }
        if (forms.resetcode !== code) {
            return Swal.fire({
                title: 'Request terminated',
                text: `Verification code not correct, check the values properly`,
                icon: 'error',
                showConfirmButton: false
            })
        }

        const data = {
            email: forms.email,
            password: forms.password,
        }
        setBtn(true)
        const res = await axios.post('/user/login', data)
        setBtn(false)
        if (res.data.status === 200) {
            setForms({
                email: '',
                password: '',
                resetcode: '',
            })
            Cookies.set('token', res.data.token)
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            setTimeout(() => {
                navigate('/dashboard')
            }, 3000)
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const resendCode = async () => {
        const res = await axios.get(`/user/send-verification-code/${forms.email}`)
        if (res.data.status === 200) {
            console.log(res.data.msg);
            setCode(res.data.msg)
            setZone(2)
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="grid grid-col-1 md:grid-cols-2 h-screen w-full">
                <div className="bg-slate-800 hidden md:block relative">
                    <div className="loginbg absolute top-0 left-0 w-full h-screen mix-blend-overlay"></div>
                </div>
                <div className="md:bg-slate-50 bg-slate-800">
                    <div className="loginbg absolute top-0 left-0 w-full h-screen mix-blend-overlay block md:hidden"></div>
                    <div className="flex items-center justify-center w-full h-screen relative"> 
                        <div className="bg-white/70 backdrop-blur-sm w-11/12 md:w-3/4 shadow-xl rounded-xl px-3 py-5">
                            <div className="flex items-center justify-center border-b-[1px] pb-3 mb-8">
                                <img src={jog} alt="" className="w-14" />
                            </div>
                            <form onSubmit={handlesaveform}>
                                <div className={`my-3 ${zone === 2 ? '' : 'hidden'}`}> <div className="cursor-pointer text-indigo-500 text-sm capitalize font-semibold" onClick={() => setZone(1)}> <i className="fas fa-arrow-left fa-fw text-xs"></i>back </div> </div>
                                <div className={zone === 1 ? '' : 'hidden'}>
                                    <div className="flex items-center mb-3">
                                        <div className=""> <i className="far fa-envelope fa-fw"></i> </div>
                                        <input type="email" name="email" value={forms.email} onChange={handleforms} placeholder='Email Address' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                    </div>
                                    <div className="text-end my-3">
                                        <Link to='/forgot_password' className='text-indigo-600'>Forgot Password?</Link>
                                    </div>
                                        <div className="flex items-center relative">
                                            <div className="absolute top-2 text-sm text-slate-600 cursor-pointer right-0" onClick={() => setPass1(!pass1)}> <i className={`fas ${pass1 ? 'fa-eye' : 'fa-eye-slash'} fa-fw`}></i> </div>
                                            <div className=""> <i className="fas fa-shield-alt fa-fw"></i> </div>
                                            <input type={pass1 ? 'text' : 'password'} name="password" value={forms.password} onChange={handleforms} placeholder='Password' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                    </div>
                                    <div className="mb-3">
                                        <label className='text-xs text-slate-500 flex gap-3'>
                                            <input type="checkbox" name="checks" value={checks} onChange={(e) => setChecks(e.target.checked)} /> Remember Me
                                        </label>
                                    </div>
                                </div>
                                <div className={zone === 2 ? '' : 'hidden'}>
                                    <div className="">{code}</div>
                                    <div className="text-xs">A verification code has been sent to {forms.email.slice(0, 3)}**********{forms.email.slice(-10)}, confirm eith the verification code sent.</div>
                                    <div className="my-4 w-2/3 mx-auto">
                                        <input type="text" name="resetcode" value={forms.resetcode} onChange={handleforms} placeholder='*******' className="outline-none border rounded-lg p-2 bg-transparent w-full text-center" />
                                    </div>
                                    <div className="mb-4"> <div className="flex gap-2 items-center text-sm text-slate-700"> Didn't recieve code? <div className="text-indigo-600 cursor-pointer" onClick={resendCode}>resend code</div> </div> </div>
                                </div>
                                <div className="flex justify-between items-center mb-4">
                                    <Link to='/register' className='capitalize font-semibold text-indigo-600 drop-shadow-xl'>create account</Link>
                                    {zone === 1 && <button type='button' onClick={nextRegister} className={`py-3 cursor-pointer px-6 ${btn ? 'bg-indigo-500' : 'bg-indigo-700'} text-white text-sm button capitalize flex items-center gap-3 rounded-full shadow-xl`} disabled={btn ? true : false}> <div className={btn ? 'loading' : ''}></div> continue</button>}
                                    {zone === 2 && <button className={`py-3 px-6 ${btn ? 'bg-indigo-500' : 'bg-indigo-700'} text-white text-sm button capitalize flex items-center gap-3 rounded-full shadow-xl`} disabled={btn ? true : false}> <div className={btn ? 'loading' : ''}></div> login account</button>}
                                </div>
                                <hr />
                                <div className="text-center"> <Link to='/' className='text-sm text-indigo-600 font-semibold'>Go Home</Link> </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Login