import axios from 'axios'
import React, { useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { toast, ToastContainer } from 'react-toastify'
import { getSinglePackage } from '../../store/reducers/dataReducer'

const ModifyPackage = props => {
    const dispatch = useDispatch()
    const { singlepackage } = useSelector(state => state.data)
    const [forms, setForms] = useState({
        name: singlepackage?.name ? singlepackage?.name : '',
        amount: singlepackage?.name ? singlepackage.amount : '',
        bonus: singlepackage?.name ? singlepackage.bonus : '',
        duration: singlepackage?.name ? singlepackage.duration : '',
        topup: singlepackage?.name ? singlepackage.topup : '',
        commission: singlepackage?.name ? singlepackage.refcom : '',
    })
    const handleforms = e => {
        setForms({ ...forms, [e.target.name]: e.target.value })
    }

    const savepackage = async e => {
        e.preventDefault()
        const data = {
            name: forms.name,
            amount: forms.amount,
            bonus: forms.bonus,
            duration: forms.duration,
            commission: forms.commission,
            topup: forms.topup
        }
        const res = !singlepackage?.name ? await axios.post(`/package/add-package`, data) : await axios.post(`/package/update-package/${singlepackage.id}`, data)
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            props.sendSignal()
            if (!singlepackage.name) {
                setForms({
                    name: '',
                    amount: '',
                    bonus: '',
                    duration: '',
                    topup: '',
                    commission: '',
                })
            }
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const handleDelete = async (e) => {
        e.preventDefault()
        const res = await axios.delete(`/package/delete-package/${singlepackage.id}`)
        if (res.data.status === 200) {
            dispatch(getSinglePackage({}))
            props.closeform()
            props.sendSignal()
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white dark:bg-slate-700 dark:text-slate-200 extras p-4 rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                    <div className="flex items-center justify-between mb-4">
                        {singlepackage.name ? <form onSubmit={handleDelete}> <button className="btn py-2 px-8 bg-red-700 text-red-200 capitalize text-xs font-semibold rounded-full"> delete</button> </form> : <div></div>}
                        <div className="">  <i onClick={props.closeform} className="fas fa-times fa-fw text-2xl cursor-pointer text-slate-500"></i> </div>
                    </div>
                    <form onSubmit={savepackage}>
                        <div className="mb-4">
                            <div className="text-xs text-slate-600 dark:text-slate-200">Package Name</div>
                            <input type="text" name="name" value={forms.name} onChange={handleforms} placeholder="Package Name" className="p-2 outline-none text-xs bg-transparent border-b-[1px] w-full" />
                        </div>
                        <div className="mb-4">
                            <div className="text-xs text-slate-600 dark:text-slate-200">Package Amount</div>
                            <input type="text" name="amount" value={forms.amount} onChange={handleforms} placeholder="Amount" className="outline-none p-2 text-xs bg-transparent border-b-[1px] w-full" />
                        </div>
                        <div className="grid grid-cols-2 gap-2">
                            <div className="mb-4">
                                <div className="text-xs text-slate-600">Bonus to recieve</div>
                                <input type="text" name="bonus" value={forms.bonus} onChange={handleforms} placeholder="Bonus" className="p-2 outline-none text-xs bg-transparent border-b-[1px] w-full" />
                            </div>
                            <div className="">
                                <div className="text-xs text-slate-600">Topup After</div>
                                <div className="mb-4 flex-1">
                                    <input type="text" name="topup" value={forms.topup} onChange={handleforms} placeholder="48 Hours" className="outline-none p-2 text-xs bg-transparent border-b-[1px] w-full" />
                                </div>
                            </div>
                        </div>
                        <div className="mb-4">
                            <div className="text-xs text-slate-600 dark:text-slate-200">Duration</div>
                            <input type="text" name="duration" value={forms.duration} onChange={handleforms} placeholder="Duration" className="outline-none p-2 text-xs bg-transparent border-b-[1px] w-full" />
                        </div>
                        <div className="mb-4">
                            <div className="text-xs text-slate-600 dark:text-slate-200">Referral Commission</div>
                            <input type="text" name="commission" value={forms.commission} onChange={handleforms} placeholder="Referral Commission" className="outline-none p-2 text-xs bg-transparent border-b-[1px] w-full" />
                        </div>
                        <div className="text-right">
                            <button className="py-3 px-5 text-white bg-indigo-600 button rounded-full shadow-xl text-xs font-semibold capitalize">save package</button>
                        </div>
                    </form>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default ModifyPackage
