import axios from 'axios'
import moment from 'moment/moment'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import { toast, ToastContainer } from 'react-toastify'
import Header from '../../components/user/Header'
import Footer from '../../components/user/Footer'
import UploadProof from '../../components/user/UploadProof'
import LeaveFeedback from '../../components/user/LeaveFeedback'
import AllFeedBacks from '../../components/user/AllFeedBacks'
import ReportDelay from '../../components/user/ReportDelay'
import Sidenav from './../../components/user/Sidenav';

const OpenPurchase = () => {
    const dispatch = useDispatch()
    const { imgurl, user } = useSelector(state => state.data)
    const [vendor, setVendor] = useState({})
    const [wallets, setWallets] = useState([])
    const [wallet, setWallet] = useState({})
    const [goodfeeds, setGoodfeeds] = useState([])
    const [badfeeds, setBadfeeds] = useState([])
    const [showdelay, setShowdelay] = useState(false)
    const [loading, setLoading] = useState(false)
    const [forms, setForms] = useState({
        topay: 0,
        toget: 0
    })
    const [topaycrypto, setTopaycrypto] = useState(0)
    const [getcrypto, setGetcrypto] = useState()
    const [propose, setPropose] = useState('')
    const [trans, setTrans] = useState({})
    const { id } = useParams()

    const handleForms = e => {
        setForms({ ...forms, [e.target.name]: e.target.value })
    }
    const getVendor = useCallback(async () => {
        const res = await axios.get(`/vendor/single/${id}`)
        if (res.data.status === 200) {
            setVendor(res.data.msg)
            setBadfeeds(res.data.badfeeds)
            setGoodfeeds(res.data.goodfeeds)
        }
    }, [id])
    const getallmywallets = useCallback(async () => {
        const res = await axios.get(`/wallet/all/${user.id}`)
        if (res.data.status === 200) {
            setWallets(res.data.msg)
        } else {
            setWallets([])
        }
    }, [user])
    const checkProposal = useCallback(async () => {
        const res = await axios.get(`/vendor/find/${user.id}/${id}`)
        if (res.data.status === 200) {
            if (res.data.msg === 'true') {
                setPropose(res.data.msg)
                setTrans(res.data.transfer)
                getVendor()
            } else {
                setPropose(res.data.msg)
            }
        }
    }, [user, id, getVendor])


    useEffect(() => {
        dispatch(() => checkProposal())
        dispatch(() => getVendor())
        dispatch(() => getallmywallets())
    }, [dispatch, getVendor, getallmywallets, checkProposal])

    const selectWallet = async e => {
        const wid = e.target.value
        if (wid !== '') {
            const res = await axios.get(`/wallet/single/${wid}`)
            if (res.data.status === 200) {
                setWallet(res.data.msg)
                setTopaycrypto(0)
                setGetcrypto(0)
                setForms({ topay: '', toget: '' })
            }
        }
    }
    const proceedToPurchase = async () => {
        const data = {
            user_wallet: wallet.id,
            amount: forms.topay,
            vendor: vendor.id,
            user_id: user.id
        }
        if (!forms.topay || !wallet.id) {
            return toast.error('Complete your form before submitting the proposal', {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        setLoading(true)
        const res = await axios.post('/vendor/add-vendortransfer', data)
        setLoading(false)
        if (res.data.status === 200) {
            dispatch(() => checkProposal())
            return toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }


    const calcToPay = e => {
        const val = e.target.value
        if (val !== '') {
            // to pay
            const sum = val * wallet.adminwallet.current_price
            setTopaycrypto(sum.toFixed(9))
            // to get
            const getscryp = parseInt(val - (val * vendor.fee - val)) * wallet.adminwallet.current_price
            const getsval = parseInt(val - (val * vendor.fee - val))
            setGetcrypto(getscryp.toFixed(9))
            setForms({ ...forms, toget: getsval })
        } else {
            setTopaycrypto(0)
            setGetcrypto(0)
            setForms({ ...forms, toget: 0 })
        }
    }
    return (
        <div>
            {showdelay && <ReportDelay sendSignal={() => dispatch(() => checkProposal())} transid={trans.id} closeview={() => setShowdelay(false)} />}

<div className="h-screen w-full flex">
    <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 font-semibold dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="my-6">
                                    <div className="text-xl text-font-semibold drop-shadow-lg font-semibold"> {propose !== 'false' ? `Transaction in progress!...` : 'How much do you want to buy?'} </div>
                                </div>
                                <div className="">
                                    <div className="py-2 px-3 mb-2 justify-between shadow-lg bg-white dark:bg-slate-700 rounded-lg">
                                        {/* now user has opened a proposal lets verify if its pending, accepted, payed, approved, confirmed */}
                                        {/* verified */}
                                        {propose === 'true' && trans.status === 'approved' && <div className="">
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Transfered</div>
                                                <div className="text-xs text-green-400">{trans.status}...</div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Paid</div>
                                                <div className="text-xs">  ${parseInt(trans.amount)} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">In local currency</div>
                                                <div className="text-xs">  {parseInt(trans.amount * vendor.rate)} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Recieved</div>
                                                <div className="text-xs">  ${parseInt(trans.amount - (trans.amount * trans.fee - trans.amount))} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Initiated</div>
                                                <div className="text-xs"> <i className="far fa-clock fa-fw"></i> {moment(trans.createdAt).calendar()}...</div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Summary</div>
                                                <div className="text-xs text-right"> <i className="far fa-user fa-fw"></i> Approved by vendor</div>
                                            </div>
                                        </div>}
                                        {/* paid */}
                                        {propose === 'true' && (trans.status === 'paid' || trans.status === 'under review') && <div className="">
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Transfer</div>
                                                <div className="text-xs text-green-400">{trans.status}...</div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Paid</div>
                                                <div className="text-xs">  ${parseInt(trans.amount)} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">In local currency</div>
                                                <div className="text-xs">  {parseInt(trans.amount * vendor.rate)} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">To Recieve</div>
                                                <div className="text-xs">  ${parseInt(trans.amount - (trans.amount * trans.fee - trans.amount))} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Initiated</div>
                                                <div className="text-xs"> <i className="far fa-clock fa-fw"></i> {moment(trans.createdAt).calendar()}...</div>
                                            </div>
                                            {trans.status === 'paid' && <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Time Paid</div>
                                                <div className="text-xs"> <i className="far fa-clock fa-fw"></i> {moment(trans.updatedAt).calendar()}...</div>
                                            </div>}
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Summary</div>
                                                <div className="text-xs text-right"> <i className="far fa-user fa-fw"></i> Awaiting vendor's approval</div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Vendor to respond within</div>
                                                <div className="text-xs text-right"> <i className="far fa-clock fa-fw"></i>{moment(trans.timelimit).fromNow()}</div>
                                            </div>
                                            {moment().isAfter(moment(trans.timelimit)) && trans.status !== 'under review' && <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs"></div>
                                                <div className="text-xs"> <button onClick={() => setShowdelay(true)} className="bg-red-600 rounded-full text-xs font-semibold shadow-xl py-2 px-5 capitalize text-red-100">report delay</button> </div>
                                            </div>}
                                        </div>}
                                        {/* accepted */}
                                        {propose === 'true' && trans.status === 'accepted' && <div className="">
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Transfer</div>
                                                <div className="text-xs text-green-400">{trans.status}...</div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">To Pay</div>
                                                <div className="text-xs">  ${parseInt(trans.amount)} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">To Recieve</div>
                                                <div className="text-xs">  ${parseInt(trans.amount - (trans.amount * trans.fee - trans.amount))} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Initiated</div>
                                                <div className="text-xs"> <i className="far fa-clock fa-fw"></i> {moment(trans.createdAt).calendar()}...</div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Summary</div>
                                                <div className="text-xs text-right"> <i className="far fa-user fa-fw"></i> Awaiting user's response</div>
                                            </div>
                                            <div className="flex items-center justify-between py-2">
                                                <div className="text-xs">Vendor's Account</div>
                                                <div className="text-xs text-right whitespace-pre-wrap"> {vendor.bank} </div>
                                            </div>

                                        </div>}
                                        {/* pending */}
                                        {propose && trans.status === 'pending' && <div className="">
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Transfer</div>
                                                <div className="text-xs text-red-400">{trans.status}...</div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">To Pay</div>
                                                <div className="text-xs">  ${parseInt(trans.amount)} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">To Recieve</div>
                                                <div className="text-xs">  ${parseInt(trans.amount - (trans.amount * trans.fee - trans.amount))} </div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Initiated</div>
                                                <div className="text-xs"> <i className="far fa-clock fa-fw"></i> {moment(trans.createdAt).calendar()}...</div>
                                            </div>
                                            <div className="flex items-center justify-between py-2 border-b-[1px] dark:border-slate-500">
                                                <div className="text-xs">Summary</div>
                                                <div className="text-xs"> <i className="far fa-user fa-fw"></i> Awaiting vendor's response</div>
                                            </div>
                                        </div>}
                                        {/* if the user has not opened any proposal with the vendor */}
                                        {propose === 'false' && <div className="">
                                            <div className="text-xs p-2">Select Crypto to pay in</div>
                                            <div className="mb-2">
                                                <select name="wallet" onChange={selectWallet} className="outline-none dark:bg-slate-700 bg-transparent text-sm w-full p-2 border-slate-500 rounded-md border text-slate-400">
                                                    <option value="">Select Crypto</option>
                                                    {wallets.map((item) => (
                                                        <option value={item.id} key={item.id}> {item.adminwallet.name} ({item.adminwallet.short}) </option>
                                                    ))}
                                                </select>
                                            </div>
                                        </div>}
                                        {propose === 'false' && wallet.id && <div className="">
                                            <div className="text-xs p-2">I will pay</div>
                                            <div className="flex items-center border-slate-500 border rounded-md">
                                                <input type="number" name="topay" value={forms.topay} onChange={handleForms} onKeyUp={calcToPay} placeholder='0.00' className="outline-none w-full bg-transparent text-sm p-2" />
                                                <button className="text-sm uppercase p-2 border-slate-500 border-l">usd</button>
                                            </div>
                                            <div className="text-right text-sm mt-1 uppercase"> <i className="fas fa-exchange-alt mr-4 fa-fw"></i> {topaycrypto} {wallet.adminwallet.short}</div>

                                            <div className="text-xs p-2">To recieve</div>
                                            <div className="flex items-center border-slate-500 border rounded-md">
                                                <input type="number" name="toget" readOnly value={forms.toget} onChange={handleForms} placeholder='0.00' className="outline-none w-full bg-transparent text-sm p-2" />
                                                <button className="text-sm uppercase p-2 border-slate-500 border-l">usd</button>
                                            </div>
                                            <div className="text-right text-sm mt-1 uppercase"> <i className="fas fa-exchange-alt mr-4 fa-fw"></i>{getcrypto} {wallet.adminwallet.short}</div>
                                        </div>}
                                    </div>
                                    {propose === 'true' && trans.status === 'approved' && <div className="py-2 px-3 mb-2 justify-between shadow-lg bg-white dark:bg-slate-700 rounded-lg">
                                        <LeaveFeedback trans={trans} sendSignal={() => dispatch(() => checkProposal())} />
                                    </div>}
                                    {propose === 'true' && trans.status === 'accepted' && <div className="py-2 px-3 mb-2 justify-between shadow-lg bg-white dark:bg-slate-700 rounded-lg">
                                        <div className="">
                                            <UploadProof trans={trans.id} sendSignal={() => dispatch(() => checkProposal())} />
                                        </div>
                                    </div>}
                                    {vendor.id && <div className="py-2 px-3 mb-2 justify-between shadow-lg bg-white dark:bg-slate-700 rounded-lg">
                                        <div className="text-sm my-3 uppercase">About this seller</div>
                                        <div className="flex gap-1">
                                            <div className=""> <img src={`${imgurl}/profiles/${vendor.user.image}`} alt="" className="w-8 h-8 rounded-full" /> </div>
                                            <div className="w-[97%] mx-auto">
                                                <div className="grid grid-cols-2">
                                                    <div className="text-sm">{vendor.user.lname} ({vendor.user.status})</div>
                                                    <div className="flex gap-5 items-center my-2 ml-auto">
                                                        <div className="text-xs"> <button className="text-xs text-green-500"> <i className="far fa-thumbs-up fa-fw"></i> </button>:{goodfeeds.length} </div>
                                                        <div className="text-xs"> <button className="text-xs text-red-500"> <i className="far fa-thumbs-down fa-fw"></i> </button>:{badfeeds.length} </div>
                                                    </div>
                                                </div>
                                                <div className="grid grid-cols-2 mb-2">
                                                    <div className="text-sm">Country</div>
                                                    <div className="text-sm text-right">{vendor.user.country}</div>
                                                </div>
                                                <div className="grid grid-cols-2 mb-2">
                                                    <div className="text-sm">Rate per dollar</div>
                                                    <div className="text-sm text-right">{vendor.rate}</div>
                                                </div>
                                                <div className="grid grid-cols-2 mb-2">
                                                    <div className="text-sm">Amount {propose === 'true' && trans.status === 'approved' ? 'recieved' : 'to recieve'} in local currency</div>
                                                    <div className="text-sm text-right">{propose === 'true' ? (vendor.rate * trans.amount).toFixed(2) : !forms.topay ? '-' : vendor.rate * parseFloat(forms.topay)}</div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="flex gap-3 mt-2">
                                            <div className="bg-slate-100 dark:bg-slate-800 text-xs rounded-lg px-2 py-1 text-green-600 dark:text-green-300"> <i className="fas text-[0.5rem] fa-check fa-fw"></i> Email </div>
                                            <div className="bg-slate-100 dark:bg-slate-800 text-xs rounded-lg px-2 py-1 text-green-600 dark:text-green-300"> <i className="fas text-[0.5rem] fa-check fa-fw"></i> Proof of Address </div>
                                            <div className="bg-slate-100 dark:bg-slate-800 text-xs rounded-lg px-2 py-1 text-green-600 dark:text-green-300"> <i className="fas text-[0.5rem] fa-check fa-fw"></i> ID Card </div>
                                        </div>
                                    </div>}
                                    <div className="py-2 px-3 mb-2 justify-between shadow-lg bg-white dark:bg-slate-700 rounded-lg">
                                        <div className="text-sm my-3">Disclaimer</div>
                                        <div className="text-xs mb-2 indent-2 font-semibold"> <i className="fas fa-exclamation-circle fa-fw mr-2 text-orange-600 dark:text-orange-300"></i> Once you select the wallet you wish to be funded in, use the calculator to determined what to pay and what you will recieve</div>
                                        <div className="text-xs mb-2 indent-2 font-semibold"> <i className="fas fa-exclamation-circle fa-fw mr-2 text-orange-600 dark:text-orange-300"></i> once clarified, click the buy to open a purchase with the vendor and await for the vendor to accept the proposal</div>
                                        <div className="text-xs mb-2 indent-2 font-semibold"> <i className="fas fa-exclamation-circle fa-fw mr-2 text-orange-600 dark:text-orange-300"></i> Once the vendor accepts the proposal, vendor's account payment details we be visible for you to pay in</div>
                                        <div className="text-xs mb-2 indent-2 font-semibold"> <i className="fas fa-exclamation-circle fa-fw mr-2 text-orange-600 dark:text-orange-300"></i> Once you pay in, upload the screenshot of payment proof and the method you paid with. then click on submit</div>
                                        <div className="text-xs mb-2 indent-2 font-semibold"> <i className="fas fa-exclamation-circle fa-fw mr-2 text-orange-600 dark:text-orange-300"></i> Once the vendor sees and confirm the transfer, your wallet will be credited automatically!..</div>
                                    </div>
                                    <div className="py-2 px-3 mb-2 justify-between shadow-lg bg-white dark:bg-slate-700 rounded-lg">
                                        <div className="text-sm my-3">FeedBacks from previous transactions</div>
                                        <AllFeedBacks trans={id} />
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        {propose === 'false' ? <div className='h-[11vh] border-t md:hidden overflow-hidden dark:bg-slate-700 dark:border-t-slate-400 flex items-center justify-center'>
                            <button onClick={proceedToPurchase} disabled={loading ? true : false} className={`py-2.5 px-10 rounded-full ${loading ? 'bg-indigo-400' : 'bg-indigo-600'} dark:bg-indigo-500 font-semibold text-indigo-100 uppercase flex items-center gap-3`}> {loading && <div className="loading"></div> } buy</button>
                        </div> : <Footer />}
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default OpenPurchase