import React, { useCallback, useEffect, useState } from 'react'
import Header from './../../components/user/Header';
import Footer from './../../components/user/Footer';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import { getSingleNews } from '../../store/reducers/dataReducer';
import ViewNewsfeeds from './../../components/admin/ViewNewsfeeds';
import moment from 'moment';
import Sidenav from './../../components/user/Sidenav';

const NewsFeeds = () => {
    const [news, setNews] = useState([])
    const dispatch = useDispatch()
    const [closeview, setCloseview] = useState(false)

    const allNews = useCallback(async () => {
        const res = await axios.get(`/news/all-news`)
        if (res.data.status === 200) {
            return setNews(res.data.msg)
        } else {
            console.log(res.data.msg);
        }
    }, [])

    useEffect(() => {
        dispatch(() => allNews())
    }, [dispatch, allNews])

    const openSingleNews = async (id) => {
        const res = await axios.get(`/news/single/${id}`)
        if(res.data.status === 200) {
            dispatch(getSingleNews(res.data.msg))
            setCloseview(true)
        }
    }

    return (
        <div>
        {closeview && <ViewNewsfeeds closeform={() => {setCloseview(!closeview)}} />} 

<div className="h-screen w-full flex">
    <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="">
                                <div className="text-2xl font-semibold capitalize drop-shadow-lg my-6 px-4">Latest NewsFeeds</div>
                                <div className="w-[95%] mx-auto">
                                    <div className="grid grid-cols-2 gap-3 mb-3">
                                        {news.length < 1 ? <div className="italic">You have not published any newsfeeds yet</div> : news.map((item) => (
                                            <div className="bg-white p-3 rounded-lg shadow-lg dark:bg-slate-700" key={item.id}>
                                                <div className="text-right text-xs"> <i className="far fa-clock fa-fw"></i> {moment(item.createdAt).fromNow()}</div>
                                                <div className="font-semibold">{item.title.slice(0, 20)}</div>
                                                {item.media ? <div className="text-xs mt-3"> <i className="fas fa-camera fa-fw"></i> photo included</div> : <div className="text-xs mt-3">no photo included</div>}
                                                <div className="py-3 text-xs">{item.message.slice(0, 70)}...</div>
                                                <div className="text-center">
                                                    <button onClick={() => openSingleNews(item.id)} className="rounded-full text-xs bg-indigo-600 p-2 animate-pulse text-indigo-100 font-bold shadow-xl"> <i className="fas fa-chevron-right fa-fw"></i> </button>
                                                </div>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default NewsFeeds
