import axios from 'axios'
import Cookies from 'js-cookie'
import React, { useEffect, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import { toast, ToastContainer } from 'react-toastify'
import jog from '../../images/jog.png'

const NewPassword = () => {
    const navigate = useNavigate()
    const [loading, setLoading] = useState(false)
    const [pass1, setPass1] = useState(false)
    const [pass2, setPass2] = useState(false)
    const [forms, setForms] = useState({
        password: '',
        cpassword: ''
    })
    const handleforms = (e) => {
        e.persist();
        setForms({ ...forms, [e.target.name]: e.target.value })
    }

    const handlesaveform = async e => {
        e.preventDefault();
        setLoading(true)

        if(forms.password.length < 1) {
            setLoading(false)
            return toast.error(`Complete all form fields`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if(forms.cpassword !== forms.password) {
            setLoading(false)
            return toast.error(`Password(s) mismatched`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        const data = {
            password: forms.password,
            cpassword: forms.cpassword,
            email: Cookies.get('loginemail')
        }

        const res = await axios.put(`/user/update-account-password`, data)
        setLoading(false)
        if(res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            Cookies.remove('loginemail')
            setTimeout(() => {
                navigate('/login')
            }, 5000);
        }else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    
    useEffect(() => {
        if(!Cookies.get('loginemail')) {
            navigate('/forgot_password')
        }
    }, [navigate])
    return (
        <div>
            <div className="grid grid-col-1 md:grid-cols-2 h-screen w-full">
                <div className="bg-slate-800 hidden md:block relative">
                    <div className="loginbg absolute top-0 left-0 w-full h-screen mix-blend-overlay"></div>
                </div>
                <div className="md:bg-slate-50 bg-slate-800">
                    <div className="loginbg absolute top-0 left-0 w-full h-screen mix-blend-overlay block md:hidden"></div>
                    <div className="flex items-center justify-center w-full h-screen relative">
                        <div className="bg-white/70 backdrop-blur-sm w-11/12 md:w-3/4 shadow-xl rounded-xl px-3 py-5">
                            <div className="flex items-center justify-center border-b-[1px] pb-3 mb-8">
                                <img src={jog} alt="" className="w-14" />
                            </div>
                            <form onSubmit={handlesaveform}>
                                <div className='mb-4'>
                                    <div className="text-sm mb-4">Enter New Password for your account</div>
                                        <div className="flex items-center relative">
                                            <div className="absolute top-2 text-sm text-slate-600 cursor-pointer right-0" onClick={() => setPass1(!pass1)}> <i className={`fas ${pass1 ? 'fa-eye' : 'fa-eye-slash'} fa-fw`}></i> </div>
                                            <div className=""> <i className="fas fa-shield-alt fa-fw"></i> </div>
                                            <input type={pass1 ? 'text' : 'password'} name="password" value={forms.password} onChange={handleforms} placeholder='New Password' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                    </div> 
                                </div>
                                <div className='mb-4'>
                                    <div className="text-sm mb-4">Confirm Password</div>
                                        <div className="flex items-center relative">
                                            <div className="absolute top-2 text-sm text-slate-600 cursor-pointer right-0" onClick={() => setPass2(!pass2)}> <i className={`fas ${pass2 ? 'fa-eye' : 'fa-eye-slash'} fa-fw`}></i> </div>
                                            <div className=""> <i className="fas fa-shield-alt fa-fw"></i> </div>
                                            <input type={pass2 ? 'text' : 'password'} name="cpassword" value={forms.cpassword} onChange={handleforms} placeholder='Confirm Password' className="bg-transparent outline-none border-b-[1px] p-2 w-full text-sm" />
                                    </div> 
                                </div>
                                <div className="flex justify-between items-center mb-4">
                                    <div className=""></div>
                                    <button disabled={loading ? true : false} className={`py-3 px-6 ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} text-white flex items-center gap-3 text-sm button capitalize rounded-full shadow-xl`}> {loading && <div className="loading"></div> } confirm</button>
                                </div>
                                <hr />
                                <div className="text-center"> <Link to='/' className='text-sm text-indigo-600 font-semibold'>Go Home</Link> </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default NewPassword
