
import axios from 'axios'
import React from 'react'
import { useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { toast, ToastContainer } from 'react-toastify'
import { useState } from 'react';

const PackageWallet = props => {
    const navigate = useNavigate()
    const {imgurl, singlepackage} = useSelector(state => state.data)
    const [loading, setLoading] = useState(false)

    const checkoutPackage = async () => {
        setLoading(true)
        // console.log(props.item.id, singlepackage);
        const res = await axios.post(`/package/checkout/${props.item.id}/${singlepackage.id}`)
        setLoading(false)
        if(res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            setTimeout(() => {
                navigate('/mining')
            }, 3000);
        }else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 3000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div className="">
                <div>
                <div className="border-b-[1px] py-2 px-3 flex items-center justify-between dark:border-b-slate-500">
                    <div className="flex items-center gap-3">
                        <div className=""> {props.item.adminwallet.image && <img src={`${imgurl}/wallets/${props.item.adminwallet.image}`} alt="" className="w-8 rounded-full" />} </div>
                        <div className="">
                            <div className="capitalize font-semibold text-lg">{props.item.adminwallet.name}</div>
                            <div className="text-xs font-semibold dark:text-slate-200 text-slate-600">${(props.item.crypto / props.item.adminwallet.current_price).toFixed(2)} <span className="uppercase">usd</span> </div>
                        </div>
                    </div>
                    <div className="">
                        <button disabled={loading ? true : false} onClick={checkoutPackage} className={`py-2 px-3 capitalize font-semibold rounded-full text-xs ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} text-indigo-200 flex items-center gap-3`}> {loading && <div className="loading"></div> } checkout</button>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default PackageWallet