import React, { useCallback, useEffect } from "react"
import { Link, useLocation } from "react-router-dom"
import Togglemode from '../general/Togglemode';
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { getAllUserNotifies, getAllNotifies } from "../../store/reducers/dataReducer";


const Header = () => {
    const { user, allnotify, allusernotify } = useSelector(state => state.data)
    const dispatch = useDispatch()
    const dets = useLocation()

    const getAllNotes = useCallback(async () => {
        const res = await axios.get(`/news/get-usernotes/${user.id}`)
        if (res.data.status === 200) {
            dispatch(getAllNotifies(res.data.notify))
            dispatch(getAllUserNotifies(res.data.usernotify))
        }
        // eslint-disable-next-line
    }, [])

    useEffect(() => {
        dispatch(() => getAllNotes())
    }, [dispatch, getAllNotes])


    return (
        <div className="flex justify-between py-3 px-3 dark:bg-slate-700">
            <div className="flex items-center gap-3">
                {dets.pathname !== '/dashboard' && <div className="cursor-pointer text-slate-600 text-xl button dark:text-slate-200"> <i onClick={() => window.history.back()} className="fas fa-arrow-left fa-fw"></i> </div>}
                <div className="font-semibold text-xl drop-shadow-lg dark:text-slate-200">Jogglecryp</div>
            </div>
            <div className="">
                <div className="flex items-center">
                    <Togglemode />
                    <Link to='/profile'><div className="relative px-4"><i className="far button text-xl fa-user fa-fw dark:text-slate-200"></i> <i className="fas text-red-500 text-[0.6rem] fa-circle fa-fw absolute right-4"></i> </div></Link>
                    <Link to='/notifications' className="relative px-4">
                        <i className="far text-xl dark:text-slate-200 fa-bell button fa-fw"></i>
                        <div className={`text-[0.6rem] rounded-full font-semibold text-center bg-red-500 text-white absolute top-0 px-1 right-2 ${((allnotify.length - allusernotify.length) > 0 ? ' animate-pulse' : '')}`}>{allnotify.length > 10 ? `10+` : `${allnotify.length - allusernotify.length}`}</div>
                    </Link>
                </div>
            </div>
        </div>
    )
}

export default Header
