import axios from 'axios'
import React, { useState } from 'react'
import { toast, ToastContainer } from 'react-toastify'
import Swal from 'sweetalert2'

const LeaveFeedback = props => {
    const [status, setStatus] = useState('')
    const [loading, setLoading] = useState(false)
    const [text, setText] = useState('')
    const handleStatus = stat => {
        setStatus(stat)
    }

    const saveFeedback = async e => {
        e.preventDefault()
        const data = {
            status: status,
            text: text,
            trans: props.trans.id,
            userid: props.trans.user_id,
            vendor: props.trans.vendor,
        }
        if(!status || !text) {
            return toast.error(`Kindly provide an appropriate feedback`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        setLoading(true)
        const res = await axios.post(`/vendor/save-feedback`, data)
        setLoading(false)
        if (res.data.status === 200) {
            props.sendSignal()
            Swal.fire({
                title: 'FeedBack sent!',
                text: res.data.msg,
                icon: 'success',
                showConfirmButton: false
            })
        } else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div className="">
            <div className="text-xl">Leave a feedBack</div>
            <form onSubmit={saveFeedback}>
                <div className="mb-4">
                    <textarea name="text" value={text} onChange={e => setText(e.target.value)} placeholder='leave a message here!...' cols="30" rows="10" className="outline-none border bg-transparent rounded-lg text-sm dark:border-slate-400 w-full p-2"></textarea>
                </div>
                <div className="mb-4 grid grid-cols-2">
                    <div className="">
                        <div className={`rounded-full mx-auto text-2xl text-center border-2 w-fit p-3 cursor-pointer ${status === 'good' ? 'text-green-300 border-green-400 bg-green-200/30' : 'text-slate-300 border-slate-400 bg-slate-200/30'}`} onClick={() => handleStatus('good')}> <i className="far fa-thumbs-up fa-fw"></i> </div>
                    </div>
                    <div className="">
                        <div className={`rounded-full mx-auto text-2xl text-center cursor-pointer border-2 w-fit p-3 ${status === 'bad' ? 'text-red-300 border-red-400 bg-red-200/30' : 'text-slate-300 border-slate-400 bg-slate-200/30'}`} onClick={() => handleStatus('bad')}> <i className="far fa-thumbs-down fa-fw"></i> </div>
                    </div>
                </div>
                <div className="text-right mt-3">
                    <button className={`py-2.5 ${loading ? 'bg-orange-400' : 'bg-orange-600'} text-sm rounded-full text-orange-100 capitalize hover:bg-orange-700 shadow-xl px-10 flex items-center gap-3`} disabled={loading ? true : false}>{loading && <div className="loading"></div> } submit</button>
                </div>
            </form>
            <ToastContainer />
        </div>
    )
}

export default LeaveFeedback