import axios from 'axios'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { toast, ToastContainer } from 'react-toastify'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import moment from 'moment/moment'
import { useNavigate } from 'react-router-dom'
import Sidenav from './../../components/user/Sidenav';

const Mining = () => {
    const { user, imgurl } = useSelector(state => state.data)
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const [mine, setMines] = useState({})
    const [pack, setPack] = useState({})
    const [wallet, setWallet] = useState({})
    const [admin, setAdmin] = useState({})
    const [detail, setDetail] = useState({})
    const [showmine, setShowmine] = useState('false')
    const [showspin, setShowspin] = useState(false)

    const getMine = useCallback(async () => {
        if (user !== {}) {
            const res = await axios.get(`/package/get-mining/${user.id}`)
            if (res.data.msg.status === 'pending') {
                return navigate('/dashboard')
            }
            setMines(res.data.msg)
            setPack(res.data.msg.package)
            setWallet(res.data.wallet)
            setAdmin(res.data.wallet?.adminwallet)
        }
    }, [user, navigate])

    useEffect(() => {
        dispatch(() => getMine())
    }, [dispatch, getMine])

    const addMineFunds = async () => {
        setShowspin(true)
        setShowmine('')
        const res = await axios.get(`/package/add-funds/${user.id}`)
        console.log(res.data)
        if (res.data.status === 200) {
            setTimeout(() => {
                if(res.data.msg.status === 'pending') {
                    toast.info('Your mining has ended!..', {
                        position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
                    });
                    setTimeout(() => {
                        navigate('/dashboard')
                    }, 5000);
                }else {
                    setShowspin(false)
                    setShowmine('true')
                    setDetail(res.data.msg)
                }
            }, 2000);
        } else if(res.data.status === 401) {
            toast.info(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            setTimeout(() => {
                navigate('/dashboard')
            }, 5000);
        }else {
            console.log(res.data)
        }
    }
    return (
        <div>

        <div className="h-screen w-full flex">
            <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-700">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800 dark:text-slate-200">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="bg-white dark:bg-slate-800 dark:text-slate-200 rounded-xl shadow-xl grid grid-cols-1 md:grid-cols-2 p-3">
                                    {mine.status && <div className="flex flex-col items-center justify-center md:border-r-2 py-3 border-b-2 md:border-b-0 mb-3 dark:border-slate-500">
                                        <img src={`${imgurl}/wallets/${admin.image}`} alt="" className="w-20 rounded-full" />
                                        <div className="font-semibold capitalize">{admin.name} <span className="uppercase">({admin.short})</span> </div>
                                        <div className="text-2xl font-bold flex"> <span className="text-sm">$</span> {!detail.status ? (wallet.crypto / admin.current_price).toFixed(2) : (detail.user_wallet_balance).toFixed(2)} <span className="uppercase text-xs italic text-slate-600 dark:text-slate-300 self-end">usd</span> </div>
                                        <div className="text-slate-700 dark:text-slate-200 font-semibold text-sm">{parseFloat(wallet.crypto).toFixed(9)}<span className="uppercase">{admin.short}</span> </div>
                                    </div>}
                                    <div className="p-3">
                                        <div className="text-right">
                                            <button onClick={addMineFunds} className="py-2 px-4 animate-bounce text-sm rounded-full bg-gradient-to-r from-indigo-700 to-orange-600 text-indigo-200 capitalize font-semibold button">mine today</button>
                                        </div>
                                        {showmine === 'true' && <div className="mt-3">
                                            <div className="grid grid-cols-2 py-1">
                                                <div className="text-sm font-semibold">Current Mining Balance:</div>
                                                <div className="text-right text-sm font-semibold">${(detail.addfund).toFixed(2)}</div>
                                            </div>
                                            <div className="grid grid-cols-2 py-1">
                                                <div className="text-sm font-semibold">Package Ends in:</div>
                                                <div className="text-right text-sm font-semibold">{moment(detail.stopdate).fromNow()} </div>
                                            </div>
                                            <div className="grid grid-cols-2 py-1">
                                                <div className="text-sm font-semibold">Wallet Balance:</div>
                                                <div className="text-right text-sm font-semibold">${(detail.user_wallet_balance).toFixed(2)}</div>
                                            </div>
                                            <div className="grid grid-cols-2 py-1">
                                                <div className="text-sm font-semibold">Next Mining Due in:</div>
                                                <div className="text-right text-sm font-semibold">{detail.remaining_time ? parseInt(detail.remaining_time) : parseInt(detail.remaining_time)} Hour{detail.remaining_time !== 1 ? 's' : ''}</div>
                                            </div>
                                            <div className="grid grid-cols-2 py-1">
                                                <div className="text-sm font-semibold">Started Mining:</div>
                                                <div className="text-right text-sm font-semibold">{moment(mine.start_date).calendar()}</div>
                                            </div>
                                            <div className="grid grid-cols-2 py-1">
                                                <div className="text-sm font-semibold">Status:</div>
                                                <div className={`text-right text-sm font-semibold capitalize italic ${detail.status === 'active' ? 'text-green-700' : ''}`}>{detail.status}...</div>
                                            </div>
                                        </div>}
                                        {showmine === 'false' && <div className="text-center text-slate-500 dark:text-slate-200 mt-5 text-sm">Click the button above to check your mining for today!..</div>}

                                        {showspin && <div className="text-center"> <i className="fas fa-redo-alt fa-fw text-2xl text-slate-500 dark:text-slate-200 animate-spin"></i> </div>}

                                        <div className="bg-orange-100 text-orange-700 rounded-lg text-xs font-semibold shadow-xl py-2 px-3 mt-4"> <i className="fas fa-exclamation-circle fa-fw"></i> Mining will be automatically credited to your wallet once the duration is covered</div>
                                    </div>
                                </div>
                                {mine.status && <div className="bg-white dark:bg-slate-800 dark:text-slate-200 rounded-xl p-3 shadow-xl mt-6">
                                    <div className="text-xl text-slate-700 dark:text-slate-200 font-semibold drop-shadow-lg py-4">Package currently Mined...</div>
                                    <div className="text-center text-2xl font-semibold italic capitalize mb-3"> {pack.name} </div>
                                    <div className="grid grid-cols-2 py-1">
                                        <div className="text-sm font-semibold">Package Topup After:</div>
                                        <div className={`text-right text-sm font-semibold`}>{pack.topup} hour{pack.fund_date !== 1 ? 's' : ''} </div>
                                    </div>
                                    <div className="grid grid-cols-2 py-1">
                                        <div className="text-sm font-semibold">Package Ends After:</div>
                                        <div className={`text-right text-sm font-semibold`}>{pack.duration} day{pack.duration !== 1 ? 's' : ''} </div>
                                    </div>
                                    <div className="grid grid-cols-2 py-1">
                                        <div className="text-sm font-semibold">Amount paid for mining:</div>
                                        <div className={`text-right text-sm font-semibold`}>${pack.amount}</div>
                                    </div>
                                    <div className="grid grid-cols-2 py-1">
                                        <div className="text-sm font-semibold">Expected Topup Amount:</div>
                                        <div className={`text-right text-sm font-semibold`}>${pack.bonus}</div>
                                    </div>
                                    <div className="grid grid-cols-2 py-1">
                                        <div className="text-sm font-semibold">Referral Commission:</div>
                                        <div className={`text-right text-sm font-semibold`}>{(pack.refcom * 100 - 100).toFixed(2)}%</div>
                                    </div>
                                </div>}

                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Mining