import React, { useCallback, useEffect, useState } from 'react'
import Cookies from 'js-cookie'
import { decodeToken, isExpired } from 'react-jwt'
import { Navigate, Outlet, useNavigate } from 'react-router-dom'
import axios from 'axios'
import { useDispatch } from 'react-redux';
import { getUserData } from '../store/reducers/dataReducer'
import loop from '../images/loop1.gif'

const PrivateRoutes = props => {
  const token = Cookies.get('token')
  const exp = isExpired(token)
  const dispatch = useDispatch()
  const navigate = useNavigate()
  const [rolestate, setRolestate] = useState('')
  const [loading, setLoading] = useState(true)

  const getUser = useCallback(async () => {
    setLoading(true)
    const decodes = decodeToken(token)
    const id = decodes.id
    const res = await axios.get(`/user/${id}`, { headers: { 'x-access-token': token } })
    setLoading(false)
    if (res.data.status === 200) {
      dispatch(getUserData(res.data.msg))
      setRolestate(res.data.msg.role)
    } else {
      navigate('/login')
    }
  }, [token, dispatch, navigate])

  useEffect(() => {
    setTimeout(() => {
      if (token) {
        if (!exp) {
          dispatch(() => getUser())
          setRolestate(false)
        } else {
          setRolestate(false)
          navigate('/login')
        }
      } else {
        setRolestate(false)
        navigate('/login')
      }
    }, 3000);
    // if(token && !exp) {
    //   setTimeout(() => {
    //     dispatch(() => getUser())
    //   }, 3000);
    // }
    // eslint-disable-next-line
  }, [])
  return (
    loading ? <div className="h-screen bg-slate-50 w-full flex items-center justify-center"> <div className="w-48 h-48" style={{ background: `url(${loop})` }}></div> </div> :
      exp ? <Navigate to='/login' /> : props.rolesRequired.includes(rolestate) ? <Outlet /> : rolestate === 'user' ? <Navigate to='/dashboard' /> : <Navigate to='/admin' />
  )
}

export default PrivateRoutes