import React, { useState } from 'react'
import ModifyWallet from '../../components/admin/ModifyWallet';
import AFooter from '../../components/admin/AFooter';
import AHeader from '../../components/admin/AHeader';
import axios from 'axios';
import { useQuery } from 'react-query';
import { useSelector, useDispatch } from 'react-redux';
import { getAdminwallet } from '../../store/reducers/dataReducer';


const Managewallets = () => {
    const [openform, setOpenform] = useState(false)
    const {imgurl} = useSelector(state => state.data)
    const dispatch = useDispatch()
    const closeForm = () => {
        setOpenform(!openform)
    }
    const getwallets = async () => {
        const res = await axios.get('/adminwallet')
        return res.data.msg
    }
    const { data, isLoading, isError, refetch } = useQuery('wallet', getwallets)
    const handleSinglewallet = async (id) => {
        try {
            const res = await axios.get(`/adminwallet/${id}`)
            if(res.data.status === 200) {
                dispatch(getAdminwallet(res.data.msg))
                setOpenform(true)
            }
        } catch (error) {
            return console.log(error)
        }
    }
    return (
        <div>
            {openform && <ModifyWallet closeform={closeForm} sendSignal={refetch} />}
            <div className="h-screen w-full flex">
                <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-24 mt-8 w-[97%] mx-auto">
                            <div className="text-right mb-5">
                                <button onClick={() => {setOpenform(!openform); dispatch(getAdminwallet({}))}} className="py-3 px-5 capitalize bg-indigo-700 text-white text-xs font-semibold rounded-full shadow-xl"> <i className="fas fa-plus fa-fw"></i> new wallet </button>
                            </div>
                            <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                                {isLoading ? <div className="italic">Loading...</div> : isError ? <div className="text-red-500">Unable to fetch wallets</div> : data.map((item) => (
                                    <div className="bg-white dark:bg-slate-700 rounded-xl shadow-xl py-5 px-3" key={item.id}>
                                        {item.image && <img src={`${imgurl}/wallets/${item.image}`} alt="" className="w-14 mx-auto rounded-full" />}
                                        <div className="font-semibold capitalize text-center"> {item.name} <span className="uppercase">({item.short})</span> </div>
                                        <div className="grid grid-cols-2 py-1 border-b-[1px]">
                                            <div className="text-xs capitalize">main price</div>
                                            <div className="text-xs text-right">{item.price}</div>
                                        </div>
                                        <div className="grid grid-cols-2 py-1 border-b-[1px]">
                                            <div className="text-xs capitalize">current price</div>
                                            <div className="text-xs text-right">{item.current_price} / ($1) </div>
                                        </div>
                                        <div className="grid grid-cols-2 py-1 border-b-[1px]">
                                            <div className="text-xs capitalize">address</div>
                                            <div className="text-xs text-right break-words">{item.ads.slice(0, 10)}...</div>
                                        </div>
                                        <div className="text-right pt-2">
                                            <button onClick={() => handleSinglewallet(item.id)} className="py-2 px-5 bg-indigo-700 text-indigo-200 font-semibold capitalize rounded-full shadow-xl text-xs">modify</button>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Managewallets