import React, { useState } from "react"
import img from '../../images/img.png'
import { useSelector } from "react-redux"
import moment from 'moment/moment';
import axios from "axios";
import { ToastContainer, toast } from "react-toastify";


const SingleTracker = (props) => {
    const { singleproposal, imgurl, wallet, vendor } = useSelector(state => state.data)
    const [loading, setLoading] = useState(false)
    const [viewproof, setViewproof] = useState(false)
    const stats = ['pending', 'accepted']

    const handleInterceed = async () => {
        setLoading(true)
        const res = await axios.get(`/vendor/admin-verified-proposal/${singleproposal.id}`)
        setLoading(false)
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            props.closeview()
            props.sendSignal()
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white p-4 rounded-xl md:w-2/5 w-11/12 max-h-[85vh] extras overflow-y-auto dark:bg-slate-700 dark:text-slate-200">
                    <div className="flex justify-between items-center mb-4">
                        <div className="text-xl dark:text-slate-200 text-slate-600 drop-shadow-xl">Manage and Supervise all Proposals</div>
                        <div className=""> <i onClick={props.closeview} className="fas fa-times cursor-pointer text-2xl dark:text-slate-200 text-slate-600 fa-fw"></i> </div>
                    </div>
                    <div className="">
                        <div className="grid grid-cols-2 relative">
                            <div className="absolute top-4 text-xl right-[10.5rem]"> <i className="fas fa-exchange-alt fa-fw"></i> </div>
                            <div className="flex items-center justify-center flex-col">
                                <img src={!singleproposal.user.image ? img : `${imgurl}/profiles/${singleproposal.user.image}`} alt="" className="w-16 h-16 border rounded-full object-cover" />
                                <div className="text-xs font-semibold">Buyer: {singleproposal.user.fname}</div>
                            </div>
                            <div className="flex items-center justify-center flex-col">
                                <img src={!vendor.user.image ? img : `${imgurl}/profiles/${vendor.user.image}`} alt="" className="w-16 h-16 border rounded-full object-cover" />
                                <div className="text-xs font-semibold">Vendor: {vendor.user.fname}</div>
                            </div>
                        </div>
                        <div className="mt-3">
                            <div className="grid grid-cols-2 pb-2">
                                <div className="text-xs font-semibold"> {stats.includes(singleproposal.status) ? 'Amount To Pay' : 'Paid'} </div>
                                <div className="text-xs text-right">${singleproposal.amount}</div>
                            </div>
                            <div className="grid grid-cols-2 pb-2">
                                <div className="text-xs font-semibold">Amount To Recieve</div>
                                <div className="text-xs text-right">${parseInt(singleproposal.amount - (singleproposal.amount * singleproposal.fee - singleproposal.amount))}</div>
                            </div>
                            <div className="grid grid-cols-2 pb-2">
                                <div className="text-xs font-semibold">Wallet to fund</div>
                                <div className="text-xs flex items-center ml-auto gap-3 capitalize"> <img src={`${imgurl}/wallets/${wallet.adminwallet.image}`} alt="" className="w-4 h-4 rounded-full" /> {wallet.adminwallet.name} </div>
                            </div>
                            {!stats.includes(singleproposal.status) && <div className="">
                                <div className="grid grid-cols-2 pb-2">
                                    <div className="text-xs font-semibold">Payer's Name</div>
                                    <div className="text-xs text-right">{singleproposal.payer_name}</div>
                                </div>
                                <div className="grid grid-cols-2 pb-2">
                                    <div className="text-xs font-semibold">Amount Paid</div>
                                    <div className="text-xs text-right">{singleproposal.paid}</div>
                                </div>
                                <div className="grid grid-cols-2 pb-2">
                                    <div className="text-xs font-semibold">Payment Method</div>
                                    <div className="text-xs text-right">{singleproposal.pay_method}</div>
                                </div>
                                <div className="grid grid-cols-2 pb-2">
                                    <div className="text-xs font-semibold">View Payment proof</div>
                                    <div className="text-xs text-right"> <button onClick={() => setViewproof(!viewproof)} className="border py-1.5 px-5 dark:border-slate-500 rounded-full hover:bg-slate-500"> <i className="fas fa-camera fa-fw"></i> </button> </div>
                                </div>
                                    <img src={`${imgurl}/proposals/${singleproposal.proof}`} alt="" className={`w-full object-contain ${viewproof ? 'h-40' : 'h-0'} transition-all delay-200`} />
                            </div>}
                            <div className="grid grid-cols-2 pb-2">
                                <div className="text-xs font-semibold">Transaction Status</div>
                                <div className="text-xs text-right">{singleproposal.status}</div>
                            </div>
                            <div className="grid grid-cols-2 pb-2">
                                <div className="text-xs font-semibold">Date Initiated</div>
                                <div className="text-xs text-right">{moment(singleproposal.createdAt).calendar()}</div>
                            </div>
                        </div>
                        {singleproposal.status === 'under review' && <div className="my-3 py-2 px-2 bg-slate-100 dark:bg-slate-800 rounded-lg shadow-lg">
                            <div className="text-xs font-semibold">Reason:</div>
                            <div className="text-xs">Lorem ipsum dolor sit amet consectetur adipisicing elit. Saepe, quo!</div>
                        </div>}
                        {singleproposal.status === 'under review' && <div className="w-fit ml-auto mt-4"><button onClick={handleInterceed} disabled={loading ? true : false} className={`py-2.5 px-6 ${loading ? 'bg-indigo-400' : 'bg-indigo-600'} flex items-center gap-3 rounded-full text-xs shadow-xl font-semibold text-slate-200 capitalize`}> {loading && <div className="loading"></div>} interceed</button></div>}
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default SingleTracker