import axios from 'axios'
import img from '../../images/img.png'
import moment from 'moment/moment'
import React, { useCallback, useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { ToastContainer } from 'react-toastify'
import DeclineTransfer from '../../components/general/DeclineTransfer'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import VendorTransfers from '../../components/user/VendorTransfers'
import { getSingleProposal, getSingleWallet } from '../../store/reducers/dataReducer'
import Sidenav from './../../components/user/Sidenav';

const ManageProposals = () => {
    const { user, imgurl } = useSelector(state => state.data)
    const dispatch = useDispatch()
    const [transfers, setTransfers] = useState([])
    const [openview, setOpenview] = useState(false)
    const [opendecline, setOpendecline] = useState(false)
    const [zone, setZone] = useState('pending')
    const [cc] = useState('py-2 px-3 cursor-pointer rounded-full button text-xs self-center shadow-xl md:text-sm text-center capitalize')
    const closeBox = useRef()
    const [cbox, setCbox] = useState(false)

    const myProposals = useCallback(async () => {
        const res = await axios.get(`/vendor/proposals/${user.id}`)
        if (res.data.status === 200) {
            return setTransfers(res.data.msg)
        } else {
            setTransfers([])
        }
    }, [user])

    useEffect(() => {
        dispatch(() => myProposals())
    }, [dispatch, myProposals])

    const setupSingleProposal = async (id) => {
        const res = await axios.get(`/vendor/single-transfer/${id}`)
        if (res.data.status === 200) {
            dispatch(getSingleProposal(res.data.msg))
            dispatch(getSingleWallet(res.data.wallet))
            setOpenview(true)
        }
    }

    const backToPrev = () => {
        setOpendecline(false)
        setOpenview(true)
    }

    const proceedTodecline = () => {
        setOpendecline(true)
        setOpenview(false)
    }

    useEffect(() => {
        if(closeBox) {
            document.addEventListener('click', togCloseBox, true)
        }
    })

    const togCloseBox = (e) => {
        if(closeBox.current !== null) {
            if(!closeBox.current.contains(e.target)) {
                setCbox(false)
            }
        }
    }
    return (
        <div>
            {opendecline && <DeclineTransfer back={backToPrev} sendSignal={() => dispatch(() => myProposals())} closeview={() => setOpendecline(false)} />}
            {openview && <VendorTransfers closeview={() => setOpenview(false)} sendSignal={() => dispatch(() => myProposals())} forward={proceedTodecline} />}

            <div className="h-screen w-full flex">
                <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 font-semibold dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="hidden grid-cols-5 gap-3 md:grid">
                                    <div onClick={() => setZone('pending')} className={`${cc} ${zone === 'pending' ? 'bg-green-100 dark:bg-green-700' : 'bg-slate-100 dark:bg-slate-700'}`}>pending</div>
                                    <div onClick={() => setZone('accepted')} className={`${cc} ${zone === 'accepted' ? 'bg-green-100 dark:bg-green-700' : 'bg-slate-100 dark:bg-slate-700'}`}>accepted</div>
                                    <div onClick={() => setZone('paid')} className={`${cc} ${zone === 'paid' ? 'bg-green-100 dark:bg-green-700' : 'bg-slate-100 dark:bg-slate-700'}`}>paid</div>
                                    <div onClick={() => setZone('approved')} className={`${cc} ${zone === 'approved' ? 'bg-green-100 dark:bg-green-700' : 'bg-slate-100 dark:bg-slate-700'}`}>approved</div>
                                    <div onClick={() => setZone('under review')} className={`${cc} ${zone === 'under review' ? 'bg-green-100 dark:bg-green-700' : 'bg-slate-100 dark:bg-slate-700'}`}>under review</div>
                                </div>
                                <div className="text-right">
                                    <button onClick={() => setCbox(true)} className='py-2 px-6 text-xl'> <i className="fas fa-align-right fa-fw"></i> </button>
                                </div>
                                <div className={`bg-white w-40 absolute top-32 right-8 shadow-xl rounded-lg dark:bg-slate-600 ${cbox ? '' : 'hidden'}`} ref={closeBox}>
                                    <div onClick={() => {setZone('pending'); setCbox(false)}}  className="text-sm hover:bg-slate-100 cursor-pointer dark:hover:bg-slate-500 py-2 px-3">pending</div>
                                    <div onClick={() => {setZone('accepted'); setCbox(false)}} className="text-sm hover:bg-slate-100 cursor-pointer dark:hover:bg-slate-500 py-2 px-3">accepted</div>
                                    <div onClick={() => {setZone('paid'); setCbox(false)}} className="text-sm hover:bg-slate-100 cursor-pointer dark:hover:bg-slate-500 py-2 px-3">paid</div>
                                    <div onClick={() => {setZone('approved'); setCbox(false)}} className="text-sm hover:bg-slate-100 cursor-pointer dark:hover:bg-slate-500 py-2 px-3">approved</div>
                                    <div onClick={() => {setZone('under review'); setCbox(false)}} className="text-sm hover:bg-slate-100 cursor-pointer dark:hover:bg-slate-500 py-2 px-3">under review</div>
                                </div>
                                <div className="text-xl p-3">Manage All Proposals</div>
                                {transfers.length < 1 ? <div className="italic">No record found</div> : transfers.map((item) => (
                                    item.status === zone && <div className="py-2 px-3 mb-2 justify-between button shadow-lg bg-white dark:bg-slate-700 rounded-lg cursor-pointer" onClick={() => setupSingleProposal(item.id)} key={item.id}>
                                        <div className="flex gap-3 w-full">
                                            <div className=""> <img src={!item.user.image ? img : `${imgurl}/profiles/${item.user.image}`} alt="" className="w-8 h-8 rounded-full object-cover" /> </div>
                                            <div className="w-full">
                                                <div className="flex items-center w-full justify-between">
                                                    <div className="text-sm font-semibold"> <i className="far fa-user text-xs fa-fw"></i> {item.user.fname} </div>
                                                    <div className="text-xs"> {item.status}... </div>
                                                </div>
                                                <div className="flex justify-between items-center my-2">
                                                    <div className="text-xs"> Amount requested: </div>
                                                    <div className="text-xs">  ${item.amount} </div>
                                                </div>
                                                <div className="flex justify-between items-center">
                                                    <div className="text-xs">Initiated</div>
                                                    <div className="text-xs">{moment(item.createdAt).calendar()}</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default ManageProposals