import axios from 'axios'
import React from 'react'
import { useQuery } from 'react-query'
import { useSelector } from 'react-redux'
import { useNavigate } from 'react-router-dom'
import { toast, ToastContainer } from 'react-toastify'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import Vendor from '../../components/user/Vendor'
import Sidenav from './../../components/user/Sidenav';

const FindVendor = () => {
    const { user } = useSelector(state => state.data)
    const navigate = useNavigate()
    const checkMyVendorship = async () => {
        const res = await axios.get(`/vendor/user/${user.id}`)
        if (res.data.status === 200) {
            const stat = res.data.msg.status
            if (stat === 'pending') {
                return toast.info(`Hi ${user.fname}! looks like your proposal is still under verification!..`, {
                    position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
                });
            }
            if (stat === 'declined') {
                return toast.error(`Hi ${user.fname}! looks like your proposal has been declined!..`, {
                    position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
                });
            }
            if (stat === 'verified') {
                return toast.success(`Hi ${user.fname}! looks like your proposal has been successfully verified!..`, {
                    position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
                });
            }
        } else {
            navigate('/become_a_vendor')
        }
    }

    const getAllVends = async () => {
        const res = await axios.get(`/vendor/sellers/${user.id}`)
        return res.data.msg
    }

    const { data, isLoading, isError } = useQuery('vendors', getAllVends)
    return (
        <div>

            <div className="h-screen w-full flex">
                <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 font-semibold dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="text-right my-5">
                                    <button onClick={checkMyVendorship} className='py-3 px-5 bg-indigo-700 text-indigo-100 text-sm rounded-full font-semibold shadow-xl'>Become a vendor</button>
                                </div>
                                <div className="my-6">
                                    <div className="text-xl text-font-semibold drop-shadow-lg font-semibold"> <i className="fas text-orange-500 text-2xl fa-user-friends fa-fw"></i> Choose a vendor</div>
                                </div>
                                <div className="">
                                    {isLoading ? <div className="italic">Loading...</div> : isError ? <div className="italic">Unable to fetch vendors records</div> : data.length < 1 ? <div className="">No Available vendor yet!...</div> : data.map((item) => (
                                        item.user_id !== user.id && <Vendor key={item.id} item={item} />
                                    ))}
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default FindVendor