import axios from 'axios'
import React, { useState } from 'react'
import { useQuery } from 'react-query'
import { useDispatch } from 'react-redux'
import { ToastContainer } from 'react-toastify'
import AFooter from '../../components/admin/AFooter'
import AHeader from '../../components/admin/AHeader'
import ModifyAirdrop from '../../components/admin/ModifyAirdrop'
import SingleAirdrop from '../../components/admin/SingleAirdrop'
import { getSingleAirdrop } from '../../store/reducers/dataReducer'

const Airdrops = () => {
    const dispatch = useDispatch()
    const [formopen, setFormopen] = useState(false)
    const getAllAirdrops = async () => {
        const res = await axios.get('/adminwallet/airdrop/all')
        if(res.data.status === 200) return await res.data.msg 
        console.log(res.data);
    }

    const {data, isLoading, isError, refetch} = useQuery('airdrop', getAllAirdrops)
    return (
        <div>
            {formopen && <ModifyAirdrop closeform={() => setFormopen(!formopen)} sendSignal={refetch} />}
            <div className="h-screen w-full flex">
                <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-24 mt-8 w-[97%] mx-auto">
                            <div className="text-right mb-5">
                                <button onClick={() => { setFormopen(!formopen); dispatch(getSingleAirdrop({})) }} className="py-3 px-5 capitalize bg-indigo-700 text-white text-xs font-semibold rounded-full shadow-xl"> <i className="fas fa-plus fa-fw"></i> new airdrop </button>
                            </div>
                            {isLoading ? <div className="italic">Loading...</div> : isError ? <div className="italic">Unable to fetch airdrops</div> : data.length > 0 ? data.map((item) => (<SingleAirdrop key={item.id} item={item} opens={() => setFormopen(!formopen)} /> )): <div className="italic">No airdrop found yet!...</div> }
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Airdrops