import React, { useCallback, useEffect, useMemo, useState } from 'react'
import Footer from '../../components/user/Footer';
import Header from '../../components/user/Header';
import imglogo from '../../images/img.png'
import axios from 'axios';
import { useDispatch, useSelector } from 'react-redux';
import moment from 'moment/moment';
import Sidenav from './../../components/user/Sidenav';
import Paginator from '../../components/general/Paginator';

const Transactions = () => {
    const { user } = useSelector(state => state.data)
    const dispatch = useDispatch()
    const [trans, setTrans] = useState([])
    const [wallets, setWallets] = useState([])
    const [accbal, setAccbal] = useState(0)
    const [page, setPage] = useState(1)
    const [limit] = useState(10)
    const [total, setTotal] = useState(0)
    const myTrans = useCallback(async () => {
        try {
            if(user.id) {
                const res = await axios.get(`/trans/all/${user.id}`)
                if(res.data.status === 200) {
                    setTotal(res.data.count)
                    setTrans(res.data.msg)
                }
            }
        } catch (error) {
            console.log(error);
        }
    }, [user])

    const indexOfLastTrans = page * limit
    const indexOfFirstTrans = indexOfLastTrans - limit
    const currentTrans = trans.slice(indexOfFirstTrans, indexOfLastTrans)

    const getallmywallets = useCallback(async () => {
      const res = await axios.get(`/wallet/all/${user.id}`)
      if (res.data.status === 200) {
        setWallets(res.data.msg)
      } else {
        setWallets([])
      }
    }, [user])

    useEffect(() => {
        dispatch(() => getallmywallets())
        dispatch(() => myTrans())
    }, [dispatch, myTrans, getallmywallets])
    
  const calcBalance = useMemo(() => {
    let output = 0;
    wallets.map((item) => (
        output += item.crypto / item.adminwallet.current_price
    ))
    setAccbal(output)
  }, [wallets])

  useEffect(() => {
    dispatch(() => calcBalance)
    // eslint-disable-next-line
  }, [])
  
  const handlePaginate = (number) => setPage(number)
  const prevPageBtn = () => setPage(prev => prev - 1)
  const nextPageBtn = () => setPage(prev => prev + 1)
    return (
        <div>

        <div className="h-screen w-full flex">
            <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200 font-semibold">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit dark:bg-slate-700 bg-white dark:text-slate-200">
                            <Header />
                        </div>
                        <div className="py-4 w-[97%] bg-white dark:bg-slate-700 dark:text-slate-200 shadow-xl mx-auto rounded-xl px-3 mt-6">
                            <div className="">
                                <div className="flex gap-3">
                                    <img src={imglogo} alt="" className="w-[4.2rem] rounded-full shadow-xl border-4 border-indigo-800 self-center" />
                                    <div className="w-full">
                                        <div className="font-semibold text-sm">{user.fname}</div>
                                        <div className="flex items-center text-xs justify-between capitalize font-semibold text-slate-600 dark:text-slate-200 py-1"> <span className="">account balance</span> <span className="">${accbal.toFixed(2)} USD</span> </div>
                                        <div className="flex items-center text-xs justify-between capitalize font-semibold text-slate-600 dark:text-slate-200 py-1"> <span className="">total deposit</span> <span className="">${user.total_dept} USD</span> </div>
                                        <div className="flex items-center text-xs justify-between capitalize font-semibold text-slate-600 dark:text-slate-200 py-1"> <span className="">total withdrawal</span> <span className="">${user.total_with} USD</span> </div>
                                    </div>
                                </div>
                                <div className=""></div>
                            </div>
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="text xl drop-shadow-lg pb-3 pl-5 font-semibold">Showing latest ({currentTrans.length} of {total}) Records</div>
                                {/* ======================== */}
                                {trans && currentTrans.length > 0 ? currentTrans.map((item) => (
                                    <div className="flex items-center bg-white dark:bg-slate-700 dark:text-slate-200 rounded-xl py-2 mb-2 shadow-lg px-3 border dark:border-slate-500 w-full gap-3" key={item.id}>
                                        <div className="text-xl mx-1 bg-slate-300 dark:bg-indigo-400 rounded-full py-1.5 px-2 text-indigo-100"> <i className="far fa-bell fa-fw"></i> </div>
                                        <div className="w-full">
                                            <div className="flex items-center justify-between w-full">
                                                <div className="flex gap-4 items-center">
                                                    <div className="font-semibold text-slate-600 dark:text-slate-200 capitalize">{item.title}</div>
                                                    {item.title !== 'deposit' ? <div className="text-red-500 text-xs">( <i className="fas fa-minus fa-fw"></i> ${item.amount} USD )</div> : <div className="text-green-700 text-xs">( <i className="fas fa-plus fa-fw"></i> ${item.amount} USD )</div> }
                                                </div>
                                                <div className="">
                                                    <div className={`${item.tag === 'pending' || item.tag === 'declined' ? 'text-red-700' : 'text-green-600'} text-xs font-semibold`}>{item.tag === 'pending' ? `${item.tag}...` : item.tag}</div>
                                                </div>
                                            </div>
                                            <div className="text-sm py-1 text-slate-600 dark:text-slate-200 px-2 font-semibold">{item.message.slice(0, 70)}...</div>
                                            <div className="text-right text-xs text-indigo-500 dark:text-blue-400"> <i className="fas fa-globe fa-fw"></i> {moment(item.createdAt).calendar()} </div>
                                        </div>
                                    </div>
                                )) : <div className="">No record found...</div>}
                                <Paginator perpage={limit} total={total} page={page} handlePaginate={handlePaginate} prevPageBtn={prevPageBtn} nextPageBtn={nextPageBtn} />
                                {/* ========================== */}
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Transactions