import axios from 'axios'
import React from 'react'

const ToggleWalletVisibleButton = props => {
    const updateVisiblilty = async () => {
        const res = await axios.get(`/wallet/${props.item.status === 'active' ? 'off-wallet' : 'on-wallet'}/${props.item.id}`)
        if(res.data.status === 200) {
            props.sendSignal()
        }
    }
  return (
    <div className="h-3 w-14 bg-slate-200 dark:bg-slate-300/60 relative rounded-full">
        <div onClick={updateVisiblilty} className={`absolute top-[-.25rem] ${props.item.status === 'active' ? 'right-0 bg-orange-500' : 'left-0 bg-zinc-500'} h-5 w-5 rounded-full cursor-pointer`}></div>
    </div>
  )
}

export default ToggleWalletVisibleButton
