import React, { useCallback, useEffect, useRef, useState } from 'react'
import imglogo from '../../images/img.png'
import { useSelector, useDispatch } from 'react-redux';
import { getUserData } from '../../store/reducers/dataReducer';
import axios from 'axios';
import { toast } from 'react-toastify';


const ProfileComponent = () => {
  const dispatch = useDispatch()
  const { user, imgurl } = useSelector(state => state.data)
  const copyinput = useRef()
  const [photo, setPhoto] = useState({ img: null, image: null })
  const [loading, setLoading] = useState(false)

  const [forms, setForms] = useState({
      firstname: '',
      lastname: '',
      contact: '',
      country: '',
      state: '',
      address: '',
      myid: ''
  })

  const getProfile = useCallback(async () => {
      try {
          if (user.id) {
              const res = await axios.get(`/user/profile/${user.id}`)
              const item = res.data.msg
              setForms({
                  firstname: item.fname,
                  lastname: item.lname,
                  contact: item.contact,
                  country: item.country,
                  state: item.state,
                  address: item.address,
                  myid: item.myref_id
              })
          }
      } catch (error) {
          return console.log(error);
      }
  }, [user])

  useEffect(() => {
      dispatch(() => getProfile())
  }, [dispatch, getProfile])
  

  const handleforms = (e) => {
      setForms({ ...forms, [e.target.name]: e.target.value })
  }

  const handleUpdate = async e => {
      e.preventDefault()
      setLoading(true)
      const data = {
          firstname: forms.firstname,
          lastname: forms.lastname,
          contact: forms.contact,
          country: forms.country,
          state: forms.state,
          address: forms.address
      }
      try {
          const res = await axios.post(`/user/update-profile/${user.id}`, data)
          setLoading(false)
          dispatch(getUserData(res.data.user))
          toast.success(res.data.msg, {
              position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
          });
      } catch (error) {
        setLoading(false)
          return console.error(error);
      }
  }

  const handleCopy = () => {
      copyinput.current.select();
      copyinput.current.setSelectionRange(0, 99999);
      navigator.clipboard.writeText(copyinput.current.value);
      toast.info('Copied', {
          position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
      });
  }

  const handleProfileUpload = e => {
      const file = e.target.files[0]
      if (file.size > 3000000) {
          return toast.error('Profile Photograph must not exceed 3MB', {
              position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
          });
      }
      if (!file.type.startsWith('image/')) {
          return toast.error('Profile Photograph must be a valid image format', {
              position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
          });
      }

      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onload = e => {
          setPhoto({ img: e.target.result, image: file })
      }
  }

  const uploadProfileImage = async e => {
      e.preventDefault()
      const data = new FormData()
      data.append('image', photo.image)
      if(photo.image === null) {
          return toast.error('Upload a valid profile image', {
              position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
          });
      }
      const res = await axios.post(`/user/upload-profile/${user.id}`, data)
      if(res.data.status === 200) {
          dispatch(getUserData(res.data.user))
          setPhoto({img: null, image: null})
          return toast.success(res.data.msg, {
              position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
          });
      }else {
          return toast.error(res.data.msg, {
              position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
          });
      }
  }

  return (
    <div className="h-[auto] pb-24 mt-8">
        <div className="w-[97%] mx-auto">
            <div className="grid grid-cols-1 md:grid-cols-2 gap-5">
                <div className="bg-white dark:bg-slate-700 p-3 rounded-xl shadow-xl relative">
                    <div className="flex flex-col items-center justify-center my-7">
                        <form onSubmit={uploadProfileImage}>
                            <label>
                                <img src={photo.img === null ? user.image ? `${imgurl}/profiles/${user.image}` : imglogo : photo.img } alt="" className="w-40 h-40 object-cover rounded-full shadow-xl cursor-pointer border-4 border-indigo-800 self-center" />
                                <input type="file" name="image" onChange={handleProfileUpload} hidden />
                            </label>
                            {photo.img ? <div className="flex justify-around items-center gap-8">
                                <button className='py-2 px-4 capitalize font-semibold mt-4 text-xs bg-green-700 rounded-full shadow-lg text-green-200'> upload</button>
                                <button type='button' onClick={() => setPhoto({img: null, image: null})} className='py-2 px-4 capitalize font-semibold mt-4 text-xs bg-red-700 rounded-full shadow-lg text-red-200'> cancel</button>
                            </div> : <div className="text-sm mt-3">Click to upload profile</div> }
                        </form>
                    </div>
                    <div className="">
                        <div className="flex items-center w-full">
                            <input type="text" ref={copyinput} readOnly onChange={handleforms} value={`http://localhost:3000/register/${forms.myid}`} className="outline-none w-full border rounded-tl-xl rounded-bl-xl p-2 text-xs bg-transparent dark:border-slate-500" />
                            <button onClick={handleCopy} className="bg-indigo-700 text-indigo-200 text-xs font-semibold py-2 w-28 capitalize border-[1px] border-indigo-700 rounded-tr-xl rounded-br-xl button">referral ID</button>
                        </div>
                    </div>
                </div>
                <div className="bg-white dark:bg-slate-700 p-3 rounded-xl shadow-xl">
                    <div className="text-right">
                        <div className="text-sm my-3"> <i className="far fa-envelope fa-fw"></i> {user.email} </div>
                    </div>
                    {user.id && <form onSubmit={handleUpdate}>
                        <div className="grid grid-cols-2 gap-4 mb-4">
                            <div className="flex items-center">
                                <div className=""> <i className="far fa-user fa-fw"></i> </div>
                                <input type="text" name="firstname" value={forms.firstname} onChange={handleforms} placeholder='Firstname' className="bg-transparent outline-none border-b-[1px] p-2 dark:border-b-slate-500 w-full text-sm" />
                            </div>
                            <div className="flex items-center">
                                <div className=""> <i className="far fa-user fa-fw"></i> </div>
                                <input type="text" name="lastname" value={forms.lastname} onChange={handleforms} placeholder='Lastname' className="bg-transparent outline-none border-b-[1px] p-2 dark:border-b-slate-500 w-full text-sm" />
                            </div>
                        </div>
                        <div className="flex items-center mb-3">
                            <div className=""> <i className="fas fa-phone-alt fa-fw"></i> </div>
                            <input type="text" name="contact" value={forms.contact} onChange={handleforms} placeholder='Contact' className="bg-transparent outline-none border-b-[1px] p-2 dark:border-b-slate-500 w-full text-sm" />
                        </div>
                        <div className="grid grid-cols-2 gap-4 mb-3">
                            <div className="flex items-center">
                                <div className=""> <i className="far fa-flag fa-fw"></i> </div>
                                <input type="text" name="country" value={forms.country} onChange={handleforms} placeholder='Country' className="bg-transparent outline-none border-b-[1px] p-2 dark:border-b-slate-500 w-full text-sm" />
                            </div>
                            <div className="flex items-center">
                                <div className=""> <i className="far fa-flag fa-fw"></i> </div>
                                <input type="text" name="state" value={forms.state} onChange={handleforms} placeholder='State' className="bg-transparent outline-none border-b-[1px] p-2 dark:border-b-slate-500 w-full text-sm" />
                            </div>
                        </div>
                        <div className="mb-3">
                            <textarea name="address" cols="30" rows="5" value={forms.address ? forms.address : ''} onChange={handleforms} className="bg-transparent outline-none border-b-[1px] p-2 dark:border-b-slate-500 w-full text-sm" placeholder='Residential Address'></textarea>
                        </div>
                        <div className="w-fit ml-auto mb-4">
                            <button disabled={loading ? true : false} className={`py-3 px-6 ${loading ? 'bg-indigo-500' : 'bg-indigo-700'} flex items-center gap-3 text-white text-sm capitalize rounded-full shadow-xl button`}> {loading && <div className="loading"></div> } save changes</button>
                        </div>
                    </form>}
                </div>
            </div>
        </div>
    </div>
  )
}

export default ProfileComponent
