import React from 'react'

const Paginator = (props) => {
  const pageNumber = []
  
  for(let i = 1; i <= Math.ceil(props.total / props.perpage); i++) {
    pageNumber.push(i)
  }
  return (
    <div className="flex justify-between items-center mt-4">
        <div className=""></div>
        <div className="border py-1">
            {props.page !== 1 && <button className="button border-r py-2 px-3 capitalize text-xs" onClick={props.prevPageBtn}>previous</button>}
          {pageNumber.map((number) => (
            <button onClick={() => props.handlePaginate(number)} className="border-r button cursor-pointer p-2 text-xs" key={number}> {number} </button>
          ))}
          {props.page !== pageNumber.length && <button className="button py-2 px-3 capitalize text-xs" onClick={props.nextPageBtn}>next</button>}
        </div>
    </div>
  )
}

export default Paginator
