import axios from 'axios'
import React, { useState } from 'react'
import { useQuery } from 'react-query'
import { useDispatch, useSelector } from 'react-redux'
import AFooter from '../../components/admin/AFooter'
import AHeader from '../../components/admin/AHeader'
import moment from 'moment/moment'
import AuthorizeVendor from '../../components/admin/AuthorizeVendor'
import { getSingleVendor } from '../../store/reducers/dataReducer'

const ManageVendors = () => {
    const {imgurl} = useSelector(state => state.data)
    const dispatch = useDispatch()
    const [openview, setOpenview] = useState(false)
    const getAllVendors = async () => {
        const res = await axios.get('/vendor/all')
        if(res.data.status === 200) return res.data.msg 
        console.log(res.data)
    }

    const {data, isLoading, isError, refetch} = useQuery('vendors', getAllVendors)

    const getSingleVendorProfile = async (id) => {
        const res = await axios.get(`/vendor/single/${id}`)
        if(res.data.status === 200){
            console.log(res.data);
            dispatch(getSingleVendor(res.data.msg))
            setOpenview(true)
        }
    }
    return (
        <div>
            {openview && <AuthorizeVendor closeview={() => setOpenview(false)} sendSignal={refetch} />}
            <div className="h-screen w-full flex">
                <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="">
                                <div className="text-2xl font-semibold capitalize drop-shadow-lg mt-6 mb-3 px-4">manage vendors</div>
                                <div className="text-sm drop-shadow-lg mb-6 px-4 italic">All recent requests</div>
                            </div>
                            <div className="bg-white">
                                {isLoading ? <div className="italic">Loading...</div> : isError ? <div className="italic">Unable to fetch vendors records</div> : data.length < 1 ? <div className="">No User has requested for a vendor yet!...</div> : data.map((item) => (
                                <div className="flex dark:bg-slate-700 bg-white shadow-xl py-2 px-3 border-b-[1px] items-center" key={item.id}>
                                    <div className="flex w-full gap-3">
                                        <div className=""> <img src={`${imgurl}/profiles/${item.user.image}`} alt="" className="w-12 h-12 rounded-full object-cover" /> </div>
                                        <div className="">
                                            <div className="text-sm font-semibold"> <i className="far fa-user text-xs fa-fw"></i> {item.user.fname}</div>
                                            <div className="text-sm font-light"> <i className="far  fa-envelope text-xs fa-fw"></i> {item.user.email}</div>
                                            <div className="font-light text-xs"> <i className="far  fa-clock text-xs fa-fw"></i>: Requested {moment(item.createdAt).fromNow()}</div>
                                            <div className="text-xs">Status: <button className={`text-xs font-semibold ${item.status === 'pending' ? 'text-slate-600' : item.status === 'declined' ? 'text-red-600' : 'text-green-600'}`}> {item.status} </button></div>
                                        </div>
                                    </div>
                                    <div className="text-right"> <button onClick={() => getSingleVendorProfile(item.id)} className="text-xs bg-indigo-700 text-orange-200 font-semibold rounded-full px-3 py-1">view</button> </div>
                                </div>
                                )) }
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default ManageVendors