import axios from 'axios'
import React from 'react'
import { useQuery } from 'react-query'
import { useSelector } from 'react-redux'
import { ToastContainer } from 'react-toastify'
import AddWalletButton from './AddWalletButton'

const AddUserWallet = props => {
    const { imgurl } = useSelector(state => state.data)
    const getAllwallets = async () => {
        const res = await axios.get('/adminwallet')
        return res.data.msg
    }

    const { data, isError, isLoading, refetch } = useQuery('wallets', getAllwallets)
    const sendSignal = () => {
        refetch()
        props.confirmSignal()
    }

    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white dark:bg-slate-700 p-4 rounded-xl md:w-2/5 w-11/12 max-h-[80vh] extras overflow-y-auto">
                    <div className="flex justify-between items-center mb-4">
                        <div className="text-xl dark:text-slate-200 text-slate-600 drop-shadow-xl">Add new Wallet</div>
                        <div className=""> <i onClick={props.closewallet} className="fas fa-times cursor-pointer text-2xl dark:text-slate-200 text-slate-600 fa-fw"></i> </div>
                    </div>
                    {isLoading ? <div className="italic">Loading...</div> : isError ? <div className="italic">Error Fetching Wallets</div> : data.map((item) => (
                        <div className="flex items-center justify-between py-3 border-b-[1px] dark:border-slate-500" key={item.id}>
                            <div className="flex gap-4 items-center">
                                <div className=""> <img src={`${imgurl}/wallets/${item.image}`} alt="" className="w-8 rounded-full" /> </div>
                                <div className="text-sm font-semibold capitalize dark:text-slate-200 text-slate-600"> {item.name} <span className="uppercase">({item.short})</span> </div>
                            </div>
                            <AddWalletButton item={item} sendSignal={sendSignal} />
                        </div>
                    ))}
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default AddUserWallet
