import React, { useCallback, useEffect, useMemo, useState } from 'react'
import AHeader from '../../components/admin/AHeader';
import AFooter from '../../components/admin/AFooter';
import { useNavigate } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import axios from 'axios';

const Adminhome = () => { 
    const navigate = useNavigate() 
    const dispatch = useDispatch()
    const [users, setUsers] = useState([])
    const [activeusers, setActiveusers] = useState(0)

    const getAllUsers = useCallback(async () => {
        const res = await axios.get(`/user/all/admin-users`)
        if (res.data.status === 200) {
            setUsers(res.data.msg)
        }
    }, [])

    const getActives = useMemo(() => {
      let output = 0;
      users.map((item) => (
          output += parseInt(item.bonus)
      ))
      setActiveusers(output)
    }, [users])

    useEffect(() => {
        dispatch(() => getAllUsers())
        dispatch(() => getActives)

    }, [dispatch, getAllUsers, getActives])
    return (
        <div>
            <div className="h-screen w-full flex">
                <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="mx-auto w-[97%]"> 
                                <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-3">
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Users Registered</div>
                                        <div className="text-center text-lg font-semibold"> <i className="far text-lg fa-user fa-fw"></i> {users.length} </div>
                                    </div>
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Users Active</div>
                                        <div className="text-center text-lg font-semibold"> <i className="far text-lg fa-user fa-fw"></i> {activeusers} </div>
                                    </div>
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Users Offline</div>
                                        <div className="text-center text-lg font-semibold"> <i className="far text-lg fa-user fa-fw"></i> 350 </div>
                                    </div>
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Fees Generated</div>
                                        <div className="text-center text-lg font-semibold"> <i className="fas text-lg fa-money-bill-wave fa-fw"></i> $350 </div>
                                    </div>
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Users Balance</div>
                                        <div className="text-center text-lg font-semibold"> <i className="fas text-lg fa-money-bill fa-fw"></i> $350 </div>
                                    </div>
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Deposits</div>
                                        <div className="text-center text-lg font-semibold"> <i className="fas text-lg fa-money-bill fa-fw"></i> $350 </div>
                                    </div>
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Withdrawals</div>
                                        <div className="text-center text-lg font-semibold"> <i className="fas text-lg fa-money-bill fa-fw"></i> $350 </div>
                                    </div>
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Packages</div>
                                        <div className="text-center text-lg font-semibold"> <i className="fas text-lg fa-award fa-fw"></i> 350 </div>
                                    </div>
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Wallets</div>
                                        <div className="text-center text-lg font-semibold"> <i className="fas text-lg fa-wallet fa-fw"></i> 350 </div>
                                    </div>
                                    <div className="bg-white rounded-lg dark:bg-slate-700 hover:scale-105 cursor-pointer transition-all shadow-xl p-3" onClick={() => navigate('/')}>
                                        <div className="font-semibold text-sm">Total Vendors</div>
                                        <div className="text-center text-lg font-semibold"> <i className="fas text-lg fa-wallet fa-fw"></i> 350 </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Adminhome