import React, { useCallback, useEffect } from 'react'
import AFooter from './../../components/admin/AFooter';
import AHeader from './../../components/admin/AHeader';
import { useState } from 'react';
import ModifyNewsfeeds from '../../components/admin/ModifyNewsfeeds';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import { getSingleNews } from '../../store/reducers/dataReducer';
import ViewNewsfeeds from './../../components/admin/ViewNewsfeeds';
import { ToastContainer } from 'react-toastify';
import moment from 'moment/moment';

const ManageNews = () => {
    const [closeform, setCloseform] = useState(false)
    const [closeview, setCloseview] = useState(false)
    const [news, setNews] = useState([])
    const dispatch =  useDispatch()

    const allNews = useCallback(async () => {
        const res = await axios.get(`/news/all-news`)
        if(res.data.status === 200) {
            return setNews(res.data.msg)
        }else {
            console.log(res.data.msg);
        }
    }, [])

    useEffect(() => {
        dispatch(() => allNews())
    }, [dispatch, allNews])

    const openSingleNews = async (id) => {
        const res = await axios.get(`/news/single/${id}`)
        if(res.data.status === 200) {
            dispatch(getSingleNews(res.data.msg))
            setCloseform(true)
        }
    }

    const readOnlyNews = async (id) => {
        const res = await axios.get(`/news/single/${id}`)
        if(res.data.status === 200) {
            dispatch(getSingleNews(res.data.msg))
            setCloseview(true)
        }
    }
    return (
        <div>
            {closeform && <ModifyNewsfeeds closeform={() => {setCloseform(!closeform); dispatch(() => getSingleNews({}))}} sendSignal={() => dispatch(() => allNews())} showSingle={openSingleNews} />}
            {closeview && <ViewNewsfeeds closeform={() => {setCloseview(!closeview)}} />} 
            <div className="h-screen w-full flex">
                <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="">
                                <div className="text-2xl font-semibold capitalize drop-shadow-lg my-6 px-4">Manage NewsFeeds</div>
                                <div className="w-[95%] mx-auto">
                                    <div className="text-right mb-5">
                                        <button onClick={() => {setCloseform(true);dispatch(getSingleNews())}} className="py-3 button px-5 capitalize bg-indigo-700 text-white text-xs font-semibold rounded-full shadow-xl"> <i className="fas fa-plus fa-fw"></i> publish news </button>
                                    </div>
                                    <div className="grid grid-cols-2 gap-3 mb-3">
                                        {news.length < 1 ? <div className="italic">You have not published any newsfeeds yet</div> : news.map((item) => (
                                        <div className="bg-white p-3 rounded-lg shadow-lg dark:bg-slate-700" key={item.id}>
                                        <div className="text-right text-xs"> <i className="far fa-clock fa-fw"></i> {moment(item.createdAt).fromNow()}</div>
                                            <div className="font-semibold">{item.title.slice(0, 20)}</div>
                                            {item.media ? <div className="text-xs mt-3"> <i className="fas fa-camera fa-fw"></i> photo included</div> : <div className="text-xs mt-3">no photo included</div>}
                                            <div className="py-3 text-xs">{item.message.slice(0, 70)}...</div>
                                            <div className="flex items-center justify-between"> 
                                            <button onClick={() => openSingleNews(item.id)} className="rounded-full text-xs bg-indigo-600 py-1.5 px-4 text-indigo-100 font-bold shadow-xl"> modify </button>
                                            <button onClick={() => readOnlyNews(item.id)} className="rounded-full text-xs bg-indigo-600 py-1.5 px-4 text-indigo-100 font-bold shadow-xl"> view </button>
                                             </div>
                                        </div>
                                        )) }
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default ManageNews
