import React from 'react'
import { ToastContainer } from 'react-toastify';
import { useDispatch, useSelector } from 'react-redux';
import { useState } from 'react';
import { toast } from 'react-toastify';
import axios from 'axios';
import { getSingleNews } from '../../store/reducers/dataReducer';

const ModifyNewsfeeds = (props) => {
    const { news } = useSelector(state => state.data)
    const dispatch = useDispatch()
    const [forms, setForms] = useState({
        title: !news ? '' : news.title,
        message: !news ? '' : news.message,
        status: !news ? 'low' : news.quality
    })
    const [photo, setPhoto] = useState({ image: null, img: null })
    const handleforms = e => {
        setForms({ ...forms, [e.target.name]: e.target.value })
    }

    const handlePhoto = e => {
        const file = e.target.files[0]
        if (file.size > 3000000) {
            return toast.error(`File must not be greater then 3MB`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if (!file.type.startsWith('image/')) {
            return toast.error(`File must be a valid image`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = e => {
            setPhoto({ img: e.target.result, image: file })
        }
    }
    const closePhoto = () => {
        setPhoto({ img: null, image: null })
        document.getElementById('photo').value = null
    }

    const savepackage = async e => {
        e.preventDefault()
        const data = new FormData()
        data.append('image', photo.image)
        data.append('title', forms.title)
        data.append('message', forms.message)
        data.append('status', forms.status)

        if (!forms.title || !forms.message || !forms.status) {
            return toast.error(`Make sure to fill all fields properly`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        const res = !news ? await axios.post(`/news/save-news`, data) : await axios.post(`/news/update-news/${news.id}`, data)
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            props.sendSignal()

            if (!news) {
                setForms({
                    title: '',
                    message: '',
                    status: 'low',
                })
            }
            if(news?.title) {
                props.showSingle(news.id)
            }
            setPhoto({ image: null, img: null })
            document.getElementById('photo').value = null
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const handleDelete = async (e) => {
        e.preventDefault()
        const res = await axios.delete(`/news/delete-news/${news.id}`)
        if (res.data.status === 200) {
            dispatch(getSingleNews({}))
            props.closeform()
            props.sendSignal()
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white dark:bg-slate-700 dark:text-slate-200 extras p-4 rounded-xl md:w-2/5 w-11/12 max-h-[80vh] overflow-y-auto">
                    <div className="flex items-center justify-between mb-1">
                        {news ? <form onSubmit={handleDelete}> <button className="btn py-2 button px-8 bg-red-700 text-red-200 capitalize text-xs font-semibold rounded-full"> delete</button> </form> : <div></div>}
                        <div className="">  <i onClick={props.closeform} className="fas fa-times fa-fw text-2xl cursor-pointer text-slate-500"></i> </div>
                    </div>
                    <div>
                        <div className="text-lg">Modify NewsFeeds</div>
                        <form onSubmit={savepackage}>
                            <div className="mb-4">
                                <div className="text-xs text-slate-600 dark:text-slate-200">Title</div>
                                <input type="text" name="title" value={forms.title} onChange={handleforms} placeholder="Title of News" className="p-2 outline-none text-xs bg-transparent border-b-[1px] w-full" />
                            </div>
                            <div className="mb-4">
                                <div className="text-xs text-slate-600 dark:text-slate-200">Status Type</div>
                                <select name="status" value={forms.status} onChange={handleforms} className="outline-none dark:bg-slate-600 p-2 text-xs bg-transparent border-b-[1px] w-full">
                                    <option value="">Select Status</option>
                                    <option value="high">High</option>
                                    <option value="medium">Medium</option>
                                    <option value="low">Low</option>
                                </select>
                            </div>
                            <div className="mb-4">
                                <div className="text-xs text-slate-600 dark:text-slate-200">Message</div>
                                <textarea name="message" value={forms.message} placeholder='Type Message here!...' onChange={handleforms} cols="30" rows="10" className="outline-none p-2 text-xs bg-transparent border-b-[1px] w-full"></textarea>
                            </div>
                            <div className="mb-4">
                                <div className="text-xs text-slate-600 dark:text-slate-200">Add Photo (Optional)</div>
                                <input type="file" id="photo" name="image" onChange={handlePhoto} className="outline-none p-2 text-xs bg-transparent border-b-[1px] w-full" />
                                {photo.img && <div className="grid grid-cols-2">
                                    <div className="text-sm"> <i className="fas fa-camera fa-fw"></i> {photo.image.name}</div>
                                    <div className="text-right text-sm text-red-500"> <i onClick={closePhoto} className="fas cursor-pointer fa-times fa-fw"></i> </div>
                                </div>}
                            </div>
                            <div className="text-right mt-10">
                                <button className="py-3 px-5 text-white bg-indigo-600 rounded-full shadow-xl text-xs font-semibold button capitalize">save newsFeed</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default ModifyNewsfeeds
