import axios from 'axios'
import React, { useState } from 'react'
import { useSelector } from 'react-redux'
import { toast, ToastContainer } from 'react-toastify'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import Sidenav from './../../components/user/Sidenav';

const BecomeVendor = () => {
    const [passport, setPassport] = useState({ img: null, image: null })
    const [idcard, setIdcard] = useState({ img: null, image: null })
    const [proof, setProof] = useState({ img: null, image: null })
    const { user } = useSelector(state => state.data)
    const [forms, setForms] = useState({ bank: '', rate: '' })

    const handleForms = e => {
        setForms({ ...forms, [e.target.name]: e.target.value })
    }

    const handlePassport = e => {
        const file = e.target.files[0]
        if (file.size > 5300000) {
            return toast.error(`Passport must not be greater than 5MB`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if (!file.type.startsWith('image/')) {
            return toast.error(`Passport must be a valid image format`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const reads = new FileReader()
        reads.readAsDataURL(file)
        reads.onload = e => {
            setPassport({ img: e.target.result, image: file })
        }
    }

    const handleIdcard = e => {
        const file = e.target.files[0]
        if (file.size > 5300000) {
            return toast.error(`ID Card must not be greater than 5MB`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if (!file.type.startsWith('image/')) {
            return toast.error(`ID Card must be a valid image format`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const reads = new FileReader()
        reads.readAsDataURL(file)
        reads.onload = e => {
            setIdcard({ img: e.target.result, image: file })
        }
    }

    const handleProof = e => {
        const file = e.target.files[0]
        if (file.size > 5300000) {
            return toast.error(`POA document must not be greater than 5MB`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if (!file.type.startsWith('image/')) {
            return toast.error(`Proof of Address must be a valid image format`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const reads = new FileReader()
        reads.readAsDataURL(file)
        reads.onload = e => {
            setProof({ img: e.target.result, image: file })
        }
    }

    const handlesaving = async e => {
        e.preventDefault()
        const data = new FormData()
        data.append('passport', passport.image)
        data.append('idcard', idcard.image)
        data.append('proof', proof.image)
        data.append('user', user.id)
        data.append('bank', forms.bank);
        data.append('rate', forms.rate);

        if (passport.img === null || idcard.img === null || proof.img === null || !forms.bank || !forms.rate) {
            return toast.error(`Upload full documents required before submitting proposal`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        const res = await axios.post('/vendor/add-vendor', data)
        if (res.data.status === 200) {
            setPassport({ img: null, image: null })
            setProof({ img: null, image: null })
            setIdcard({ img: null, image: null })
            setForms({ ...forms, [e.target.value]: '' })
            setForms({ bank: '', rate: '' })
            return toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <div>

            <div className="h-screen w-full flex">
                <Sidenav />
                <div className="bg-slate-50 dark:bg-slate-800 dark:text-slate-200 w-full md:w-[80%]">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[97%] mx-auto">
                                <div className="mt-6 mb-10">
                                    <div className="text-xl text-font-semibold drop-shadow-lg font-semibold"> <i className="fas text-orange-500 text-2xl fa-user-friends fa-fw"></i> Become a vendor</div>
                                </div>
                                <form onSubmit={handlesaving}>
                                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                                        <div className="">
                                            <div className="font-semibold mb-3">Upload a valid Government-issued identity card</div>
                                            <label className='cursor-pointer'>
                                                {idcard.img ? <img src={idcard.img} alt="" className="flex items-center object-cover justify-center h-64 border-dashed w-full border-2 text-slate-500" /> : <div className="flex items-center justify-center h-64 border-dashed w-full border-2 text-slate-500"> <i className="fas fa-plus fa-fw"></i> </div>}
                                                <input type="file" name="idcard" hidden onChange={handleIdcard} />
                                            </label>
                                            {idcard.img && <div className="text-right mt-3"> <button type='button' onClick={() => { setIdcard({ img: null, image: null }) }} className="py-1 text-xs text-red-200 capitalize rounded-full font-semibold px-3 bg-red-600">cancel</button> </div>}
                                        </div>
                                        <div className="">
                                            <div className="font-semibold mb-3">Upload proof of address</div>
                                            <label className='cursor-pointer'>
                                                {proof.img ? <img src={proof.img} alt="" className="flex items-center object-cover justify-center h-64 border-dashed w-full border-2 text-slate-500" /> : <div className="flex items-center justify-center h-64 border-dashed w-full border-2 text-slate-500"> <i className="fas fa-plus fa-fw"></i> </div>}
                                                <input type="file" name="proof" hidden onChange={handleProof} />
                                            </label>
                                            {proof.img && <div className="text-right mt-3"> <button type='button' onClick={() => { setProof({ img: null, image: null }) }} className="py-1 text-xs text-red-200 capitalize rounded-full font-semibold px-3 bg-red-600">cancel</button> </div>}
                                        </div>
                                        <div className="">
                                            <div className="font-semibold mb-3">Upload a recent passport photograph</div>
                                            <label className='cursor-pointer'>
                                                {passport.img ? <img src={passport.img} alt="" className="flex items-center object-cover justify-center h-64 border-dashed w-full border-2 text-slate-500" /> : <div className="flex items-center justify-center h-64 border-dashed w-full border-2 text-slate-500"> <i className="fas fa-plus fa-fw"></i> </div>}
                                                <input type="file" name="passport" hidden onChange={handlePassport} />
                                            </label>
                                            {passport.img && <div className="text-right mt-3"> <button type='button' onClick={() => { setPassport({ img: null, image: null }) }} className="py-1 text-xs text-red-200 capitalize rounded-full font-semibold px-3 bg-red-600">cancel</button> </div>}
                                        </div>
                                        <div className="">
                                            <div className="font-semibold mb-3">Enter Exchange rate according to your local currency ({user.country})</div>
                                            <input type="number" name="rate" placeholder='0.00' value={forms.rate} onChange={handleForms} className="w-full text-sm py-3 pr-3 bg-transparent pl-2 outline-none border" />
                                        </div>
                                        <div className="">
                                            <div className="font-semibold mb-3">Enter the account you wish to be paid in as a vendor</div>
                                            <textarea name="bank" placeholder='Enter the account you wish to be paid in as a vendor' value={forms.bank} onChange={handleForms} cols="30" rows="10" className="w-full text-sm py-3 pr-3 bg-transparent pl-2 outline-none border"></textarea>
                                        </div>
                                    </div>
                                    <div className="mt-7">
                                        <div className="text-sm bg-orange-100 text-orange-800 p-2 dark:bg-orange-100/80 rounded-lg shadow-lg">Please note that this information is required for verification purposes and will not be shared to any third party agency whatsoever. the purpose of this information is to ensure you are who you say you are and can be trusted to work with.</div>
                                        <div className="text-sm bg-orange-100 text-orange-800 p-2 dark:bg-orange-100/80 rounded-lg shadow-lg mt-7">
                                            Note that you will be chared 0.5% for every successfull transaction you make
                                        </div>
                                    </div>
                                    <div className="text-right mt-6">
                                        <button className="py-3 px-6 bg-indigo-700 capitalize text-indigo-100 rounded-full text-sm font-semibold">submit proposal</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default BecomeVendor