import React, { useCallback, useEffect, useMemo, useState } from 'react'
import Footer from '../../components/user/Footer';
import Header from '../../components/user/Header';
import imglogo from '../../images/img.png'
import { useSelector, useDispatch } from 'react-redux';
import Logout from '../../components/general/Logout';
import { logoutUser } from '../../store/reducers/dataReducer';
import axios from 'axios';
import { ToastContainer } from 'react-toastify';
import ProfileComponent from '../../components/general/ProfileComponent';
import Sidenav from './../../components/user/Sidenav';

const Profile = () => {
    const dispatch = useDispatch()
    const { user, logout, imgurl } = useSelector(state => state.data)
    const [wallets, setWallets] = useState([])
    const [accbal, setAccbal] = useState(0)

    const togLogout = () => {
        dispatch(logoutUser())
    }
    const getallmywallets = useCallback(async () => {
        const res = await axios.get(`/wallet/all/${user.id}`)
        if (res.data.status === 200) {
            setWallets(res.data.msg)
        } else {
            setWallets([])
        }
    }, [user])

    useEffect(() => {
        dispatch(() => getallmywallets())
    }, [dispatch, getallmywallets])

    const calcBalance = useMemo(() => {
        let output = 0;
        wallets.map((item) => (
            output += item.crypto / item.adminwallet.current_price
        ))
        setAccbal(output)
    }, [wallets])

    useEffect(() => {
        dispatch(() => calcBalance)
        // eslint-disable-next-line
    }, [])

    return (
        <div>

        <div className="h-screen w-full flex">
            <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white">
                            <Header />
                        </div>
                        <div className="py-4 w-[97%] bg-white dark:bg-slate-700 shadow-xl mx-auto rounded-xl px-3 mt-6">
                            <div className="">
                                <div className="flex gap-3">
                                    <img src={user.image === null ? imglogo : user.image && `${imgurl}/profiles/${user.image}`} alt="" className="w-14 h-14 object-cover rounded-full shadow-xl border-2 border-indigo-800 self-start" />
                                    <div className="w-full">
                                        <div className="font-semibold text-sm">{user.fname} {user.lname}</div>
                                        <div className="flex items-center text-xs justify-between capitalize font-semibold dark:text-slate-200 text-slate-600 py-1"> <span className="">account balance</span> <span className="">${accbal.toFixed(2)} USD</span> </div>
                                        <div className="flex items-center text-xs justify-between capitalize font-semibold dark:text-slate-200 text-slate-600 py-1"> <span className="">total deposit</span> <span className="">${user.total_dept} USD</span> </div>
                                        <div className="flex items-center text-xs justify-between capitalize font-semibold dark:text-slate-200 text-slate-600 py-1"> <span className="">total withdrawal</span> <span className="">${user.total_with} USD</span> </div>
                                        <div className="text-right mt-3">
                                            {logout && <Logout />}
                                            <button className='text-xs text-red-100 bg-red-700 py-2 px-6 rounded-full capitalize font-semibold button' onClick={togLogout}> <i className="fas fa-power-off fa-fw"></i> logout </button>
                                        </div>
                                    </div>
                                </div>
                                <div className=""></div>
                            </div>
                        </div>
                        <ProfileComponent />
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default Profile