import axios from 'axios'
import moment from 'moment'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { ToastContainer } from 'react-toastify'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import Sidenav from './../../components/user/Sidenav';

const Earnings = () => {
    const { user } = useSelector(state => state.data)
    const [markets, setMarkets] = useState([])
    const [counts, setCounts] = useState(0)
    const [wallets, setWallets] = useState(0)
    const [earned, setEarned] = useState(0)
    const dispatch = useDispatch()


    const allMyEarnings = useCallback(async () => {
        const res = await axios.get(`/package/user-earnings/${user.id}`)
        if (res.data.status === 200) {
            setMarkets(res.data.msg)
            setCounts(res.data.count)
            setEarned(res.data.output)
            setWallets(res.data.outs)
        }
    }, [user])

    useEffect(() => {
        dispatch(() => allMyEarnings())
    }, [dispatch, allMyEarnings])
    
    return (
        <div>

        <div className="h-screen w-full flex">
            <Sidenav />
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <Header />
                        </div>
                        <div className="h-[auto] pb-24 mt-8">
                            <div className="w-[98%] mx-auto">
                                <div className="bg-white rounded-xl shadow-xl dark:bg-slate-700 mb-9">
                                    <div className="grid grid-cols-2 p-2.5 border-b">
                                        <div className="text-sm">Current Account Balance</div>
                                        <div className="text-sm text-right">${parseFloat(wallets).toFixed(2)}</div>
                                    </div>
                                    <div className="grid grid-cols-2 p-2.5 border-b">
                                        <div className="text-sm">Total Earned</div>
                                        <div className="text-sm text-right">${parseFloat(earned).toFixed(2)}</div>
                                    </div>
                                    <div className="grid grid-cols-2 p-2.5 border-b">
                                        <div className="text-sm">Current Bonus Balance</div>
                                        <div className="text-sm text-right">${parseFloat(user.bonus).toFixed(2)}</div>
                                    </div>
                                    <div className="grid grid-cols-2 p-2.5">
                                        <div className="text-sm">Total Bonus Earned</div>
                                        <div className="text-sm text-right">${parseFloat(user.total_bonus).toFixed(2)}</div>
                                    </div>
                                </div>
                                <div className="p-4 text-xl drop-shadow-lg font-semibold">My Earnings History ({counts}) </div>
                                {markets.length < 1 ? <div className="italic">You have no earnings!...</div> : markets.map((item) => (
                                    <div className="bg-white rounded-xl shadow-xl mb-1 p-3 dark:bg-slate-700" key={item.id}>
                                        <div className="grid grid-cols-2">
                                            <div className="">
                                                <div className="text-sm">{item.title}</div>
                                                <div className="text-sm">${item.amount}</div>
                                            </div>
                                            <div className="text-sm text-right"> <i className="far text-xs fa-clock fa-fw"></i> {moment(item.createdAt).fromNow()}</div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <Footer />
                    </div>
                </div>
                <ToastContainer />
            </div>
        </div>
    )
}

export default Earnings
