import axios from 'axios'
import React, { useState } from 'react'
import { toast, ToastContainer } from 'react-toastify'

const UploadProof = props => {
    const [load1, setLoad1] = useState(false)
    const [load2, setLoad2] = useState(false)
    const [forms, setForms] = useState({
        paid: '',
        method: '',
        name: ''
    })
    const [photo, setPhoto] = useState({img: null, image: null})
    const handleForms = e => {
        setForms({...forms, [e.target.name]: e.target.value})
    }

    const handleUpload = e => {
        const file = e.target.files[0]
        if(file.size > 3000000) {
            return toast.error(`File must not be greater then 3MB`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        if (!file.type.startsWith('image/')) {
            return toast.error(`File must be a valid image`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
        const reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = e => {
            setPhoto({img: e.target.result, image: file})
        }
    }

    const clearUpload = () => {
        setPhoto({img: null, image: null})
        document.getElementById('fileform').value = null
    }
    const handleProcess = async e => {
        e.preventDefault()
        const data = new FormData()
        data.append('image', photo.image)
        data.append('paid', forms.paid)
        data.append('method', forms.method)
        data.append('name', forms.name)
        data.append('trans', props.trans)

        if(!photo.image || !forms.paid || !forms.name || !forms.method) {
            return toast.error(`Complete filling out the form before you proceed to submit`, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }

        setLoad2(true)
        const res = await axios.post(`/vendor/proposal-paid`, data);
        setLoad2(false)
        if(res.data.status === 200) {
            props.sendSignal()
            return toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const userDeclinesProposal = async () => {
        setLoad1(true)
        const res = await axios.get(`/vendor/user-declines-proposal/${props.trans}`)
        setLoad1(false)
        if(res.data.status === 200) {
            props.sendSignal()
            return toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }else {
            return toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }
    return (
        <form onSubmit={handleProcess}>
            <div className="text-xs py-2 flex items-center gap-3">Upload Payment Proof <label> <div className="py-2 cursor-pointer px-3 border border-yellow-600 text-yellow-500 text-xs rounded-full" type='button'>Upload <i className="fas fa-camera fa-fw"></i> <input type="file" hidden id='fileform' name="image" onChange={handleUpload} /> </div> </label> </div>
            {photo.img && <img src={photo.img} alt='' className="border-dashed border-slate-300 border h-56 w-full object-cover" />}
            {photo.img && <div className="flex items-center justify-end mt-2 mb-3"> <div onClick={clearUpload} className="bg-red-500 cursor-pointer rounded-full text-xs py-2 px-4 text-red-100 capitalize w-fit">cancel</div> </div>} 
            <div className="mb-3">
                <div className="mb-1 text-sm">Payer's Name</div>
                <input type="text" name="name" value={forms.name} onChange={handleForms} placeholder="Payer's Name" className="outline-none bg-transparent rounded-lg text-sm border p-2 w-full" />
            </div>
            <div className="mb-3">
                <div className="mb-1 text-sm">Payment Method</div>
                <input type="text" name="method" value={forms.method} onChange={handleForms} placeholder='Payment Method' className="outline-none bg-transparent rounded-lg text-sm border p-2 w-full" />
            </div>
            <div className="mb-3">
                <div className="mb-1 text-sm">Amount Paid</div>
                <input type="number" name="paid" value={forms.paid} onChange={handleForms} placeholder='Amount paid' className="outline-none bg-transparent rounded-lg text-sm border p-2 w-full" />
            </div>
            <div className="flex items-center justify-between">
                <button type='button' onClick={userDeclinesProposal} className={`py-2 px-5 ${load1 ? 'bg-red-300' : 'bg-red-500'} text-red-100 rounded-full text-sm font-semibold capitalize flex items-cetner gap-3`} disabled={load1 ? true : false}> {load1 && <div className="loading"></div> } decline</button>
                <button className={`py-2 px-5 ${load2 ? 'bg-indigo-400' : 'bg-indigo-600'} text-indigo-100 rounded-full text-sm font-semibold capitalize flex items-center gap-3`} disabled={load2 ? true : false}> {load2 && <div className="loading"></div> } submit</button>
            </div>
            <div className="text-xs mt-2 p-2 rounded-lg bg-slate-200 shadow-lg dark:bg-slate-800">The amount paid must match the exact amount vendor will recieve in local currency, check vendor's rate per dollar before making any deposit</div>
            <ToastContainer />
        </form>
    )
}

export default UploadProof