import React, { useCallback, useEffect, useState } from 'react'
import AHeader from './../../components/admin/AHeader';
import AFooter from './../../components/admin/AFooter';
import { ToastContainer } from 'react-toastify';
import ModifyNotify from './../../components/admin/ModifyNotify';
import axios from 'axios';
import { useDispatch } from 'react-redux';
import moment from 'moment';
import ViewNotify from '../../components/admin/ViewNotify';
import Paginator from './../../components/general/Paginator';

const ManageNotify = () => {
    const [closeform, setCloseform] = useState(false)
    const [closeview, setCloseview] = useState(false)
    const [notes, setNotes] = useState([])
    const [notify, setNotify] = useState({})
    const dispatch = useDispatch()
    const [page, setPage] = useState(1)
    const [limit] = useState(10)
    const [total, setTotal] = useState(0)

    const getAllNotes = useCallback(async() => {
        const res = await axios.get(`/news/all-notifies`)
        if(res.data.status === 200) {
            setNotes(res.data.msg)
            setTotal(res.data.count)
        }
    }, [])


    const indexOfLastTrans = page * limit
    const indexOfFirstTrans = indexOfLastTrans - limit
    const currentNote = notes.slice(indexOfFirstTrans, indexOfLastTrans)
    useEffect(() => {
        dispatch(() => getAllNotes())
    }, [dispatch, getAllNotes])

    const getSingleNotify = async (id) => {
        const res = await axios.get(`/news/single-notify/${id}`)
        if(res.data.status === 200) {
            setNotify(res.data.msg)
            setCloseview(!closeview)
        }else {
            console.log(res.data)
        }
    }
    
    const handlePaginate = (number) => setPage(number)
    const prevPageBtn = () => setPage(prev => prev - 1)
    const nextPageBtn = () => setPage(prev => prev + 1)
    return (
        <div>
            {closeform && <ModifyNotify closeform={() => setCloseform(!closeform)} sendSignal={() => dispatch(() => getAllNotes())} />}
            {closeview && <ViewNotify notify={notify} closeform={() => setCloseview(!closeview)} /> }
            <div className="h-screen w-full flex">
                <div className="bg-slate-800 hidden md:w-[20%] md:block"></div>
                <div className="bg-slate-50 w-full md:w-[80%] dark:bg-slate-800 dark:text-slate-200">
                    <div className="h-[89vh] md:h-screen overflow-y-auto">
                        <div className="h-fit bg-white dark:bg-slate-800">
                            <AHeader />
                        </div>
                        <div className="h-[auto] pb-24 mt-8 w-11/12 md:w-2/3 mx-auto">
                            <div className="bg-white dark:bg-slate-700 shadow-xl rounded-xl p-4">
                                <div className="text-right mb-5">
                                    <button onClick={() => setCloseform(!closeform)} className="py-2.5 px-6 rounded-full text-sm shadow-xl font-semibold capitalize bg-orange-600 text-orange-100">new notification</button>
                                </div>

                                
                                {notes.length > 0 ? currentNote.map((item) => (
                                <div onClick={() => getSingleNotify(item.id)} className="flex items-center bg-white cursor-pointer hover:bg-slate-100 dark:bg-slate-700 dark:text-slate-200 rounded-xl py-2 mb-2 shadow-lg px-3 border dark:border-slate-500 w-full gap-3" key={item.id}>
                                    <div className="text-xl mx-1 bg-slate-300 dark:bg-indigo-400 rounded-full py-1.5 px-2 text-indigo-100"> <i className="far fa-bell fa-fw"></i> </div>
                                    <div className="w-full">
                                        <div className="grid grid-cols-2">
                                            <div className="font-semibold text-sm">{`${item.title.slice(0, 20)}`} ...</div>
                                                <div className="text-xs text-right"> <i className="far fa-clock fa-fw"></i> {moment(item.createdAt).fromNow()}</div>
                                            </div>
                                            <div className="text-sm">{item.message.slice(0, 60)}...</div>
                                        </div>
                                    </div>
                                )) : <div className="">No Notificatons found...</div>}
                                
                                <Paginator perpage={limit} total={total} page={page} handlePaginate={handlePaginate} prevPageBtn={prevPageBtn} nextPageBtn={nextPageBtn} />
                            </div>
                        </div>
                    </div>
                    {/* footer */}
                    <div>
                        <AFooter />
                    </div>
                </div>
            </div>
            <ToastContainer />
        </div>
    )
}

export default ManageNotify
