import axios from 'axios'
import moment from 'moment/moment'
import React, { useCallback, useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { toast } from 'react-toastify'
import { saveAs } from 'file-saver'
import img from '../../images/img.png'


const VendorTransfers = props => {
    const { singleproposal, imgurl, wallet, user } = useSelector(state => state.data)
    const stats = ['pending', 'accepted']
    const [userwallets, setUserwallets] = useState([])
    const dispatch = useDispatch()
    const [uwallet, setUwallet] = useState('')
    const [load1, setLoad1] = useState(false)
    const [load2, setLoad2] = useState(false)
    const [bal, setBal] = useState(null)

    const getUserwallets = useCallback(async () => {
        const res = await axios.get(`/wallet/all/${user.id}`)
        if (res.data.status === 200) {
            setUserwallets(res.data.msg)
        }
    }, [user])

    useEffect(() => {
        dispatch(() => getUserwallets())
    }, [dispatch, getUserwallets])

    const acceptProposal = async () => {
        if (!uwallet) {
            return toast.error('Enter a valid wallet', {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            })
        }

        setLoad2(true)
        const res = await axios.get(`/vendor/accept-proposal/${uwallet}/${singleproposal.id}`)
        setLoad2(false)
        if (res.data.status === 200) {
            props.closeview()
            props.sendSignal()
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const declineProposal = async () => {
        setLoad1(true)
        const res = await axios.get(`/vendor/declineproposal/${singleproposal.id}`)
        setLoad1(false)
        if (res.data.status === 200) {
            props.closeview()
            props.sendSignal()
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const calcBal = async e => {
        const val = e.target.value
        if (val) {
            setUwallet(e.target.value)
            const res = await axios.get(`/wallet/single/${val}`)
            if (res.data.status === 200) {
                const walls = res.data.msg
                setBal(`$${walls.crypto === 0 ? '0' : (walls.crypto / walls.adminwallet.current_price).toFixed(2)}`)
            } else {
                setBal(null)
            }
        }
    }

    const paymentVerified = async () => {
        setLoad2(true)
        const res = await axios.get(`/vendor/verified-proposal/${singleproposal.id}`)
        setLoad2(false)
        if (res.data.status === 200) {
            toast.success(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
            props.closeview()
            props.sendSignal()
        } else {
            toast.error(res.data.msg, {
                position: "top-center", autoClose: 5000, hideProgressBar: false, closeOnClick: true, pauseOnHover: true, draggable: true, progress: undefined, theme: "colored",
            });
        }
    }

    const downloadImage = () => {
        const date = new Date()
        const url = ` ${imgurl}/proposals/${singleproposal.proof}`
        saveAs(url, `paymentproof_${date.getTime()}.png`)
    }
    return (
        <div>
            <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
                <div className="bg-white p-4 rounded-xl md:w-2/5 w-11/12 max-h-[85vh] extras overflow-y-auto dark:bg-slate-700 dark:text-slate-200">
                    <div className="flex justify-between items-center mb-4">
                        <div className="text-xl dark:text-slate-200 text-slate-600 drop-shadow-xl">Manage Proposal requests</div>
                        <div className=""> <i onClick={props.closeview} className="fas fa-times cursor-pointer text-2xl dark:text-slate-200 text-slate-600 fa-fw"></i> </div>
                    </div>
                    <div className="">
                        <div className="flex gap-2 mb-2 pb-2">
                            <div className=""> <img src={!singleproposal.user.image ? img : `${imgurl}/profiles/${singleproposal.user.image}`} alt="" className="w-10 h-10 rounded-full" /> </div>
                            <div className="w-full">
                                <div className="flex justify-between w-full mb-1">
                                    <div className="text-xs">{singleproposal.user.fname}</div>
                                    <div className="text-xs text-right">{singleproposal.status}</div>
                                </div>
                                <div className="flex justify-between w-full mb-1">
                                    <div className="text-xs">{stats.includes(singleproposal.status) ? 'Amount To Pay' : 'Paid'}</div>
                                    <div className="text-xs text-right">${singleproposal.amount}</div>
                                </div>
                                <div className="flex justify-between w-full mb-1">
                                    <div className="text-xs">to recieve</div>
                                    <div className="text-xs text-right">${parseInt(singleproposal.amount - (singleproposal.amount * singleproposal.fee - singleproposal.amount))}</div>
                                </div>
                                <div className="flex justify-between w-full mb-1">
                                    <div className="text-xs">initaited transfer</div>
                                    <div className="text-xs text-right"> <i className="far fa-clock fa-fw"></i> {moment(singleproposal.createdAt).calendar()}</div>
                                </div>
                                <div className="flex justify-between w-full mb-1">
                                    <div className="text-xs">destination wallet</div>
                                    <div className="text-xs text-right flex items-center"> <img src={`${imgurl}/wallets/${wallet.adminwallet.image}`} alt="" className="w-4 rounded-full" /> <span className="ml-2 capitalize">{wallet.adminwallet.name}</span> </div>
                                </div>
                                {!stats.includes(singleproposal.status) && <div className="">
                                    <div className="flex justify-between w-full mb-1">
                                        <div className="text-xs">payer's name</div>
                                        <div className="text-xs text-right"> <i className="far fa-user fa-fw"></i> {singleproposal.payer_name}</div>
                                    </div>
                                    <div className="flex justify-between w-full mb-1">
                                        <div className="text-xs">amount paid</div>
                                        <div className="text-xs text-right">{singleproposal.paid}</div>
                                    </div>
                                    <div className="flex justify-between w-full mb-1">
                                        <div className="text-xs">payment method</div>
                                        <div className="text-xs text-right"> <i className="fas fa-exchange-alt fa-fw"></i> {singleproposal.pay_method}</div>
                                    </div>
                                </div>}
                                {singleproposal.status === 'paid' && <div className="flex justify-between w-full mb-1">
                                    <div className="text-xs">Time left, user awaiting your response</div>
                                    <div className="text-xs text-right"> <i className="far fa-clock fa-fw"></i> {moment(singleproposal.timelimit).fromNow()}</div>
                                </div>}
                            </div>
                        </div>
                        {singleproposal.status === 'under review' && <div className="bg-slate-200 dark:bg-slate-800 rounded-xl text-xs shadow-lg p-3 indent-3">Reason: {singleproposal.reason}</div>}

                        {singleproposal.status === 'under review' && <><div className="grid grid-cols-2 mt-3">
                            <div className=""></div>
                            <div className="text-right"> <button onClick={paymentVerified} className="py-2.5 px-5 button bg-orange-500 text-orange-100 dark:bg-orange-700 capitalize font-semibold rounded-full text-xs">redeem <i className="fas fa-arrow-right fa-fw"></i> </button> </div>
                        </div></>}

                        {singleproposal.status === 'under review' && <div className="bg-slate-200 dark:bg-slate-800 light:redeem mt-5 dark:redees relative rounded-xl text-xs shadow-xl p-3 indent-3">Redeeming a trasaction indicates there is no futher issues tagged along, and this will result in a completed transaction</div>}

                        {singleproposal.status === 'paid' && <div className='relative py-3'> <div className="my-2 text-sm">Payment Proof Uploaded</div>  <img src={`${imgurl}/proposals/${singleproposal.proof}`} alt="" className="object-contain h-40 w-full" /><div onClick={() => downloadImage()} className="py-2 absolute top-0 right-0 cursor-pointer text-orange-100 w-fit font-semibold px-5 rounded-full text-xs capitalize bg-orange-600"> download</div>  </div>}
                        {singleproposal.status === 'pending' && <form>
                            <div className="flex justify-between w-full flex-col mt-3">
                                <div className="text-xs mb-1">Select wallet to deduct funds from</div>
                                <select name="uwallet" value={uwallet} className="outline-none w-full text-xs rounded-lg p-2 bg-transparent border dark:bg-slate-700" onChange={e => calcBal(e)}>
                                    <option value="">Select wallet</option>
                                    {userwallets.map((item) => (
                                        <option value={item.id} key={item.id}> {item.adminwallet.name} </option>
                                    ))}
                                </select>
                                <div className="text-right text-xs">Available Balance: {bal}</div>
                            </div>
                        </form>}
                    </div>
                    {singleproposal.status === 'pending' && <div className="flex w-full justify-between mt-5">
                        <button className={`py-2.5 px-10 rounded-full button capitalize text-xs ${load1 ? 'bg-red-300' : 'bg-red-500'} font-semibold text-red-100 flex items-center gap-3`} disabled={load1 ? true : false} onClick={declineProposal}> {load1 && <div className="loading"></div>} decline</button>
                        <button className={`py-2.5 px-10 rounded-full button capitalize text-xs ${load2 ? 'bg-green-400' : 'bg-green-600'} text-green-100 font-semibold flex items-center gap-3`} disabled={load2 ? true : false} onClick={acceptProposal}> {load2 && <div className="loading"></div>} accept</button>
                    </div>}
                    {singleproposal.status === 'paid' && <div className="flex w-full justify-between mt-5">
                        <button className={`py-2.5 px-10 rounded-full button capitalize text-xs bg-red-500 font-semibold text-red-100 flex items-center gap-3`} onClick={props.forward}> decline</button>
                        <button className={`py-2.5 px-10 rounded-full button capitalize text-xs ${load2 ? 'bg-green-400' : 'bg-green-600'} text-green-100 font-semibold flex items-center gap-3`} disabled={load2 ? true : false} onClick={paymentVerified}> {load2 && <div className="loading"></div>} verify</button>
                    </div>}
                </div>
            </div>
        </div>
    )
}

export default VendorTransfers