import React, { useCallback, useEffect, useState } from 'react'
import img from '../../images/img.png'
import { Typewriter } from 'react-simple-typewriter'
import { useDispatch, useSelector } from 'react-redux'
import axios from 'axios'
import { Link } from 'react-router-dom'
import { logoutUser } from '../../store/reducers/dataReducer'
import Logout from './../general/Logout';

const Sidenav = () => {
  const {user, imgurl, logout} = useSelector(state => state.data)
  const dispatch = useDispatch()
  const [vendor, setVendor] = useState({})
  const checkVendor = useCallback(async() => {
    const res = await axios.get(`/vendor/user/${user.id}`)
    if(res.data.status === 200) {
      setVendor(res.data.msg)
    }
  }, [user])

  useEffect(() => {
    dispatch(() => checkVendor())
  }, [dispatch, checkVendor])
  
  const togLogout = (e) => {
    e.preventDefault()
    dispatch(logoutUser())
}
  return (
    <div className="bg-slate-800 hidden md:w-[20%] md:block border-r dark:border-slate-500">
    {logout && <Logout />}
        <div className="h-[30vh] bg-gradient-to-t from-indigo-900 to-indigo-400 flex flex-col justify-center items-center">
            <div className=" text-center">
                <img src={!user.image ? img : `${imgurl}/profiles/${user.image}`} alt="" className=" rounded-full bg-orange-400 p-1 w-24 h-24 object-cover" />
            </div>
            <div className="text-lg text-center font-semibold text-indigo-200"> <Typewriter loop={10000} words={[`${user.fname} ${user.lname}`, `${user.email}`, `${user.status}`]} /> &nbsp; </div>
            <div className="text-sm lowercase font-semibold text-indigo-200"> <i className="far fa-user fa-fw"></i> {vendor.id ? `Verified Vendor`: 'Not a Vendor'}</div>
        </div>
        <div className="h-[70vh] overflow-y-auto">
          <Link to='/dashboard'>
            <div className="py-2 px-3 text-sm text-slate-300 hover:text-slate-200 font-semibold hover:bg-slate-600 transition-all"> <i className="fas text-xs mr-4 fa-house-damage fa-fw"></i> Home</div>
          </Link>
          <Link to='/transactions'>
            <div className="py-2 px-3 text-sm text-slate-300 hover:text-slate-200 font-semibold hover:bg-slate-600 transition-all"> <i className="fas text-xs mr-4 fa-exchange-alt fa-fw"></i> Transactions</div>
          </Link>
          <Link to='/profile'>
            <div className="py-2 px-3 text-sm text-slate-300 hover:text-slate-200 font-semibold hover:bg-slate-600 transition-all"> <i className="far text-xs mr-4 fa-user fa-fw"></i> Profile</div>
          </Link>
          <Link to='/notifications'>
            <div className="py-2 px-3 text-sm text-slate-300 hover:text-slate-200 font-semibold hover:bg-slate-600 transition-all"> <i className="fas text-xs mr-4 fa-chart-line fa-fw"></i> Notifications</div>
          </Link>
          <Link to='/settings'>
            <div className="py-2 px-3 text-sm text-slate-300 hover:text-slate-200 font-semibold hover:bg-slate-600 transition-all"> <i className="fas text-xs mr-4 fa-cog fa-fw"></i> Settings</div>
          </Link>
          <Link to='/pages'>
            <div className="py-2 px-3 text-sm text-slate-300 hover:text-slate-200 font-semibold hover:bg-slate-600 transition-all"> <i className="fas text-xs mr-4 fa-align-left fa-fw"></i> Pages</div>
          </Link>
          <Link to='/' onClick={togLogout}>
            <div className="py-2 px-3 text-sm text-slate-300 hover:text-slate-200 font-semibold hover:bg-slate-600 transition-all"> <i className="fas text-xs mr-4 fa-power-off fa-fw"></i> Logout</div>
          </Link>
        </div>
    </div>
  )
}

export default Sidenav
