
import { Typewriter } from 'react-simple-typewriter';
import React, { useState, useCallback, useEffect, Suspense, useMemo, } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import axios from 'axios'
import AddUserWallet from '../../components/user/AddUserWallet'
import Footer from '../../components/user/Footer'
import Header from '../../components/user/Header'
import moment from 'moment/moment'
import UserPackages from '../../components/user/UserPackages'
import PackageWallet from '../../components/user/PackageWallet'
import { ToastContainer } from 'react-toastify'
import img from '../../images/img.png'
import Sidenav from '../../components/user/Sidenav'
const Mywallets = React.lazy(() => import('../../components/user/Mywallets'));

const UserHome = () => {
  const { user, imgurl } = useSelector(state => state.data)
  const [viewstate, setViewstate] = useState(false)
  const [addwallet, setAddwallet] = useState(false)
  const [viewpacks, setViewpacks] = useState(false)
  const dispatch = useDispatch()
  const [wallets, setWallets] = useState([])
  const [deposit, setDeposit] = useState([])
  const [withdraws, setWithdraws] = useState([])
  const [packages, setPackages] = useState([])
  const [openpack, setOpenpack] = useState(false)
  const [accbal, setAccbal] = useState(0)
  const [localStore, setLocalstore] = useState(false)

  const getallmywallets = useCallback(async () => {
    const res = await axios.get(`/wallet/all/${user.id}`)
    if (res.data.status === 200) {
      setWallets(res.data.msg)
    } else {
      setWallets([])
    }
  }, [user])

  const dashboardDeposit = useCallback(async () => {
    const res = await axios.get(`/deposit/dashboard-deposit/${user.id}`)
    if (res.data.status === 200) {
      setDeposit(res.data.msg)
    } else {
      setDeposit([])
    }
  }, [user])

  const dashboardWithdrawal = useCallback(async () => {
    const res = await axios.get(`/withdraw/dashboard-withdrawal/${user.id}`)
    if (res.data.status === 200) {
      setWithdraws(res.data.msg)
    } else {
      setWithdraws([])
    }
  }, [user])

  const getAllpackages = useCallback(async () => {
    const res = await axios.get('/package')
    setPackages(res.data.msg)
  }, [])

  useEffect(() => {
    dispatch(() => getallmywallets())
    dispatch(() => dashboardDeposit())
    dispatch(() => getAllpackages())
    dispatch(() => dashboardWithdrawal())
  }, [dispatch, getallmywallets, dashboardDeposit, getAllpackages, dashboardWithdrawal])

  const confirmSignal = () => {
    dispatch(() => getallmywallets())
  }

  const tryOpenpack = () => {
    setViewpacks(false)
    setOpenpack(true)
  }

  const closeNextpage = () => {
    setViewpacks(true)
    setOpenpack(false)
  }

  const closePage = () => {
    setViewpacks(false)
    setOpenpack(false)
  }
  const calcBalance = useMemo(() => {
    let output = 0;
    wallets.map((item) => (
      output += item.crypto / item.adminwallet.current_price
    ))
    setAccbal(output)
  }, [wallets])

  useEffect(() => {
    dispatch(() => calcBalance)
    // eslint-disable-next-line
  }, [])
  const balVisible = () => {
    setLocalstore(!localStore)
    localStorage.setItem('viewbal', !localStore)
  }

  return (
    <div className=''>
      {viewstate && <div className="bg-black/70 h-screen w-full fixed z-10 flex justify-center items-center">
        <div className="p-3 bg-white dark:bg-slate-700 rounded-xl w-[97%] md:w-2/4 relative">
          <div className="absolute top-0 right-0 text-2xl text-slate-500 dark:text-slate-200 cursor-pointer" onClick={() => setViewstate(false)}> <i className="fas fa-times fa-fw"></i> </div>
        </div>
      </div>}
      {addwallet && <AddUserWallet closewallet={() => setAddwallet(false)} confirmSignal={confirmSignal} />}
      {viewpacks && <UserPackages packages={packages} closeViews={() => setViewpacks(false)} openNextpage={tryOpenpack} />}
      <Suspense fallback='Loading...'>
        {/* ========================= */}
        {openpack && <div>
          <div className="h-screen w-full bg-black/60 flex items-center justify-center fixed z-10">
            <div className="bg-white dark:bg-slate-700 dark:text-slate-200 px-4 py-6 rounded-xl md:w-2/5 w-[97%] extras max-h-[80vh] overflow-y-auto">
              <div className="flex justify-between items-center pb-6">
                <div className="text-xl dark:text-slate-200 text-slate-600"> <i onClick={closeNextpage} className="fas fa-arrow-left fa-fw cursor-pointer"></i> </div>
                <div className="text-xl font-semibold dark:text-slate-200 text-slate-600 drop-shadow-xl">Select your preferred wallet</div>
                <div className="text-2xl text-zinc-600 dark:text-zinc-200"> <i onClick={closePage} className="fas fa-times fa-fw cursor-pointer"></i> </div>
              </div>
              {wallets.length > 0 ? wallets.map((item) => (
                item.status === 'active' && <PackageWallet key={item.id} item={item} />
              )) : <div className="bg-orange-100 py-2 px-3 rounded-lg w-[97%] mx-auto shadow-lg text-sm mt-5 border-[1px] border-orange-200 text-orange-600 font-semibold">You have not added any wallet yet</div>}
              {/* ================================== */}
            </div>
          </div>
        </div>}
      </Suspense>
      <div className="h-screen w-full flex">
        <Sidenav />
        <div className="bg-slate-50 w-full md:w-[80%]">
          <div className="h-[89vh] md:h-screen overflow-y-auto dark:bg-slate-800 font-semibold">
            {/* top home head */}
            <div className="h-fit bg-white">
              <Header />
            </div>
            <div className="w-[97%] mx-auto my-4 ">
              <div className="flex justify-between rounded-xl shadow-sm py-8 px-3 relative dark:bg-slate-200 bg-gradient-to-r from-indigo-800 to-indigo-400">
                <div className="flex gap-2">
                  <div className=""> <img src={user.image ? `${imgurl}/profiles/${user.image}` : img} alt="" className="w-10 h-10 md:w-14 md:h-14 object-cover rounded-full shadow-xl" /> </div>
                  <div className="">
                    <div className="text-xl text-indigo-200 font-semibold"> <Typewriter loop={10000} words={[`${user.fname} ${user.lname}`, `${user.email}`, `${user.status}`]} /> &nbsp; </div>
                    <div className="capitalize text-sm text-orange-300 font-semibold">account balance</div>
                    <div className="font-bold text-indigo-200 text-2xl">{localStorage.getItem('viewbal') === 'true' ? `$${accbal.toFixed(2)}` : '*****'} </div>
                  </div>
                </div>
                <div className="">
                  <div className="text-indigo-200 text-2xl cursor-pointer"><i onClick={balVisible} className={`fas ${localStorage.getItem('viewbal') === 'true' ? 'fa-eye' : 'fa-eye-slash'} fa-fw`}></i></div>
                </div>
                <div className="absolute bottom-[-0.3rem] right-[-0.1rem] bg-slate-50 dark:bg-slate-800 w-2/4 h-10 rounded-tl-[50rem]"></div>
              </div>
            </div>
            <div className="md:w-2/3 w-[97%] mx-auto">
              <div className="grid grid-cols-2 gap-8">
                <div className="flex justify-center items-center rounded-xl py-7 bg-gradient-to-r from-white to-sky-50 shadow-xl dark:from-slate-700 dark:to-zinc-800">
                  <div className="text-center cursor-pointer" onClick={() => setAddwallet(true)}>
                    <div className="bg-sky-800 text-sky-200 rounded-full py-3.5 px-4 w-fit mx-auto"><i className="fas fa-sync-alt text-xl fa-fw"></i></div>
                    <div className="text-slate-700 text-sm font-semibold dark:text-slate-200 capitalize mt-2">add new wallets</div>
                  </div>
                </div>
                {/* <div className="flex justify-center items-center rounded-xl py-7 bg-gradient-to-r from-white to-sky-50 shadow-xl dark:from-slate-700 dark:to-zinc-800">
                  <div className="text-center cursor-pointer" onClick={() => navigate('/vendors')}>
                    <div className="bg-sky-800 text-sky-200 rounded-full py-3.5 px-4 w-fit mx-auto"><i className="fas fa-user-friends text-xl fa-fw"></i></div>
                    <div className="text-slate-700 text-sm font-semibold dark:text-slate-200 capitalize mt-2">external transfers </div>
                  </div>
                </div> */}
                <div className="flex justify-center items-center rounded-xl py-7 bg-gradient-to-r from-white to-sky-50 shadow-xl dark:from-slate-700 dark:to-zinc-800">
                  <div className="text-center cursor-pointer" onClick={() => setViewpacks(true)}>
                    <div className="bg-sky-800 text-sky-200 rounded-full py-3.5 px-4 w-fit mx-auto"><i className="fas fa-sync-alt text-xl fa-fw"></i></div>
                    <div className="text-slate-700 text-sm font-semibold dark:text-slate-200 capitalize mt-2">mining packages</div>
                  </div>
                </div>
                {/* <div className="flex justify-center items-center rounded-xl py-7 bg-gradient-to-r from-white to-sky-50 shadow-xl dark:from-slate-700 dark:to-zinc-800">
                  <div className="text-center cursor-pointer" onClick={() => setViewstate(true)}>
                    <div className="bg-sky-800 text-sky-200 rounded-full py-3.5 px-4 w-fit mx-auto"><i className="fas fa-sync-alt text-xl fa-fw"></i></div>
                    <div className="text-slate-700 text-sm font-semibold dark:text-slate-200 capitalize mt-2">account overview</div>
                  </div>
                </div> */}
              </div>
            </div>
            <div className="h-[auto]">
              <div className="w-[97%] mx-auto py-3">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4 mt-7">
                  <div className="">
                    <div className="text-xl font-semibold text-slate-700 dark:text-slate-200 mb-2"> <i className="fas fa-exchange-alt fa-fw"></i> Last Deposit</div>
                    {/* ======================== */}
                    {deposit && deposit.length > 0 ? deposit.map((item) => (
                      <div className="flex justify-between items-center bg-white dark:bg-slate-700 dark:border-slate-500 rounded-xl py-2 mb-2 shadow-lg px-3 border" key={item.id}>
                        <div className="text-xl mx-1 bg-blue-300 rounded-full py-1.5 px-2 text-sky-200"> <i className="far fa-bell fa-fw"></i> </div>
                        <div className="w-full">
                          <div className="flex items-center justify-between w-full">
                            <div className="flex gap-4 items-center">
                              <div className="text-lg font-semibold text-slate-600 capitalize dark:text-slate-200">{item.title}</div>
                              <div className="text-green-500 text-xs">( <i className="fas fa-plus fa-fw"></i> ${item.amount} )</div>
                            </div>
                            <div className="">
                              <div className={`text-xs italic font-semibold capitalize ${item.status === 'pending' || item.status === 'declined' ? 'text-red-600' : 'text-green-600'}`}>{item.status}</div>
                            </div>
                          </div>
                          <div className="text-sm py-1 text-slate-600 px-2 dark:text-slate-200">{item.message.slice(0, 60)}...</div>
                          <div className="text-right text-xs text-indigo-500 dark:text-blue-400"> <i className="fas fa-globe fa-fw"></i> {moment(item.createdAt).calendar()} </div>
                        </div>
                      </div>
                    )) : <div className="bg-orange-100/40 shadow-xl py-3 text-sm px-3 rounded-lg text-orange-600 dark:text-orange-200 border-[1px] border-orange-400"> <i className="fas fa-exclamation-circle fa-fw"></i> You have no saved Deposits yet</div>}

                    {/* ========================== */}
                  </div>
                  <div className="mb-5">
                    <div className="text-xl font-semibold text-slate-700 mb-2 dark:text-slate-200"> <i className="fas fa-exchange-alt fa-fw"></i> Last Withdrawal</div>
                    {/* ======================== */}
                    {withdraws && withdraws.length > 0 ? withdraws.map((item) => (
                      <div className="flex justify-between items-center bg-white dark:bg-slate-700 dark:border-slate-500 rounded-xl py-2 mb-2 shadow-lg px-3 border" key={item.id}>
                        <div className="text-xl mx-1 bg-blue-300 rounded-full py-1.5 px-2 text-sky-200"> <i className="far fa-bell fa-fw"></i> </div>
                        <div className="w-full">
                          <div className="flex items-center justify-between w-full">
                            <div className="flex gap-4 items-center">
                              <div className="text-lg font-semibold text-slate-600 dark:text-slate-200 capitalize">{item.title}</div>
                              <div className="text-red-600 text-xs">( <i className="fas fa-plus fa-fw"></i> ${item.amount} )</div>
                            </div>
                            <div className="">
                              <div className={`text-xs italic font-semibold capitalize ${item.status !== 'confirmed' ? 'text-red-600' : 'text-green-600'}`}>{item.status}</div>
                            </div>
                          </div>
                          <div className="text-sm py-1 text-slate-600 px-2 dark:text-slate-200">{item.message.slice(0, 60)}...</div>
                          <div className="text-right text-xs text-indigo-500 dark:text-blue-400"> <i className="fas fa-globe fa-fw"></i> {moment(item.createdAt).calendar()} </div>
                        </div>
                      </div>
                    )) : <div className="bg-orange-100/40 shadow-xl py-3 text-sm px-3 rounded-lg text-orange-600 dark:text-orange-200 border-[1px] border-orange-400"> <i className="fas fa-exclamation-circle fa-fw"></i> You have no saved Withdrawals yet</div>}

                    {/* ========================== */}
                  </div>
                </div>

                <div className="grid grid-cols-2 md:grid-cols-4"></div>
              </div>
              {/* <div className="grid grid-cols-2 mb-8 w-[97%] mx-auto">
                <div onClick={() => setTogwall('wallets')} className={`text-center dark:text-slate-200 uppercase rounded-tl-3xl rounded-bl-3xl font-semibold shadow-lg ${togwall === 'wallets' ? 'bg-indigo-500 text-orange-200' : ''} cursor-pointer py-4 text-sm`}>coins</div>
                <div onClick={() => setTogwall('airdrops')} className={`text-center dark:text-slate-200 uppercase rounded-tr-3xl rounded-br-3xl font-semibold shadow-lg ${togwall === 'airdrops' ? 'bg-indigo-500 text-orange-200' : ''} cursor-pointer py-4 text-sm`}>airdrops</div>
              </div> */}
              {/* crypto calls */}
              <div className="mb-12">
                 <Suspense fallback='Loading...'>
                  {/* ========================= */}
                  {wallets.length > 0 ? wallets.map((item) => (
                    item.status === 'active' && <Mywallets key={item.id} item={item} />
                  )) : <div className="bg-orange-100/40 py-2 px-3 rounded-lg w-[97%] mx-auto shadow-lg text-sm mt-5 border-[1px] border-orange-200 text-orange-600 dark:text-orange-200 font-semibold">You have not added any wallet yet</div>}
                  {/* ================================== */}
                </Suspense>
              </div>
            </div>
          </div>
          {/* footer */}
          <div>
            <Footer />
          </div>
        </div>
      </div>
      <ToastContainer />
    </div>
  )
}

export default UserHome